import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { SharedServices } from '../../../../shared/services/services.module';

@Component({
  selector: 'app-ques-del-notification',
  templateUrl: './ques.del.notification.component.html',
  styleUrls: ['./ques.del.notification.component.scss']
})
export class QuesDelNotificationComponent implements OnInit {
  @Input() delNotification;
  // <delNotification> = {
  //   'header': '',
  //   'logics': {
  //     qid: [logicName]
  //   },
  //   'criteria': [cname],
  //   'quota': {
  //     'name': ''
  //   },
  //   'piping': [qid],
  //   'carryfrwd': [qid]
  // }
  @Input() titlesOfDelQues;
  // <titlesOfDelQues> = {
  //   qid: title
  // }
  @Output() onClose: EventEmitter<any> = new EventEmitter<any>();

  allQuestionsOfSurvey: any;
  questionCountAndId: any;

  constructor(
    private services: SharedServices
  ) { }

  ngOnInit() {
    this.allQuestionsOfSurvey = this.services.form.allQuestionsOfSurvey;
    this.questionCountAndId = this.services.form.getQuestionCountAndId();
  }

  closeDelNotfn(){
    this.onClose.emit();
  }

}



