import { Injectable } from '@angular/core';
import { SharedServices } from '../../../../shared/services/services.module';

@Injectable()
export class PageOperationsService {

  constructor(
    private services: SharedServices
  ) {}

  isQuestionInvolvedInLogic(delQId) {
      //function to see if question is involved in any logic
      let allQuestionsOfSurvey = this.services.form.allQuestionsOfSurvey;
      let tempObj: any = {};
      for(let quesId in allQuestionsOfSurvey){
        let quesAffected: Array<string> = [];
        for(let logicItem of allQuestionsOfSurvey[quesId].logicArr){
          if(logicItem.destQId == delQId){
            if(quesAffected.indexOf(logicItem.logic) == -1){
              quesAffected.push(logicItem.logic);
            }
          }
        }
        if(quesAffected.length > 0){
          tempObj[quesId] = quesAffected;
        }
      }
      return tempObj;
  }

  logicLeftOnAffQues(qId: string, logicName: string){
    let allQuestionsOfSurvey = this.services.form.allQuestionsOfSurvey;
    allQuestionsOfSurvey[qId].logicArr = allQuestionsOfSurvey[qId].logicArr.filter((logic) =>
      (logic.logic != logicName)
    )
  }

}



