import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormArray, FormControl, FormGroup, FormBuilder } from '@angular/forms';

import { SharedServices } from '../../../../../../../shared/services/services.module';
import { LogicService } from '../logic.service';

@Component({
  selector: 'app-redirect-logic',
  templateUrl: './redirect.logic.component.html',
  styleUrls: [
    '../logics.component.scss',
    './redirect.logic.component.scss'
  ]
})
export class RedirectLogicComponent implements OnInit {
  @Input() qId: any;

  @Output() logicChanged: EventEmitter<any> = new EventEmitter<any>();
  @Output() canceled: EventEmitter<any> = new EventEmitter<any>();

  question: any;
  criteriaArr: any;
  logicFormGroup: FormGroup;
  logicName: string = this.logicService.allLogicName.rLogic.name;
  additionalKeysInLogic: any = {
    "ifCount": "",
    "url": ""
  };

  constructor(
  	private services: SharedServices,
  	private logicService: LogicService
  ) {}

  ngOnInit() {
    this.question = this.services.form.getFormData().getQuestionById(this.qId);
    this.criteriaArr = this.services.form.getFormData().getAllCriterias();
  	this.setAppliedLogic();
  }

  setAppliedLogic() {
    let question = this.services.utils.cloneObject(this.question);
  	question.logicArr = question.logicArr.filter((logic) => {return logic.logic == this.logicName;});
  	if(!question.logicArr.length) {
  		question.logicArr.push(this.getLogicData(1));
  		question.logicArr.push(this.getLogicData(0));
  	}
    this.buildCriteriaForm(question);
  }

  buildCriteriaForm(question: any) {
    this.logicFormGroup = this.logicService.buildCriteriaForm(question, this.additionalKeysInLogic);
  }

  saveLogic() {
    var logicArr = this.logicFormGroup.value.logicArr;

    let logicFormData = this.logicService.getLogicFormData(this.question, logicArr, this.logicName);

    this.logicService.saveLogicData(logicFormData, this.question._id).subscribe((data) => {
      this.logicChanged.emit({logicName: this.logicName, res: data});
    });
  }

  getLogicData(ifCount: number) {
    let logicObject = this.services.utils.cloneObject(this.logicService.logicData);
    if(this.additionalKeysInLogic) {
      for(let key in this.additionalKeysInLogic) {
        logicObject[key] = this.additionalKeysInLogic[key];
      }
    }
    logicObject["ifCount"] = ifCount;
    logicObject["logic"] = this.logicName;
    logicObject["lType"] = "normal";
    return logicObject;
  }

  removeAllLogic() {
    this.logicService.removeAllLogicByName(this.question, this.logicName).subscribe((data) => {
      this.logicChanged.emit({logicName: this.logicName, res: data});
      this.services.toastr.success('Logic has been successfully removed.');
    });
  }

  showCriteriaForm(event) {
    (event.target.value == '0') && this.logicChanged.emit({view: 'Criteria'})
  }

  cancel() {
    this.canceled.emit();
  }

}



