import { Component, OnInit, OnDestroy } from '@angular/core';
import { EditDataService } from '../edit.data.service';
import { SharedServices } from '../../../../shared/services/services.module';
import { Subscription } from 'rxjs/Subscription';

@Component({
  selector: 'app-survey.design.component',
  templateUrl: './survey.design.component.html',
  styleUrls: ['../survey.edit.component.scss', './survey.design.component.scss']
})
export class SurveyDesignComponent implements OnInit, OnDestroy {
  onSuccessAction: any = {}
  isDataReady: boolean = false;
  surveyDesignData: any;
  actionObject: any;
  subscription: Subscription;

  constructor(
    private editDataService : EditDataService,
    private service: SharedServices
  ) {}

  ngOnInit() {
    this.surveyDesignData = this.editDataService.surveyDesignData;
    this.subscription = this.editDataService.getSurveyData().subscribe(data => {
      if(!data) return;
      this.isDataReady = false;
      this.surveyDesignData.presentAction = this.surveyDesignData.presentAction || 'survey-flow';
      setTimeout(() => {
        this.isDataReady = true;
        if(this.surveyDesignData.presentAction == 'survey-flow'){
          this.surveyDesignData.displaySidebar = true;
        }else{
          this.surveyDesignData.displaySidebar = false;
        }
      },1);
    });
  }

  hideOverlay() {
    this.surveyDesignData.presentAction = 'survey-flow';
    this.surveyDesignData.displaySidebar = true;
  }

  onChangeMainList(event: any) {
    this.actionObject = {};
    event && (this.actionObject = event);
    this.surveyDesignData.presentAction = this.actionObject.action;
    this.surveyDesignData.displaySidebar = false;
  }

  onSuccessToActions(act){
    this.onSuccessAction = {
      action: act
    }
  }

  onChangeLeftPanel(event) {
    this.onChangeMainList(event);
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

}



