import { Component, OnInit, Input, Output, ViewChild, EventEmitter } from '@angular/core';
import { FormArray, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';

import { SharedServices } from '../../../../../shared/services/services.module';
import { EditDataService } from '../../edit.data.service';
import { CriteriaService } from './criteria.service';

@Component({
  selector: 'app-criteria',
  templateUrl: './criteria.component.html',
  styleUrls: ['../../survey.edit.component.scss','./criteria.component.css'],
  providers: [CriteriaService]
})
export class CriteriaComponent implements OnInit {
  @ViewChild('criteriaSelect') criteriaSelect;
  @Input() qId?: string;

  @Output() onChange: EventEmitter<any> = new EventEmitter();
  @Output() canceled: EventEmitter<any> = new EventEmitter<any>();

  criteriaArr: any[];
  pageFields: any[];
  surId: string;
  criteriaSelected: any = {
    idx: '',
    criteriaInFormGroup: '',
  }
  criteriaData: Array<{selectedQuestion: any, rows: any, cols: any, countArray: any, containsBothRowCol: boolean}> = [];
  copyOfCriteriaSelected: any;
  copyOfCriteriaData: any;
  saveOrUpdate: boolean; //save-> true, update-> false
  allQuestionsOfSurvey: any;
  questionCountAndId: any;
  allQuestionsForCriteria: any;
  formObj: any;

  constructor(
    private criteriaService: CriteriaService,
    private toastr: ToastrService,
    private _fb: FormBuilder,
    private editService: EditDataService,
    private services: SharedServices) { }

  ngOnInit() {
    this.surId = this.services.form.getFormData().getId();
    this.criteriaArr = this.services.form.getFormData().getAllCriterias();
    this.pageFields = this.services.form.getFormData().pageFields;
    this.allQuestionsForCriteria = this.services.form.getAllQuestions().filter(item =>
      (item.qType && (item.qType.type == 'single-choice' || item.qType.type == 'mul-choice' ||
        item.qType.type == 'dropdown' || item.qType.type == 'nps' || item.qType.type == 'matrix-rating' ||
        item.qType.type == 'graph-rating'))
    );
    this.criteriaSelected.criteriaInFormGroup = this.criteriaService.buildCriteriaForm(this.criteriaService.criteriaJSON);
    this.criteriaSelected.idx = -1;
    this.criteriaData.push({'selectedQuestion': '', 'rows': '', 'cols': '', 'countArray':[0], 'containsBothRowCol': false});
    this.saveOrUpdate = true;
    this.allQuestionsOfSurvey = this.services.form.allQuestionsOfSurvey;
    this.questionCountAndId = this.services.form.getQuestionCountAndId();
    this.formObj = this.services.form.formObj;
  }

  changeCriteria(idx: number){
    if(idx == -1){
      this.makeNewCriteria();
      return false;
    }
    this.criteriaSelected.criteriaInFormGroup = this.criteriaService.buildCriteriaForm(this.criteriaArr[idx]);
    this.criteriaSelected.idx = idx;
    this.criteriaData = [];
    this.criteriaArr[idx].quesProp.forEach((item,i) => {
      this.criteriaData.splice(i, 0, {'selectedQuestion': '','rows': '', 'cols': '', 'countArray':[0], 'containsBothRowCol': false});
      this.setSelectedQuestion(item.quesId, i, null);
    });
    this.copyOfCriteriaData = JSON.parse(JSON.stringify(this.criteriaData));
    this.copyOfCriteriaSelected = JSON.parse(JSON.stringify(this.criteriaArr[idx]));
    this.saveOrUpdate = false;
    if(this.criteriaArr[idx].isCorrupt){
      this.services.alert({
        title: '<p class="sweetalert_title">This criteria is corrupted.</p>',
        text: "Some questions involved in this criteria have been deleted.",
        type: 'warning',
        showCancelButton: true,
        buttonsStyling: false,
        confirmButtonClass: 'sweetalert_apply_btn',
        cancelButtonClass: 'sweetalert_cancel_btn',
        confirmButtonText: 'Yes, delete it!'
      }).then((result) => {
         if (result.value) {
           this.confirmDelCriteria();
         }
      });
    }
  }

  addQuesInCriteria(){
    let question_idx: number = (<FormArray>this.criteriaSelected.criteriaInFormGroup.get('quesProp')).length;
    (<FormArray>this.criteriaSelected.criteriaInFormGroup.get('quesProp')).insert(question_idx, this.criteriaService.addQues());
    (<FormArray>(<FormArray>this.criteriaSelected.criteriaInFormGroup.get('quesProp')).at(question_idx).get('eData')).push(this.criteriaService.addEData());
    this.criteriaData.splice(question_idx, 0, {'selectedQuestion': '','rows': '', 'cols': '', 'countArray':[0], 'containsBothRowCol': false});
    console.log(this.criteriaData);
  }

  changeCriteriaType(str: string, criteria: any){
    criteria.get('typeOf').setValue(str);
    if(str == 'normal'){
      criteria.get('count').setValue('0');
      criteria.get('countOp').setValue('eq');
      criteria.controls['eData'] = this._fb.array([
        this.criteriaService.addEData()
      ]);
    }else if(str == 'count'){
      criteria.get('opArr').setValue('');
      criteria.get('rowId').setValue('');
      criteria.controls['eData'] = this._fb.array([
        this.criteriaService.addEData()
      ]);
    }else if(str == 'embed'){
      criteria.get('count').setValue('0');
      criteria.get('countOp').setValue('eq');
      criteria.get('opArr').setValue('');
      criteria.get('rowId').setValue('');
      if((<FormArray>criteria.get('eData')).length == 0){
        (<FormArray>criteria.get('eData')).push(this.criteriaService.addEData());
      }
    }
  }

  addNewEmbedDataInCriteria(quesProp: any, idx: number){
    quesProp.get('eData').insert(idx, this.criteriaService.addEData());
  }

  removeEmbedDataInCriteria(quesProp: any, idx: number){
    if(quesProp.get('eData').length < 2){
      this.toastr.error('There should be atleast one embedded data.', 'Cannot be deleted');
      return false;
    }
    quesProp.get('eData').removeAt(idx);
  }

  removeQuesFromCriteria(criteria: any, idx: number){
    if(criteria.get('quesProp').length < 2){
      this.toastr.error('There should be atleast one condition.', 'Cannot be deleted');
      return false;
    }
    criteria.get('quesProp').removeAt(idx);
    this.criteriaData.splice(idx, 1);
  }

  resetCriteriaBlock(idx: number){
    let criteraBlock = this.services.utils.cloneObject(this.copyOfCriteriaSelected.quesProp[idx]);
    (<FormArray>this.criteriaSelected.criteriaInFormGroup.get('quesProp')).at(idx).patchValue(criteraBlock);
    this.criteriaData[idx] = this.services.utils.cloneObject(this.copyOfCriteriaData[idx]);
  }

  saveOrUpdateCriteria(){
    let flag = false;
    if(this.saveOrUpdate == true){
      for(let criteria of this.criteriaArr){
        if(criteria.name == this.criteriaSelected.criteriaInFormGroup.get('name').value){
          flag = true;
          break;
        }
      }
      if(flag){
        this.toastr.error('Criteria with same name exists.','Error in Saving Criteria.')
        return false;
      }
    }
    let criteriaJSON = this.criteriaSelected.criteriaInFormGroup.getRawValue();
    if(criteriaJSON.name.trim() == ''){
      this.toastr.error('Criteria name is not valid.','Error in Saving Criteria.')
      return false;
    }
    let idx: number = 0;
    for(let item of criteriaJSON.quesProp){
      console.log(item);
      let ques = this.services.form.formObj.getQuestionById(item.quesId);
      if(item.rowId == ''){
        item.rowId = [];
      }
      if(item.opArr == ''){
        item.opArr = [];
      }
      if(item.typeOf == 'normal'){
        if(item.quesId == '' || item.quesId == 0){
          this.toastr.error('Please select question');
          return false;
        }
        if(this.criteriaData[idx].containsBothRowCol && !(ques['fieldType']['family'] == 'matrix' && ques['fieldType']['subtype'] == 'rating')){
          if(item.opArr.length == 0 || item.rowId.length == 0){
            this.toastr.error('Please select atleast one row and one column');
            return false;
          }
        }else{
          if(item.opArr.length == 0){
            this.toastr.error('Please select atleast one option');
            return false;
          }
        }
        delete item.count;
        delete item.countOp;
        item.eData = [];
      }else if(item.typeOf == 'embed'){
        console.log(item);
        for(let eDItem of item.eData){
          if(eDItem.value.trim() == '' || eDItem.field.substr(6, ( eDItem.field.length-8)).trim() == ''){
            this.toastr.warning('Make Sure all fields are filled.', 'Empty Fields');
            return false;
          }
        }
        delete item.count;
        delete item.countOp;
        delete item.quesId;
        delete item.opArr;
        delete item.rowId;
      }else if(item.typeOf == 'count'){
        if(item.quesId == '' || item.quesId == 0){
          this.toastr.error('Please select question');
          return false;
        }
        item.eData = [];
      }
      idx++;
    }
    delete criteriaJSON.quesProp[criteriaJSON.quesProp.length - 1].operator;
    if(criteriaJSON.isCorrupt == ''){
      delete criteriaJSON.isCorrupt;
    }
    if(this.saveOrUpdate == true){
      delete criteriaJSON._id;
      criteriaJSON.quesProp.forEach((ques) => {
        delete ques._id;
        ques.eData.forEach((item) => {
          delete item._id;
        })
      })
    }else{
      if(criteriaJSON._id == '') delete criteriaJSON._id;
      criteriaJSON.quesProp.forEach((ques) => {
        if(ques._id == '') delete ques._id;
        ques.eData.forEach((item) => {
          if(item._id == '') delete item._id;
        })
      })
    }
    if(this.saveOrUpdate == true){
      this.services.resource.saveCriteria(this.surId, criteriaJSON).subscribe((data) => {
        this.services.form.formObj.setCriteriaArray(data.criteria);
        this.criteriaArr = data.criteria;
        this.makeNewCriteria();
        this.toastr.success('Criteria ' + criteriaJSON.name + ' has been saved successfully.');
        this.onChange.emit('saved');
      },(err) => {
        this.services.toastr.error('Error in saving criteria');
      })
    }else{
      //update criteria
      delete criteriaJSON.isCorrupt;
      this.criteriaArr.splice(this.criteriaSelected.idx, 1, criteriaJSON);
      let criteriaUpdatedArr: any = {
        criteria: this.criteriaArr,
        _id: this.surId
      }
      this.services.resource.updateCriteria(this.surId, criteriaUpdatedArr).subscribe((data) => {
        this.services.form.formObj.setCriteriaArray(data.criteria);
        this.makeNewCriteria();
        this.toastr.success('Criteria ' + criteriaJSON.name + ' has been updated successfully.');
        this.onChange.emit('updated');
      },(err) => {
        this.services.toastr.error('Error in updating criteria');
      })
    }
  }

  setFieldInEmbedData(embedData: any, embedLogicType: any, embedContactType: any, fieldInEmbedData:any){
    let str = (embedLogicType.value == 'c') ? embedContactType.value : fieldInEmbedData.value;
    embedData.get('field').setValue('{{' + embedLogicType.value + '://' + str + '}}')
    console.log('{{' + embedLogicType.value + '://' + str + '}}');
  }

  setSelectedQuestion(questionSelectInCriteria: any, idx: number,str: string){
    if(str == 'resetPrevious'){
      (<FormArray>this.criteriaSelected.criteriaInFormGroup.get('quesProp')).at(idx).get('rowId').setValue('');
      (<FormArray>this.criteriaSelected.criteriaInFormGroup.get('quesProp')).at(idx).get('opArr').setValue('');
    }
    this.pageFields.forEach(page => {
      page.formFields.forEach(ques => {
        if(ques._id == questionSelectInCriteria){
          this.criteriaData[idx].selectedQuestion = ques;
          this.criteriaData[idx].rows = [];
          this.criteriaData[idx].cols = [];
          let carryopts = this.formObj.getCarryOptionArrays(questionSelectInCriteria);
          console.log(carryopts);
          carryopts['topArray'].forEach((carryItem) => {
            if(carryItem.carrObj.scale == 0 || carryItem.carrObj.scale == 1){
              carryItem.options.forEach((fieldOption) => {
                this.criteriaData[idx].rows.push(fieldOption);
              })
            }else if(carryItem.carrObj.scale == 2){
              carryItem.options.forEach((fieldOption) => {
                this.criteriaData[idx].cols.push(fieldOption);
              })
            }
          })
          ques.fieldOptions.row.forEach((fieldOption) => {
            this.criteriaData[idx].rows.push(fieldOption);
          })
          ques.fieldOptions.col.forEach((fieldOption) => {
            this.criteriaData[idx].cols.push(fieldOption);
          })
          carryopts['bottomArray'].forEach((carryItem) => {
            if(carryItem.carrObj.scale == 0 || carryItem.carrObj.scale == 1){
              carryItem.options.forEach((fieldOption) => {
                this.criteriaData[idx].rows.push(fieldOption);
              })
            }else if(carryItem.carrObj.scale == 2){
              carryItem.options.forEach((fieldOption) => {
                this.criteriaData[idx].cols.push(fieldOption);
              })
            }
          })
          let count = this.findCount(ques, this.criteriaData[idx].rows, this.criteriaData[idx].cols);
          this.criteriaData[idx].countArray = Array(count + 1).fill(0).map((x,i) => i);
          this.criteriaData[idx].containsBothRowCol = this.checkForBothRowAndCol(ques.fieldType.family, ques.fieldType.subtype);
          console.log(this.criteriaData);
          return false;
        }
      })
    })
  }

  findCount(ques: any, rows: any, cols: any): number{
    let family = ques.fieldType.family;
    let subtype = ques.fieldType.subtype
    if(family == 'multiChoice'){
      if(subtype == 'radio' || subtype == 'dropdown'){
        return 1;
      }else{
        return rows.length;
      }
    }else if(family == 'matrix'){
      if(subtype == 'rating'){
        return 1;
      }else if(subtype == 'radio' || subtype == 'graphical'){
        return rows.length;
      }else{
        return (rows.length * cols.length);
      }
    }
    return 0;
  }

  checkForBothRowAndCol(family: string, subtype: string): boolean{
    if(family == 'multiChoice'){
      return false;
    }else{
      return true;
    }
  }

  makeNewCriteria(){
    this.criteriaSelected.criteriaInFormGroup = this.criteriaService.buildCriteriaForm(this.criteriaService.criteriaJSON);
    this.criteriaSelected.idx = -1;
    this.criteriaData = [];
    this.criteriaData.push({'selectedQuestion': '','rows': '', 'cols': '', 'countArray':[0], 'containsBothRowCol': false});
    this.criteriaSelect.nativeElement.value = -1;
    this.saveOrUpdate = true;
    console.log(this.criteriaService.criteriaJSON);
    console.log(this.criteriaSelected.criteriaInFormGroup);
  }

  delCriteria(){
    this.services.alert({
      title: '<p class="sweetalert_title">Delete Criteria?</p>',
      text: "If this criteria is used in any logic on any question, that particular logic would be corrupted for that question.",
      type: 'warning',
      showCancelButton: true,
      buttonsStyling: false,
      confirmButtonClass: 'sweetalert_apply_btn',
      cancelButtonClass: 'sweetalert_cancel_btn',
      confirmButtonText: 'Yes, delete it!'
    }).then((result) => {
       if (result.value) {
         this.confirmDelCriteria();
       }
    })
  }

  confirmDelCriteria(){
    this.criteriaArr.splice(this.criteriaSelected.idx, 1);
    let criteriaUpdatedArr: any = {
      criteria: this.criteriaArr,
      updatedLogics: {},
      _id: this.surId
    }
    let detailsOfQuesAndLogicToBeRemoved: any = {};
    let str: string = '';

    /* object criteriaUpdatedArr.updatedLogics generated is ---->  {  QuesId(objecId) : [ "array of ids of logic to be made corrupt" ]  }  */
    /* object detailsOfQuesAndLogicToBeRemoved generated is ---->  {  QuesId(objecId) : [ "array of name of logics that are corrupted" ]  }  */
    for(let qid in this.allQuestionsOfSurvey){
      let ques = this.allQuestionsOfSurvey[qid];
      let tempArrlogic: Array<string> = [];
      let tempArrQuesAndLogic: Array<string> = [];
      ques.logicArr.forEach((logic) => {
        if(logic.ansId == this.criteriaSelected.criteriaInFormGroup.get('_id').value){
          tempArrlogic.push(logic._id);
          str = this.editService.getLogicType(logic.logic);
          if(tempArrQuesAndLogic.indexOf(str) == -1){
            tempArrQuesAndLogic.push(str);
          }
        }
      })
      if (tempArrlogic.length > 0) {
        criteriaUpdatedArr.updatedLogics[qid] = tempArrlogic;
      }
      if (tempArrQuesAndLogic.length > 0) {
        detailsOfQuesAndLogicToBeRemoved[qid] = tempArrQuesAndLogic;
      }
    }

    if(Object.keys(detailsOfQuesAndLogicToBeRemoved).length){
      let html = '<p class="logics_affected_title">The logics mentioned will be corrupted for the corresponding questions.</p>';
      html += '<table class="logics_affected_table">';
      html += '<tr><th class="td1">Ques No.</th><th class="td2">Title</th><th class="td3">Logics</th></tr>';
      for(let qid in detailsOfQuesAndLogicToBeRemoved){
        html += '<tr><td class="td1">' + this.questionCountAndId.data[qid] + '</td><td class="td2">' + this.services.utils.getTextOutOfHtml(this.allQuestionsOfSurvey[qid].title) + '</td><td class="td3">' + detailsOfQuesAndLogicToBeRemoved[qid].join(', ') + '</td></tr>';
      }
      html += '</table>';
      this.services.alert({
        title: 'Questions Affected',
        customClass: 'swal_wide',
        html: html
      });
    }
    this.services.resource.updateCriteria(this.surId, criteriaUpdatedArr).subscribe((data) => {
      this.updateAllQuesIfLogicsIsCorrupt(criteriaUpdatedArr.updatedLogics);
      this.toastr.success('Criteria ' + this.criteriaSelected.criteriaInFormGroup.get('name').value + ' has been deleted successfully.');
      this.makeNewCriteria();
    },(err) => {
      this.services.toastr.error('Error in getting existing criterias.');
    })
  }

  updateAllQuesIfLogicsIsCorrupt(updatedLogics: any){  //making all the logics corrupt in which the deleted criteria was involved
    let idx: number;
    for(let qid in updatedLogics){
      let tempArr = updatedLogics[qid];
      tempArr.forEach((logic) => {
        idx = this.services.utils.findIndex(this.allQuestionsOfSurvey[qid].logicArr, '_id', logic);
        this.allQuestionsOfSurvey[qid].logicArr[idx].isCorrupt = 1;
      })
    }
    console.log(this.allQuestionsOfSurvey);
  }

  cancel() {
    this.canceled.emit('canceled');
  }

}



