import { Component, OnInit } from '@angular/core';
import { FormArray, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { QuotaService } from './quota.service';
import { EditDataService } from '../../edit.data.service';
import { SharedServices } from '../../../../../shared/services/services.module';


@Component({
  selector: 'app-quota',
  templateUrl: './quota.component.html',
  styleUrls: ['../survey.advance.component.scss','./quota.component.css']
})
export class QuotaComponent implements OnInit {
  sId: string
  formObj: any;
  allQuestions: any;
  quotaEditObj: any;
  quotaForm: FormGroup;
  quotaData:any = { //to display at the front end
    simple: {},  // <obj of opt_id> opt_id : {text: '', checked: true/false, limit: '', select: 1/0}
    combtn: []   //[<obj>]: {qid: '', <obj>opts: {optId: optText}}
  };
  updateOrNew: boolean = false; //false-->new, true-->update
  reBuild: boolean = true;

  constructor(
    private quotaService: QuotaService,
    private editDataService: EditDataService,
    private services: SharedServices
  ) { }

  ngOnInit() {
    this.sId = this.editDataService.sId;
    this.formObj = this.services.form.formObj;
    this.quotaEditObj = this.formObj.quota;
    this.allQuestions = this.services.form.getAllQuestions().filter(item =>
      (item.qType && (item.qType.type == 'single-choice' || item.qType.type == 'mul-choice' || item.qType.type == 'dropdown'))
    );
    this.allQuestions = this.allQuestions.filter(item =>
      (!this.formObj.getQuestionById(item._id).isHidden)
    )

    if(this.quotaEditObj && (this.quotaEditObj.sQuota || this.quotaEditObj.cQuota)) {
      this.setSimQuotaOnInit();
      this.setComQuotaOnInit();
      this.updateOrNew = true;
      this.setQuotaForm(this.quotaEditObj);
    }else{
      this.setQuotaForm(this.quotaService.quotaJSON);
      this.quotaData.combtn.push({
        qid: '',
        opts: {}
      });
    }
  }

  setQuotaForm(obj: any){
    this.quotaForm = this.quotaService.buildQuotaFormGroup(this.services.utils.cloneObject(obj));
    if((<FormArray>this.quotaForm.get('cQuota.quesInQuota')).length == 0){
      (<FormArray>this.quotaForm.get('cQuota.quesInQuota')).push(this.quotaService.addQuesInCombtnQuota());
      this.quotaData.combtn.push({
        qid: '',
        opts: {}
      });
    }
    if(this.quotaForm.get('sQuota.quesId').value == '' || this.quotaForm.get('sQuota.quesId').value == null){
          //if quota has been affected by deleting some question
      this.quotaForm.get('sQuota.quesId').patchValue('-1');
    }
  }

  setSimQuotaOnInit(){
    if(this.quotaEditObj.sQuota){
      this.getOptForQuesInSquota(this.quotaEditObj.sQuota.quesId);
      if(this.quotaEditObj.sQuota.optArr){
        this.quotaEditObj.sQuota.optArr.forEach((item) => {
          if(this.quotaData.simple[item.ansId]){
            this.quotaData.simple[item.ansId].checked = true;
            this.quotaData.simple[item.ansId].limit = item.value;
            this.quotaData.simple[item.ansId].select = item.select;
          }
        })
      }
    }
  }

  setComQuotaOnInit(){
    if(this.quotaEditObj.cQuota && this.quotaEditObj.cQuota.quesInQuota){
      this.quotaEditObj.cQuota.quesInQuota.forEach((item, idx) => {
        this.quotaData.combtn.push({
          qid: '',
          opts: {}
        });
        this.getOptForQuesInCquota(item.quesId, idx);
      });
    }
  }

  validateLimit(e: any){
    if(e.key==" ")
      e.preventDefault();
  }

  getOptForQuesInSquota(qid: string){
    let idx: number = this.services.utils.findIndex(this.allQuestions, '_id', qid);
    this.quotaData.simple = {};
    if(idx > -1){
      this.allQuestions[idx].options.row.forEach((opt) => {
        this.quotaData.simple[opt._id] = {
          'text': opt.text,
          'checked': false,
          'limit': '',
          'select': 1
        }
      })
    }
  }

  getOptForQuesInCquota(qid: string, q_idx: number){
    let idx: number = this.services.utils.findIndex(this.allQuestions, '_id', qid);
    this.quotaData.combtn[q_idx].qid = qid;
    this.quotaData.combtn[q_idx].opts = {};
    if(idx > -1){
      this.allQuestions[idx].options.row.forEach((opt) => {
        this.quotaData.combtn[q_idx].opts[opt._id] = opt.text;
      })
    }
  }

  clearFormArray(str: any){
    while((<FormArray>this.quotaForm.get(str)).length){
      (<FormArray>this.quotaForm.get(str)).removeAt((<FormArray>this.quotaForm.get(str)).length - 1);
    }
  }

  applyQuota(e: any){
    this.quotaForm.get('isQuota').patchValue(e.target.checked ? 1 : 0);
  }

  addQuesInCquota(){
    (<FormArray>this.quotaForm.get('cQuota.quesInQuota')).push(this.quotaService.addQuesInCombtnQuota());
    this.quotaData.combtn.push({
      qid: '',
      opts: {}
    });
  }

  removeQuesInCquota(idx: number){
    if((<FormArray>this.quotaForm.get('cQuota.quesInQuota')).length > 1){
      this.quotaData.combtn.splice(idx, 1);
      (<FormArray>this.quotaForm.get('cQuota.quesInQuota')).removeAt(idx);
      if(idx > 0){ //if not the first element
        (<FormArray>this.quotaForm.get('cQuota.quesInQuota')).at(idx - 1).get('ope').patchValue(1);
      }
    }else{
      this.services.toastr.error('Atleast one question is required.','Cannot be deleted');
    }
  }

  resetQuota(){
    this.quotaForm = this.quotaService.buildQuotaFormGroup(this.services.utils.cloneObject(this.quotaService.quotaJSON));
    this.quotaData = {
      simple: {},
      combtn: []
    };
    this.quotaData.combtn.push({
      qid: '',
      opts: {}
    });
    this.updateOrNew = false;
    this.reBuild = false;
    setTimeout(() => {
      this.reBuild = true
    },1);
  }

  saveQuota(){
    let res = this.quotaForm.getRawValue();
    for(let opt in this.quotaData.simple){
      let temp = this.quotaData.simple[opt];
      let idx = this.services.utils.findIndex(res.sQuota.optArr, 'ansId', opt);
      if(idx > -1){ //if exists earlier
        if(temp.checked){
          res.sQuota.optArr[idx].ansId = opt;
          res.sQuota.optArr[idx].select = temp.select;
          res.sQuota.optArr[idx].value = temp.limit;
        }else{
          res.sQuota.optArr.splice(idx,1);
        }
      }else{ //if don't exists, push data
        if(temp.checked){
          res.sQuota.optArr.push({
            ansId: opt,
            select: temp.select,
            value: temp.limit
          })
        }
      }
    }


    //validations
    if(res.type == 1){
      if(res.sQuota.quesId == -1){
        this.services.toastr.error('Please select atleast one question.');
        return false;
      }else if(res.sQuota.optArr && res.sQuota.optArr.length == 0){
        this.services.toastr.error('Please select atleast one option.');
        return false;
      }else if(res.sQuota.optArr && res.sQuota.optArr.length){
         for(let item of res.sQuota.optArr){
           if(isNaN(item.value) || item.value == '' || item.value == null || parseInt(item.value) < 1){
             this.services.toastr.error('Limit should be greater than 0.');
             return false;
           }
         }
      }
      res.cQuota = {}
    }else if(res.type == 2){
      if(isNaN(res.cQuota.value) || res.cQuota.value == '' || res.cQuota.value == null || parseInt(res.cQuota.value) < 1){
        this.services.toastr.error('Quota Limit should be greater than 0.');
        return false;
      }else if(res.cQuota.quesInQuota && res.cQuota.quesInQuota.length){
        for(let item of res.cQuota.quesInQuota){
          if(item.quesId == '' || item.quesId == -1){
            this.services.toastr.error('Please select question.');
            return false;
          }else if(item.optArr.length == 0){
            this.services.toastr.error('Please select atleast one option.');
            return false;
          }
        }
      }
      res.sQuota = {}
    }

    let quotaRes = {
      'quota': res
    }

    this.services.resource.saveQuota(this.sId, quotaRes).subscribe((data) => {
      this.services.toastr.success('Quota Logic has been applied successfully.','Logic Applied');
      this.services.form.formObj.quota = this.services.utils.cloneObject(data.quota);
    },(err) => {
      this.services.toastr.error('Error in saving Quota Details.');
    })
  }

}



