import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormArray, FormControl, FormGroup, FormBuilder } from '@angular/forms';

import { SharedServices } from '../../../../../../../shared/services/services.module';
import { LogicService } from '../logic.service';

@Component({
  selector: 'app-promoter-amplification',
  templateUrl: './promoter.amplification.component.html',
  styleUrls: ['../logics.component.scss','./promoter.amplification.component.css']
})
export class PromoterAmplificationComponent implements OnInit {
  @Input() qId: any;

  @Output() logicChanged: EventEmitter<any> = new EventEmitter<any>();
  @Output() canceled: EventEmitter<any> = new EventEmitter<any>();

  question: any;

apiDocObj =
  {
    share: '',
    fa:{
      appId:  '1551121098529209',
      shareType: ''
    },
    go:{
      // appId:  '',
      shareType: ''
    },
    tw:{
      // appId:  '',
      shareType: ''
    },
    li:{
      // appId:  '',
      shareType: ''
    },
    wh:{
      // appId:  '',
      shareType: ''
    }
};
shareContentObj =
{
  fa:{
    shareCaption: '',
    shareMsg: '',
    shareType: '',
    shareUrl: ''
  },
  go:{
    shareMsg: '',
    shareType: '',
    shareUrl: ''
  },
  tw:{
    shareMsg: '',
    shareType: '',
    shareUrl: ''
  },
  li:{
    shareMsg: '',
    shareType: '',
    shareUrl: ''
  },
  wh:{
    shareMsg: '',
    shareType: '',
    shareUrl: ''
  },

};
res = {
 apiDoc:[],
 share:'',
 lType:"normal",
 logic:"promoter",
 metUnmet:1,
 shareContent:[],
 shareText:''
};

logicArr: Array<any> = [];
apiDoc: Array<any> = [];
shareContentArr: Array<any> = [];


  sharety:string = '';
  sharedText:string = 'Thanks for the positive feedback. Please share with your friends.';
  logicName:string = 'promoter';
  buttonFlag:boolean = false;
  fbapiFlag:boolean = false;
  facebookAppIdDefaullt:string = '1551121098529209';
  correctApi:boolean= true;
  tagscheck:boolean= false;
  flagsArr: any;

  constructor(
    private logicService: LogicService,
    private services: SharedServices
  ) { }

  ngOnInit() {
    this.question = this.services.form.getFormData().getQuestionById(this.qId);
    this.flagsArr=[];
    this.flagsArr['fa'] = false;
    this.flagsArr['go'] = false;
    this.flagsArr['tw'] = false;
    this.flagsArr['li'] = false;
    this.flagsArr['wh'] = false;
    this.checkPrevData();
  }

  togglePanelBody(str: string){
    if(!this.flagsArr['fa']){
      this.validatefbApi();
    }
    else{
      this.apiDocObj.fa.appId=this.facebookAppIdDefaullt;
      this.buttonFlag=false;

    }
    this.disableSubmitButton();
  }

  disableSubmitButton(){
    for(let i=0; i < Object.keys(this.flagsArr).length; i++){
       if(!this.shareContentObj[Object.keys(this.flagsArr)[i]].shareUrl.trim()
          && this.flagsArr[Object.keys(this.flagsArr)[i]]){
            this.buttonFlag=true;
            return;
       }

       if(Object.keys(this.flagsArr)[i] == 'fa'
          && !this.apiDocObj.fa.appId.trim()
          && this.flagsArr[Object.keys(this.flagsArr)[i]]){
            this.buttonFlag=true;
            return;
       }
    }
    this.buttonFlag=false;
  }

  validatefbApi(){
    if(!this.flagsArr['fa']){
      return this.buttonFlag=false;
    }
    if(this.apiDocObj.fa.appId == ''){
      this.buttonFlag=true;
      return;
    }
    this.logicService.validateFbAppId(this.apiDocObj.fa.appId).subscribe(
      data=>{
      this.services.toastr.success('Successfully verified the AppId');
      if(this.shareContentObj.fa.shareUrl==''){
        this.buttonFlag=true;
      }
      else
        this.buttonFlag=false;
      this.fbapiFlag=false;
      this.facebookAppIdDefaullt=this.apiDocObj.fa.appId;
    },
      err=>{
      this.services.toastr.error('Please enter the correct Facebook AppId');
      this.buttonFlag=true;
      this.fbapiFlag=true;
    }
    );
  }
  checkPrevData(){
    for(var i=0;i<this.question.logicArr.length;i++){
      if(this.question.logicArr[i].logic=='promoter'){
        for(var j=0;j<this.question.logicArr[i].apiDoc.length;j++){
            let shareVar=this.question.logicArr[i].apiDoc[j].shareType;
            let flag = shareVar+'Flag';
            this.apiDocObj[shareVar]=this.question.logicArr[i].apiDoc[j];
        }
        for(var j=0;j<this.question.logicArr[i].shareContent.length;j++){
            let shareVar=this.question.logicArr[i].apiDoc[j].shareType;
            this.flagsArr[shareVar] = true;
            this.shareContentObj[shareVar]=this.question.logicArr[i].shareContent[j];
        }
      }
    }
  }
  saveLogic(){
    this.logicArr=[];
    this.apiDoc = [];
    this.shareContentArr = [];
    this.sharety='';
    if(this.flagsArr['fa']){
        this.apiDocObj.fa.shareType='fa';
        this.apiDoc.push(this.apiDocObj.fa);
        this.sharety=this.sharety+'fa|';
        this.shareContentObj.fa.shareType='fa';
        this.shareContentArr.push(this.shareContentObj.fa);
      }
    if(this.flagsArr['go']){
        this.apiDocObj.go.shareType='go';
        this.apiDoc.push(this.apiDocObj.go);
        this.sharety=this.sharety+'go|';
        this.shareContentObj.go.shareType='go';
        this.shareContentArr.push(this.shareContentObj.go);
      }
    if(this.flagsArr['tw']){
        this.apiDocObj.tw.shareType='tw';
        this.apiDoc.push(this.apiDocObj.tw);
        this.sharety=this.sharety+'tw|';
        this.shareContentObj.tw.shareType='tw';
        this.shareContentArr.push(this.shareContentObj.tw);
      }
    if(this.flagsArr['li']){
        this.apiDocObj.li.shareType='li';
        this.apiDoc.push(this.apiDocObj.li);
        this.sharety=this.sharety+'li|';
        this.shareContentObj.li.shareType='li';
        this.shareContentArr.push(this.shareContentObj.li);
    }
    if(this.flagsArr['wh']){
        this.apiDocObj.wh.shareType='wh';
        this.apiDoc.push(this.apiDocObj.wh);
        this.sharety=this.sharety+'wh|';
        this.shareContentObj.wh.shareType='wh';
        this.shareContentArr.push(this.shareContentObj.wh);
      }


        this.res.apiDoc = this.apiDoc;
        this.res.share = this.sharety;
        this.res.shareContent = this.shareContentArr;
        this.res.shareText = this.sharedText;
        this.logicArr.push(this.res);
        let logicFormData = this.logicService.getLogicFormData(this.question, this.logicArr, this.logicName);

        this.logicService.saveLogicData(logicFormData, this.question._id).subscribe((data) => {
          this.logicChanged.emit({logicName: this.logicName, res: data});
        });

  }

  removeAllLogic() {
    this.logicService.removeAllLogicByName(this.question, this.logicName).subscribe((data) => {
      this.logicChanged.emit({logicName: this.logicName, res: data});
      this.services.toastr.success('Logic has been successfully removed.');
    });
  }

  cancel() {
    this.canceled.emit();
  }



}



