import { Injectable } from '@angular/core';
import { FormArray } from '@angular/forms';
import { QuestionTypesService } from '../../../../shared/questionTemplates/question-types.service';

@Injectable()
export class SurveyQuestionEditService {

  constructor(
    private quesTypesService: QuestionTypesService
  ) { }

  transposeRowAndCol(completeQuestion: any, quesType: string){
    let rawRowArr = completeQuestion.data.get('fieldOptions.row').getRawValue();
    let rawColArr = completeQuestion.data.get('fieldOptions.col').getRawValue();
    let rowArr = (<FormArray>completeQuestion.data.get('fieldOptions.row'));
    let colArr = (<FormArray>completeQuestion.data.get('fieldOptions.col'));
    let rowLen: number = rowArr.length;
    let colLen: number;
    let naFlag: boolean = false;
    if(quesType == 'matrix-rating' && colArr.at(colArr.length - 1).get('isNa').value == 1){
      naFlag = true;
      colLen = colArr.length - 1;
    }else{
      colLen = colArr.length;
    }
    let i: number = 0;
    if(rowLen < colLen){
      let diff: number = (colLen - rowLen);
      while(i < diff){
        rowArr.insert(rowArr.length, this.quesTypesService.rowColGroup());
        i++;
      }
      i = 0;
      while(i < diff){
        if(naFlag){
          colArr.removeAt(colArr.length - 2);
        }else{
          colArr.removeAt(colArr.length - 1);
        }
        i++;
      }
    }else if(colLen < rowLen){
      i = 0;
      let diff: number = (rowLen - colLen);
      while(i < diff){
        if(naFlag){
          colArr.insert(colArr.length - 1, this.quesTypesService.rowColGroup());
        }else{
          colArr.insert(colArr.length, this.quesTypesService.rowColGroup());
        }
        i++;
      }
      i = 0;
      while(i < diff){
        rowArr.removeAt(rowArr.length - 1);
        i++;
      }
    }

    completeQuestion.data.get('fieldOptions.row').patchValue(rawColArr);
    completeQuestion.data.get('fieldOptions.col').patchValue(rawRowArr);
  }

}



