import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

import { SharedServices } from '../../../../../../shared/services/services.module';
import { QuestionTypesService } from '../../../../../../shared/questionTemplates/question-types.service';
import { LogicService } from './logic.service';

@Component({
  selector: 'app-logics',
  templateUrl: './logics.component.html',
  styleUrls: ['../../../survey.edit.component.scss', './logics.component.scss'],
  providers: [LogicService]
})
export class LogicsComponent implements OnInit {
  @Input() pId: string;
  @Input() qId: string;
  @Input() qType: string;
  @Input() editLogicType: string;

  @Output() logicChanged: EventEmitter<any> = new EventEmitter<any>();

  allLogicName: any = {}
  actionObj: any = {
    name: '',
    prev: ''
  };
  actions: any = {};
  isSkipLogic: boolean = false;
  isDelayedBranching: boolean = true;
  isDisplayLogic: boolean = true;
  isTicketGeneration: boolean = true;
  isPromoter: boolean = false;
  isEmbedded: boolean = true;
  isRedirection: boolean = true;
  logicDisplayObj: any;

  constructor(
    private logicServ: LogicService,
    private qTypesServ: QuestionTypesService,
    private services: SharedServices
  ) { }

  ngOnInit() {
    if(this.editLogicType){
      this.actionObj.name = this.editLogicType;
    }
    this.allLogicName = this.logicServ.allLogicName;
    this.setLogicVisibility();
    this.setLogicArray();
  }

  setLogicVisibility() {
    this.qTypesServ.questionTypesJSON[this.qType].actions.forEach(item=> {
      this.actions[item.value] = true;
    });
    let question = this.services.form.getFormData().getQuestionById(this.qId);
    let fieldType = question.fieldType;
    if(fieldType.family == "multiChoice" || (fieldType.subtype == "radio" && fieldType.subtype == "checkbox" && fieldType.subtype == "dropdown")) {
      this.isSkipLogic = true;
    }
    if(fieldType.family == "matrix" && fieldType.subtype == "rating") {
      this.isSkipLogic = true;
      this.isPromoter = true;
    }
  }

  setLogicArray() {
    this.logicDisplayObj = {};
    let question = this.services.form.getFormData().getQuestionById(this.qId);
    this.logicServ.logicNameArr.forEach(logicName => {
      let logicArr = [];
      question.logicArr.filter(logic => {
        return logic.logic === logicName;
      }).forEach(logic=> {
        let logicItem = {
          isCorrupt: logic.isCorrupt,
          metUnmet: logic.metUnmet
        }
        if(logicName === this.allLogicName.skip.name) {
          logicItem['optionText'] = this.services.utils.getTextOutOfHtml(question.getOptionById(logic.ansId).text);
          let skipTo = this.getRedirectQuestion(logic.lType, logic.destQId);
          logicItem['skipTo'] = skipTo;
        } else if (logicName === this.allLogicName.compound.name) {
          if (logic.metUnmet == 1) {
            let criteria = this.services.form.formObj.getCriteriaById(logic.ansId);
            logicItem['criteria'] = criteria ? criteria.name : '';
          }
          let skipTo = this.getRedirectQuestion(logic.lType, logic.destQId);
          logicItem['skipTo'] = skipTo;
        } else if (logicName === this.allLogicName.display.name) {
          logicItem['optArr'] = logic.optArr;
          if (logic.metUnmet == 1) {
            let criteria = this.services.form.formObj.getCriteriaById(logic.ansId);
            logicItem['criteria'] = criteria ? criteria.name : '';
          }
          let skipTo = this.getRedirectQuestion(logic.lType, logic.destQId);
          logicItem['skipTo'] = skipTo;
        }
        logicArr.push(logicItem);
      });
      this.logicDisplayObj[logicName] = logicArr;
    });
  }

  getRedirectQuestion(lType: string, destQId: string): string {
    let result = '';
    if (lType === 'normal') {
      let countObj: any = this.services.form.formObj.getQuestionsCount();
      if(countObj.hasOwnProperty(destQId)){
        let qCount = countObj[destQId];
        result = qCount ? 'Question ' + qCount: 'Statement';
      }else{
        result = null;
      }
    }
    (lType === 'disqua') && (result = 'Disqualification Page');
    (lType === 'end') && (result = 'End Page');
    (lType === 'end') && (result = 'End Page');
    return result
  }

  changeView(viewName?: string) {
    this.actionObj.prev = this.actionObj.name;
    this.actionObj.name = viewName || '';
  }

  gotoCarryForward(scale: number) {
    this.actionObj.scale = scale;
    this.changeView('Carry Forward');

  }

  onChange(event) {
    // console.log(event, this.services.form.getFormData());
    // this.logicChanged.emit(event);
    this.changeView(event.logicName);
  }

  onChangeLogic(event) {
    try {
      let question = this.services.form.getFormData().getQuestionById(this.qId);
      if(event.res) {
        question.setLogicArray(event.res.resBody.logicArr);
        this.setLogicArray();
      }
      this.changeView(event.view);
      this.services.form.allQuestionsOfSurvey[this.qId] = question;
      this.services.form.setLogicNamesArr(this.pId, this.qId, event.res.resBody.logicArr);
    } catch (ex) { console.error(ex); }
  }

  onChangeCriteria(event) {
    if (event === 'saved' || event === 'canceled') {
      this.changeView(this.actionObj.prev);
    }
  }

  onChangeRdmz(event){
    this.logicChanged.emit(event.ranInpData);
  }

}



