import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormArray, FormControl, FormGroup, FormBuilder } from '@angular/forms';

import { SharedServices } from '../../../../../../../shared/services/services.module';
import { QuestionTypesService } from '../../../../../../../shared/questionTemplates/question-types.service';
import { LogicService } from '../logic.service';

@Component({
  selector: 'app-display-logic',
  templateUrl: './display.logic.component.html',
  styleUrls: ['../logics.component.scss','./display.logic.component.scss']
})
export class DisplayLogicComponent implements OnInit {
  @Input() qId: string;

  @Output() logicChanged: EventEmitter<any> = new EventEmitter<any>();
  @Output() canceled: EventEmitter<any> = new EventEmitter<any>();

  question: any;
  criteriaArr: any[];
  logicFormGroup: FormGroup;
  questionList: any;
  additionalOptions: Array<any>;
  appliedLogicsLength: number;
  logicName: string = this.logicService.allLogicName.display.name;
  additionalKeysInLogic: any = {
    "optArr":[]
  };
  isLogicOnOptions: boolean = false;
  fieldOptions: any[] = [];

  constructor(
  	private logicService: LogicService,
    private qTypesService: QuestionTypesService,
    private services: SharedServices
 	) {}

  ngOnInit() {
    this.question = this.services.form.getFormData().getQuestionById(this.qId);
    this.criteriaArr = this.services.form.getFormData().getAllCriterias();
  	this.questionList = this.services.form.getQuestionsAfter(this.question._id)
  	this.setAppliedLogic();
  }

  setAppliedLogic() {
    let question = this.services.utils.cloneObject(this.question);
  	let logicArr = question.logicArr.filter((logic) => {return logic.logic == this.logicName;});
  	if(!logicArr.length) {
  		question.logicArr = [this.getLogicData()];
  	} else {
  		(logicArr.length > 1) && (this.isLogicOnOptions = true);
      question.logicArr = logicArr;
  	}
    this.buildCriteriaForm(question);
    this.appliedLogicsLength = this.logicFormGroup.get('logicArr')["controls"].length;
    this.setFieldOptions(question, logicArr);
  }

  setFieldOptions(question: any, logicArr: any[]) {
    this.fieldOptions = [];
    let carryOptions: any = this.logicService.getCarryOption(question._id);
    carryOptions.topArray.forEach(option => {
      let logic = this.logicService.getLogicOnOption(logicArr, option, this);
      this.fieldOptions.push(logic);
    });
    question.fieldOptions.row.forEach((option) => {
      let logic = this.logicService.getLogicOnOption(logicArr, option, this);
      this.fieldOptions.push(logic);
    });
    question.fieldOptions.col.forEach((option) => {
      let logic = this.logicService.getLogicOnOption(logicArr, option, this);
      this.fieldOptions.push(logic);
    });
    carryOptions.bottomArray.forEach(option => {
      let logic = this.logicService.getLogicOnOption(logicArr, option, this);
      this.fieldOptions.push(logic);
    });
  }

  buildCriteriaForm(question: any) {
    if(this.isLogicOnOptions){
      this.logicFormGroup = this.logicService.buildCriteriaForm(question, this.additionalKeysInLogic);
    }else{
      this.logicFormGroup = this.logicService.buildCriteriaForm(question);
    }
  }

  addNewCondition() {
  	this.logicFormGroup.get('logicArr')["push"](this.logicService.getFormGroup(this.getLogicData()));
    this.appliedLogicsLength = this.logicFormGroup.get('logicArr')["controls"].length;
  }

  saveLogic() {
    var logicArr = this.logicFormGroup.value.logicArr;
    if(logicArr.length > 1) {
      logicArr.forEach((logic, index) => {
        if(index == logicArr.length - 1) {
          logic.metUnmet = 0;
          logic.ansId = logicArr[0].ansId;
        }
      });
    }

    let logicFormData = this.logicService.getLogicFormData(this.question, logicArr, this.logicName);

    this.logicService.saveLogicData(logicFormData, this.question._id).subscribe((data) => {
      this.logicChanged.emit({logicName: this.logicName, res: data});
    });
  }

  updateLogic(logic: any, index: number) {

  }

  updateLogicForOption(event) {
    let question = this.services.utils.cloneObject(this.question);
    let qtype = this.qTypesService.getQuestionType(question.fieldType.family, question.fieldType.subtype);
    if (['single-text', 'ranking', 'graph-rating', 'nps', 'contacts', 'comment', 'statement', 'file', 'slider'].indexOf(qtype.type) != -1) {
      this.services.toastr.error("Option Dsiplay Logic cant be applied on this type of question.", "Logical Error");
      this.isLogicOnOptions = false;
      event.target.checked = false
      return;
    }
    let logicArr = question.logicArr.filter((logic) => {return logic.logic == this.logicName;});
    if(this.isLogicOnOptions == true) {
      if (logicArr.length > 1) {
        this.setAppliedLogic();
      } else {
        question.logicArr = [this.getLogicData(), this.getLogicData()]
        this.buildCriteriaForm(question);
      }
    } else {
      if (logicArr.length > 1) {
        question.logicArr = [this.getLogicData()]
        this.buildCriteriaForm(question);
      } else {
        this.setAppliedLogic();
      }
    }
    this.appliedLogicsLength = this.logicFormGroup.get('logicArr')["controls"].length;
  }

  deleteLogic(index: number) {
  	if(this.appliedLogicsLength > 2) {
	  	this.logicFormGroup.get('logicArr')["removeAt"](index);
      this.appliedLogicsLength = this.logicFormGroup.get('logicArr')["controls"].length;
	  }
  }

  getLogicData(option?: any) {
    let logicObject = this.services.utils.cloneObject(this.logicService.logicData);
    if(this.isLogicOnOptions){
      for(let key in this.additionalKeysInLogic) {
        logicObject[key] = this.services.utils.cloneObject(this.additionalKeysInLogic[key]);
      }
    }
    logicObject["logic"] = this.logicName;
    logicObject["lType"] = "normal";
    logicObject["text"] = option ? option.text : '';
    logicObject["id"] = option ? option._id : '';
    return logicObject;
  }

  removeAllLogic() {
    this.logicService.removeAllLogicByName(this.question, this.logicName).subscribe((data) => {
      this.logicChanged.emit({logicName: this.logicName, res: data});
      this.services.toastr.success('Logic has been successfully removed.');
    });
  }

  showCriteriaForm(event) {
    (event.target.value == '0') && this.logicChanged.emit({view: 'Criteria'})
  }

  cancel() {
    this.canceled.emit();
  }

}



