import { Component, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { FormArray, FormControl, FormGroup } from '@angular/forms';
import { TicketRuleService } from './ticket-rule.service';
import { SharedServices } from '../../../../../shared/services/services.module';
import { BsModalService } from 'ngx-bootstrap/modal';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';

@Component({
  selector: 'app-ticket-rule',
  templateUrl: './ticket-rule.component.html',
  styleUrls: ['../survey.advance.component.scss','./ticket-rule.component.css']
})
export class TicketRuleComponent implements OnInit {
  ticketRuleForm: FormGroup;
  editTicketFormJSON: any;
  updateOrNew: boolean = false; //update--> true, new--> false
  allTicRules: any;
  allUserGroupsObj: any;
  allUsersObj: any;
  allSurveyList: any;
  triggerSurveyMethods: any;
  ownersForSelGroup: any;
  ticketTags: any = {
    types: [],
    contactFields: [],
    surveyQuestions: []
  }
  linkedSurveyObj:any = {
    feedback: false,
    satisfy: false
  }
  tagsDataArr: Array<any> = [];
  folderDetails: any = {};
  watchersArr: Array<any> = [];
  selWatcherIds: Array<any> = [];

  @ViewChild('selFolderModal') selFolderModal;
  @ViewChild('ticRuleSelect') ticRuleSelect;
  modalRef: BsModalRef;

  openModal(template: TemplateRef<any>) {
    this.modalRef = this.modalService.show(template, { class: 'modal-lg' });
  }

  hideModal(): void {
    this.modalRef.hide();
  }

  constructor(
    private ticRuleService: TicketRuleService,
    private services: SharedServices,
    private modalService: BsModalService
  ) { }

  ngOnInit() {
    this.getAllTicketRules();
    this.getUsers();
    this.getAllsurveys();
    this.ticketTags.types = this.services.constants.tagTypes;
    this.ticketTags.contactFields = this.services.constants.contactFields;
    this.ticketTags.surveyQuestions = this.services.form.getAllQuestions().filter(item => (item.type == 'q')); //this will get only questions (excluding statements)
    this.triggerSurveyMethods = this.ticRuleService.triggerSurveyMethods;
    this.newTicRule();
    console.log(this.ticketRuleForm);
  }

  getAllTicketRules(){
    this.services.resource.getTicketRules().subscribe((data) => {
      this.allTicRules = data;
      console.log(this.allTicRules);
    },(err) => {
      console.log('All Tickets could not be fetched');
    })
  }

  getAllsurveys(){
    this.services.resource.getSurveyList().subscribe((data)=> {
      this.allSurveyList = data.data.list;
    },(err) => {
      console.log('Complete Survey List could not be fetched');
    })
  }

  getUserGroups(usersArr?: any){
    this.services.resource.getUserGroups().subscribe((data) => {
      this.allUserGroupsObj = data;
      console.log(this.allUserGroupsObj);
      let groupsArr = [];
      this.allUserGroupsObj.res.forEach((item) => {
        groupsArr.push({
          text: item.name,
          id: item._id,
          type: 'Groups'
        })
      })
      this.makeWatchersArr(usersArr, groupsArr);
    },(err) => {
      console.log('User Groups could not be fetched');
    })
  }

  getUsers(){
    this.services.resource.getUsers().subscribe((data) => {
      this.allUsersObj = data;
      console.log(this.allUsersObj);
      let usersArr = [];
      this.allUsersObj.userList.forEach((item) => {
        usersArr.push({
          text: item.name.first + ' ' + item.name.last,
          id: item._id,
          type: 'Users'
        })
      });
      this.getUserGroups(usersArr);
    },(err) => {
      console.log('Users could not be fetched');
    })
  }

  makeWatchersArr(usersArr: any, groupsArr: any){
    let temp: Array<any> = [];
    let children: Array<any> = [];
    usersArr.forEach((item) => {
      children.push(item);
    })
    temp.push({
      id: 1,
      text: 'Users',
      children: children
    })
    children = [];
    groupsArr.forEach((item) => {
      children.push(item);
    })
    temp.push({
      id: 2,
      text: 'Groups',
      children: children
    })
    this.watchersArr = temp;
  }

  selected(value:any) {
    let temp: Array<any> = [];
    let idx: number = 0;
    while(idx < this.selWatcherIds.length){
      temp.push(this.selWatcherIds[idx]);
      idx++;
    }
    let prevIdx: number = this.services.utils.findIndex(temp, 'id', value.id);
    if(prevIdx > -1){
      this.selWatcherIds = temp;
      return false;
    }
    temp.push({
      text: value.text,
      id: value.id
    });
    if(value.parent.text == 'Groups'){
      (<FormArray>this.ticketRuleForm.get('watcherGroups')).push(new FormControl(value.id));
    }else if(value.parent.text == 'Users'){
      (<FormArray>this.ticketRuleForm.get('watcherUsers')).push(new FormControl(value.id));
    }
    this.selWatcherIds = temp;
  }

  removed(value: any){
    let obj = this.ticketRuleForm.getRawValue();
    let gIdx = obj.watcherGroups.indexOf(value.id);
    if(gIdx > -1){
      (<FormArray>this.ticketRuleForm.get('watcherGroups')).removeAt(gIdx);
    }else{
      let uIdx = obj.watcherUsers.indexOf(value.id);
      if(uIdx > -1){
        (<FormArray>this.ticketRuleForm.get('watcherUsers')).removeAt(uIdx);
      }
    }
    let temp: Array<any> = [];
    let idx: number = 0;
    while(idx < this.selWatcherIds.length){
      temp.push(this.selWatcherIds[idx]);
      idx++;
    }
    let prevIdx: number = this.services.utils.findIndex(temp, 'id', value.id);
    if(prevIdx > -1){
      temp.splice(prevIdx, 1);
      this.selWatcherIds = temp;
    }
  }

  getOwnersForGroup(grpId: string){
    this.services.resource.getUsersForGroupId(grpId).subscribe((data) => {
      this.ownersForSelGroup = data;
      console.log(this.ownersForSelGroup);
    },(err) => {
      this.services.toastr.error(err._body);
    });
  }

  setOwnersForGroup(grpId: string){
    if(grpId == ''){
      this.ownersForSelGroup = [];
    }else{
      this.getOwnersForGroup(grpId);
    }
    this.ticketRuleForm.get('assignee').patchValue('');
  }

  selectTicketRule(id: any){
    console.log(id);
    if(id == -1){
      this.newTicRule();
      return false;
    }
    let idx: number = this.services.utils.findIndex(this.allTicRules,'_id',id);
    if(idx > -1){
      this.editTicketFormJSON = this.services.utils.cloneObject(this.allTicRules[idx]);
      this.editTicketFormJSON = this.services.utils.extendObjectValue(this.editTicketFormJSON, this.ticRuleService.ticketRuleJSON);
      this.ticketRuleForm = this.ticRuleService.buildTicketRuleFormGroup(this.editTicketFormJSON);
      this.selectOrResetTicRule();
      this.updateOrNew = true;
      console.log(this.ticketRuleForm);
    }
  }

  selectOrResetTicRule(){
    if(this.editTicketFormJSON.grpId != ''){
      this.getOwnersForGroup(this.editTicketFormJSON.grpId);
    }
    this.setSelectedWatcherIds();
    this.makeTagsDataArr();
    if(this.editTicketFormJSON.linkedSurvey.feedback.surId){
      this.linkedSurveyObj.feedback = true;
    }else{
      this.linkedSurveyObj.feedback = false;
      this.ticketRuleForm.get('linkedSurvey.feedback.surId').patchValue('');
      this.ticketRuleForm.get('linkedSurvey.feedback.method').patchValue('');
    }
    if(this.editTicketFormJSON.linkedSurvey.satisfy.surId){
      this.linkedSurveyObj.satisfy = true;
    }else{
      this.linkedSurveyObj.satisfy = false;
      this.ticketRuleForm.get('linkedSurvey.satisfy.surId').patchValue('');
      this.ticketRuleForm.get('linkedSurvey.satisfy.method').patchValue('');
    }
  }

  resetTicketRule(){
    this.ticketRuleForm = this.ticRuleService.buildTicketRuleFormGroup(this.editTicketFormJSON);
    if(this.editTicketFormJSON.folderId == ''){
      this.folderDetails = {}
    }
    this.selectOrResetTicRule();
  }

  setSelectedWatcherIds(){
    let selWatcherIds = [];
    this.editTicketFormJSON.watcherUsers.forEach((item) => {
      let idx = this.services.utils.findIndex(this.allUsersObj.userList, '_id', item);
      if(idx > -1){
        let name = this.allUsersObj.userList[idx].name.first + ' ' + this.allUsersObj.userList[idx].name.last;
        selWatcherIds.push({
          text: name,
          id: item
        });
      }
    })
    this.editTicketFormJSON.watcherGroups.forEach((item) => {
      let idx = this.services.utils.findIndex(this.allUserGroupsObj.res, '_id', item);
      if(idx > -1){
        let name = this.allUserGroupsObj.res[idx].name;
        selWatcherIds.push({
          text: name,
          id: item
        });
      }
    });
    this.selWatcherIds = selWatcherIds;
  }

  makeTagsDataArr(){
    this.tagsDataArr = [];
    for(let type in this.editTicketFormJSON.ticketData){
      for(let item in this.editTicketFormJSON.ticketData[type]){
        let obj = this.editTicketFormJSON.ticketData[type][item];
        this.addNewTag(obj.name, type, obj.id);
      }
    }
    if(this.tagsDataArr.length == 0){
      this.addNewTag();
    }
    console.log(this.tagsDataArr);
  }

  addNewTag(name?: string, type?: string, id?: string){
    this.tagsDataArr.push({
      name: (name ? name : ''),
      type: (type ? type : ''),
      id: (id ? id : '')
    })
  }

  removeTag(idx: number){
    if(this.tagsDataArr.length == 1){
      this.services.toastr.warning('Atleast one tag is required.','Cannot be removed');
    }else{
      this.tagsDataArr.splice(idx, 1);
    }
  }

  resetTagId(idx: number){
    this.tagsDataArr[idx].id = '';
  }

  browseFolder(){
    this.openModal(this.selFolderModal);
  }

  onFolderSelect(e: any){
    this.hideModal();
    this.folderDetails = e;
    this.ticketRuleForm.get('folderId').patchValue(e.selFolderId);
    if(e.libType == 1){
      this.ticketRuleForm.get('owner.typeOf').patchValue("MyLibrary");
      this.ticketRuleForm.get('owner.id').patchValue('');
    }else{
      this.ticketRuleForm.get('owner.typeOf').patchValue("GroupLibrary");
      this.ticketRuleForm.get('owner.id').patchValue(e.grpId);
    }
  }

  makeTicketTags(){
    let ticketTags: any = {
      contactsData: [],
      embedData: [],
      manualData: [],
      questions: []
    }
    for(let item of this.tagsDataArr){
      if(item.name == '' || item.id == ''){
        return {};
      }
      if(item.type == 'contactsData'){
        ticketTags['contactsData'].push({
          name: item.name,
          id: item.id
        })
      }else if(item.type == 'embedData'){
        ticketTags['embedData'].push({
          name: item.name,
          id: item.id
        })
      }else if(item.type == 'manualData'){
        ticketTags['manualData'].push({
          name: item.name,
          id: item.id
        })
      }else if(item.type == 'questions'){
        ticketTags['questions'].push({
          name: item.name,
          id: item.id
        })
      }
    }
    return ticketTags;
  }

  deleteTicketRule(){
    this.services.alert({
      title: '<p class="sweetalert_title">Are you sure you want to delete this Ticket Rule?</p>',
      text: "",
      type: 'warning',
      showCancelButton: true,
      buttonsStyling: false,
      confirmButtonClass: 'sweetalert_apply_btn',
      cancelButtonClass: 'sweetalert_cancel_btn',
      confirmButtonText: 'Yes, delete it!'
    }).then((result) => {
      if (result.value) {
        let ticRuleId: string = this.editTicketFormJSON._id;
        this.services.resource.deleteTicketRule(ticRuleId).subscribe((data) => {
          let idx: number = this.services.utils.findIndex(this.allTicRules, '_id', ticRuleId);
          if(idx > -1){
            this.allTicRules.splice(idx, 1);
            this.services.toastr.success('Ticket Rule has been deleted successfully.', 'Deleted');
            this.newTicRule();
          }
        },(err) => {
          this.services.toastr.error('Error in delting ticket rule.');
        })
      }
    })
  }

  newTicRule(){
    this.editTicketFormJSON = this.services.utils.cloneObject(this.ticRuleService.ticketRuleJSON);
    this.ticketRuleForm = this.ticRuleService.buildTicketRuleFormGroup(this.editTicketFormJSON);
    this.makeTagsDataArr();
    this.selWatcherIds = [];
    this.folderDetails = {};
    this.ticRuleSelect.nativeElement.value = -1;
    this.linkedSurveyObj.feedback = false;
    this.linkedSurveyObj.satisfy = false;
    this.updateOrNew = false;
  }

  saveOrUpdateTicketRule(){
    let res = this.ticketRuleForm.getRawValue();
    if(this.updateOrNew == false){ //this is the case of saving new ticket rule
      if(res.folderId == ''){
        this.services.toastr.error('Please select a folder to save ticket.','Folder not selected');
        return false;
      }
      delete res._id;
      delete res.linkedSurvey.feedback._id;
      delete res.linkedSurvey.satisfy._id;
      if(res.owner.id == ''){
        delete res.owner.id;
      }
    }else{
      delete res.folderId;
    }

    res['ticketData'] = this.makeTicketTags();
    res['tags'] = [];
    for(let type in res['ticketData']){
      if(res['ticketData'][type].length == 0){
        delete res['ticketData'][type];
      }else{
        res['tags'].push(type);
      }
    }
    if(res['tags'].length == 0){
      this.services.toastr.error('Please enter Tag Details.','Tags Required');
      return false;
    }
    if(this.linkedSurveyObj.feedback == false){
      res.linkedSurvey.feedback = {};
    }else{
      if(res.linkedSurvey.feedback.surId == '' || res.linkedSurvey.feedback.method == ''){
        this.services.toastr.error('Please fill respective details for feedback survey.');
        return false;
      }
    }
    if(this.linkedSurveyObj.satisfy == false){
      res.linkedSurvey.satisfy = {};
    }else{
      if(res.linkedSurvey.satisfy.surId == '' || res.linkedSurvey.satisfy.method == ''){
        this.services.toastr.error('Please fill respective details for satisfation survey.');
        return false;
      }
    }

    console.log(res);
    if(this.updateOrNew == false){
      this.services.resource.saveTicketRule(res).subscribe((data) => {
        this.allTicRules.push(data);
        this.services.toastr.success('Ticket Rule has been saved successfully.', 'Saved');
        this.newTicRule();
      },(err) => {
        this.services.toastr.error('Error in saving Ticket Rule.')
      })
    }else{
      let idx: number = this.services.utils.findIndex(this.allTicRules, '_id', res._id);
      this.services.resource.updateTicketRule(res._id, res).subscribe((data) => {
        this.allTicRules.splice(idx, 1, data);
        this.services.toastr.success('Ticket Rule has been updated successfully.', 'Updated');
        this.newTicRule();
      },(err) => {
        this.services.toastr.error('Error in updating Ticket Rule.')
      })
    }
  }
}



