import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

import { SharedServices } from "../../../../../../shared/services/services.module";
import { CarryForwardService } from './carry.forward.service';
import { EditDataService } from '../../../edit.data.service';

@Component({
  selector: 'app-carry-forward',
  templateUrl: './carry.forward.component.html',
  styleUrls: ['../logics/logics.component.scss', './carry.forward.component.scss'],
  providers: [CarryForwardService]
})
export class CarryForwardComponent implements OnInit {
  @Input() qId;
  @Input() scale;

  @Output() carryForwardChanged: EventEmitter<any> = new EventEmitter<any>();
  @Output() canceled: EventEmitter<any> = new EventEmitter();

  carryQuestions: any // Question list
  carryJSON: any;
  carryMatrixJSON: any;
  frmCarryForward: Array<any> = []; // Carry Forward Array
  carryObj: any = { // Carry forward Structure
    quesId: '',
    type: 0,
    scale: 0,
    rowId: null,
    colId: null,
    weightDoc: {},
    wtOrInd: 0,
    carryAtTop: 0
  };
  carryTypes: Array<Array<any>> = []; // Types array for each carry forward object
  carryTypeChoiceArray: Array<any> = []; // Types value array for each carry forward object
  allQuestionsOfSurvey: any;
  carryForwardLength: number;
  constructor(private services: SharedServices,private carryService : CarryForwardService, private editService: EditDataService) {

  }

  ngOnInit() {

    this.allQuestionsOfSurvey = this.services.form.allQuestionsOfSurvey;
    this.carryJSON = this.carryService.getCarryJSON();
    this.carryMatrixJSON = this.carryService.getCarryMatrixJSON();
    let type = ['single-choice', 'mul-choice', 'dropdown', 'matrix-rating'];
    let pageId = this.services.form.formObj.getPageByQuestionId(this.qId)._id;
    let cQuestions = this.services.form.getQuestionsBeforeCurrPage(pageId);
    this.carryQuestions = [];
    for (let qIndex = 0; qIndex < cQuestions.length; qIndex++) {
      if (type.indexOf(cQuestions[qIndex].type.type) > -1) {
        this.carryQuestions.push(cQuestions[qIndex]);
      }
    }
    let ques = this.services.form.formObj.getQuestionById(this.qId);
    if (ques.frmCarryForward && ques.frmCarryForward.length) {
      this.frmCarryForward = this.services.utils.cloneObject(ques.frmCarryForward);
    } else {
      this.carryObj["scale"] = this.scale;
      this.frmCarryForward.push(this.services.utils.cloneObject(this.carryObj));
    }
    for (let i = 0; i < this.frmCarryForward.length; i++) {
      if (this.frmCarryForward[i].quesId) {
        this.frmCarryForward[i]["scale"] = this.scale;
        this.carryTypeOnQuestionType(this.frmCarryForward[i].quesId, i);
        let carType = this.frmCarryForward[i].type;
        let rowId = this.frmCarryForward[i].rowId;
        let colId = this.frmCarryForward[i].colId;
        for (let k = 0; k < this.carryTypes[i].length; k++) {
          if (this.carryTypes[i][k].val == carType && (colId && this.carryTypes[i][k].colId == colId || rowId && this.carryTypes[i][k].rowId == rowId || !(rowId || colId))) {
            let obj = {};
            obj["value"] = k;
            this.carryTypeChoiceArray.push(obj);
          }
        }
      } else {
        this.carryTypeChoiceArray.push({ value: "" });
      }
    }
  }
  addCarryArea(index) {
    this.frmCarryForward.push(this.services.utils.cloneObject(this.carryObj));
    this.carryTypes.push([]);
    this.carryTypeChoiceArray.push({ value: "" });
  }
  removeCarryArea(index) {
    this.frmCarryForward.splice(index, 1);
    this.carryTypes.splice(index, 1);
    this.carryTypeChoiceArray.push(index, 1);
  }
  removeCarryForward() {
      this.services.alert({
        title: '<p class="sweetalert_title">Are you sure you want to delete?</p>',
        text: "",
        type: 'warning',
        showCancelButton: true,
        buttonsStyling: false,
        confirmButtonClass: 'sweetalert_apply_btn',
        cancelButtonClass: 'sweetalert_cancel_btn',
        confirmButtonText: 'Yes, delete it!'
      }).then((result) => {
        if (result.value) {
            let carryForward = this.services.utils.cloneObject(this.allQuestionsOfSurvey[this.qId].frmCarryForward);
            this.services.resource.updateQuestionData(this.qId, { frmCarryForward: [] }).subscribe((data) => {
              this.services.form.formObj.getQuestionById(this.qId).setFrmCarryForward(data.resBody.frmCarryForward);
              /*  Removing Present Question ID from toCarryForward of carried QUestions */
              for(let i = 0; i < carryForward.length; i++) {
                  let cQId = carryForward[i].quesId;
                  if(this.allQuestionsOfSurvey[cQId].toCarryForward && this.allQuestionsOfSurvey[cQId].toCarryForward.length) {
                      let idx = this.allQuestionsOfSurvey[cQId].toCarryForward.indexOf(this.qId);
                      this.allQuestionsOfSurvey[cQId].toCarryForward.splice(idx,1);
                  }
              }
              /*  Removing Present Question ID from toCarryForward of carried QUestions */
              this.services.toastr.success("Question has been updated successfully.", "Success");
            });
            this.frmCarryForward=[];
            this.canceled.emit();
        }
    });
 }
  carryTypeOnQuestionType(qId, index) {
    if (this.frmCarryForward.length != 1) {
      for (let fC = 0; fC < this.frmCarryForward.length; fC++) {
        if (this.frmCarryForward[fC].quesId == qId && fC !== index) {
          this.frmCarryForward[fC] = this.services.utils.cloneObject(this.carryObj);
          this.carryTypes[fC] = [];
          this.carryTypeChoiceArray[fC] = { value: "" };
          break;
        }
      }
    }
    this.carryTypes[index] = [];
    if (qId) {
      let ques = this.services.form.formObj.getQuestionById(qId);
      let rows = ques.getRowArray();
      let jsonArrayToAdd = null;
      if (ques.fieldType.family == 'multiChoice') {
        jsonArrayToAdd = this.services.utils.cloneObject(this.carryJSON);
        let flag = 0;
        for (let j = 0; j < rows.length; j++) {
          if (rows[j].hasText || rows[j].isComment) {
            flag = 1;
          }
        }
        if (!flag) {
            jsonArrayToAdd.splice(5, 2);
        }
      } else {
        jsonArrayToAdd = this.services.utils.cloneObject(this.carryMatrixJSON);
        let cols = ques.getColArray();
        for (let i = 0; i < cols.length; i++) {
          let obj = {};
          obj["val"] = 12;
          obj["text"] = 'Selected Rows - ' + this.services.utils.getTextOutOfHtml(cols[i].text) + ' Selected choices';
          obj["colId"] = cols[i]._id;
          jsonArrayToAdd.push(obj);
          obj = {}
          obj["val"] = 13;
          obj["text"] = 'Unselected Rows - ' + this.services.utils.getTextOutOfHtml(cols[i].text) + ' Selected choices';
          obj["colId"] = cols[i]._id;
          jsonArrayToAdd.push(obj);
        }
        for (let j = 0; j < rows.length; j++) {
          if (!(rows[j].hasText || rows[j].isComment)) {
            jsonArrayToAdd.splice(8, 1);
          }
          let obj = {};
          obj["val"] = 14;
          obj["text"] = 'Selected Columns - ' + this.services.utils.getTextOutOfHtml(rows[j].text);
          obj["rowId"] = rows[j]._id;
          jsonArrayToAdd.push(obj);
        }
      }
      for (let idx = 0; idx < jsonArrayToAdd.length; idx++) {
        jsonArrayToAdd[idx]["scale"] = this.scale;
      }
      this.carryTypes[index] = jsonArrayToAdd;
    }
  }

  setData(cTypeIndex, index) {
    if (cTypeIndex) {
      this.frmCarryForward[index].type = this.carryTypes[index][cTypeIndex].val;
      this.frmCarryForward[index].scale = this.carryTypes[index][cTypeIndex].scale;
      if (this.carryTypes[index][cTypeIndex].hasOwnProperty('rowId')) {
        this.frmCarryForward[index].rowId = this.carryTypes[index][cTypeIndex].rowId;
      } else {
        this.frmCarryForward[index].rowId = null;
      }
      if (this.carryTypes[index][cTypeIndex].hasOwnProperty('colId')) {
        this.frmCarryForward[index].colId = this.carryTypes[index][cTypeIndex].colId;
      } else {
        this.frmCarryForward[index].colId = null;
      }
    }
  }
  saveCarryForward() {
    for (let i = 0; i < this.frmCarryForward.length; i++) {
      if (!this.frmCarryForward[i].quesId || this.frmCarryForward[i].type == 0) {
        this.services.toastr.error("All fields must have a value", "Error");
        return;
      }
    }
    this.services.resource.updateQuestionData(this.qId, { frmCarryForward: this.frmCarryForward }).subscribe((data) => {
      this.services.form.formObj.getQuestionById(this.qId).setFrmCarryForward(data.resBody.frmCarryForward);
      /*  Adding Present Question ID to toCarryForward of carried QUestions */

      for(let i = 0; i < this.frmCarryForward.length; i++) {
          let qId = this.frmCarryForward[i].quesId;
          if(this.allQuestionsOfSurvey[qId].toCarryForward && this.allQuestionsOfSurvey[qId].toCarryForward.length) {
              if(!(this.allQuestionsOfSurvey[qId].toCarryForward.indexOf(this.qId) > -1)) {
                  this.allQuestionsOfSurvey[qId].toCarryForward.push(this.qId);
              }
          } else {
              this.allQuestionsOfSurvey[qId].toCarryForward = [];
               this.allQuestionsOfSurvey[qId].toCarryForward.push(this.qId);
          }
      }
      /*  Adding Present Question ID to toCarryForward of carried QUestions */
      this.services.toastr.success("Question has been updated successfully.", "Success");
      this.canceled.emit();
    });
  }
  cancelCarryForward() {
    this.frmCarryForward = [];
    this.carryTypes = [];
    this.carryTypeChoiceArray = [];
    this.canceled.emit();
  }
  changeCarryDetails(key, event, index) {
    if (event.target.checked) {
      this.frmCarryForward[index][key] = 1;
    } else {
      this.frmCarryForward[index][key] = 0;
    }
  }
}



