import { Component, OnInit, ViewChild, OnDestroy } from '@angular/core';
import { Subscription } from 'rxjs/Subscription';
import { SharedServices } from '../../../../shared/services/services.module';
import { EditDataService } from '../edit.data.service';
import { LogoService } from '../survey.main-list/logo.service';
import { ThemeService } from '../survey.main-list/theme.service';

@Component({
  selector: 'app-survey-settings',
  templateUrl: './survey.settings.component.html',
  styleUrls: ['../survey.edit.component.scss','./survey.settings.component.scss']
})
export class SurveySettingsComponent implements OnInit, OnDestroy {
  @ViewChild('progBarPages') progBarPages;
  @ViewChild('progBarPerc') progBarPerc;
  // @ViewChild('surveyLogo') surveyLogo;

  sId: string;
  surveyData: any;
  formObj: any;
  allQuestionsInFormGroup: any;
  logoDetails: any;
  subscription: Subscription;
  show_progressbr_config: boolean;
  btnText: any = {
    previous: {
      text: '',
      edit: false
    },
    next: {
      text: '',
      edit: false
    },
    done: {
      text: '',
      edit: false
    },
    exit: {
      text: '',
      edit: false
    },
    endBtn: {
      text: '',
      edit: false
    }
  }
  themeData: any;
  themeDetails: any = {
    pageTheme: '',
    quesTheme: '',
    optionTheme: ''
  };

  constructor(
    private services: SharedServices,
    private editDataService: EditDataService,
    private logoService: LogoService,
    private themeService: ThemeService
  ) { }

  ngOnInit() {
    this.sId = this.editDataService.sId;
    this.logoDetails = this.logoService.logoDetails;
    this.subscription = this.editDataService.getSurveyData().subscribe(data => {
      if(!data) return;
      this.formObj = this.services.form.formObj;
      this.surveyData = {};
      this.surveyData['surveyTitle'] = this.formObj.getSurveyTitle();
      this.surveyData['allPagesAndQues'] = this.services.form.getAllPagesAndQuestions();
      this.allQuestionsInFormGroup = this.services.form.allQuestionsInFormGroup;
      this.logoService.setLogoOnLoad(this.sId, this.formObj);
      this.setBtnsText();
      this.setTheme();
    })
  }

  setBtnsText(){
    this.btnText['previous']['text'] = this.formObj.previous;
    this.btnText['next']['text'] = this.formObj.next;
    this.btnText['done']['text'] = this.formObj.done;
    this.btnText['exit']['text'] = this.formObj.exit;
    this.btnText['endBtn']['text'] = this.formObj.endBtn;
  }

  editBtnText(type: string){
    this.btnText[type].action = true;
  }

  setSingleQuesFocus(type,e){
    let data: any = {}
    if(e.target.checked){
      if(type == 'm')
        data['singleQuesFocusTabMobile'] = 1;
      else
        data['singleQuesFocusDesktop'] = 1;
    }else{
      if(type == 'm')
        data['singleQuesFocusTabMobile'] = 0;
      else
        data['singleQuesFocusDesktop'] = 0;
    }
    this.services.resource.updateSurveyData(this.sId, data).subscribe((data) => {
      this.services.toastr.success('Survey Setting has been updated succesfully.');
    })
  }

  saveBtnText(type: string){
    let res: any = {};
    res[type] = this.btnText[type].text;
    this.btnText[type].action = false;
    this.services.resource.updateSurveyData(this.sId, res).subscribe((data) => {
      this.formObj[type] = data[type];
      this.services.toastr.success('Survey Setting has been updated succesfully.');
    })
  }

  cancelBtnText(type: string){
    this.btnText[type].action = false;
  }

  setTheme(){
    this.themeData = this.formObj.theme;
    this.themeDetails.pageTheme = this.themeService.getPageTheme(this.formObj);
    this.themeDetails.quesTheme = this.themeService.getQuesTheme(this.formObj);
    this.themeDetails.optionTheme = this.themeService.getOptionTheme(this.formObj);
  }

  // logoUploadChange(){
  //   if(this.surveyLogo.nativeElement.files.length > 0){
  //     this.logoService.logoUploadChange(this.sId, this.surveyLogo.nativeElement.files[0], this.formObj);
  //   }
  // }
  //
  // selectLogo(){
  //   this.surveyLogo.nativeElement.click();
  // }
  //
  // removeLogo(){
  //   this.logoService.removeLogo(this.sId, this.formObj);
  //   this.surveyLogo.nativeElement.value = '';
  // }
  //
  // replaceLogo(){
  //   this.surveyLogo.nativeElement.click();
  // }
  //
  // setLogo(){
  //   let data: any = this.formObj.logo;
  //   let res: any = {
  //     logo: data
  //   }
  //   this.services.resource.addLogo(this.sId, res).subscribe((data) => {
  //     this.services.toastr.success('Survey Logo Configuration is successfully set.', 'Updated Successfully');
  //     this.logoService.setLogoOnLoad(this.sId, this.formObj);
  //   })
  // }

  setSurveyTitle(e: any){
    let data: any = {}
    if(e.target.checked){
      data['showTitle'] = 1;
    }else{
      data['showTitle'] = 0;
    }
    this.services.resource.updateSurveyData(this.sId, data).subscribe((data) => {
      this.services.toastr.success('Survey Setting has been updated succesfully.');
    })
  }

  setPrevBtnDisplay(e: any){
    let data: any = {}
    if(e.target.checked){
      data['showPrevBtn'] = 1;
    }else{
      data['showPrevBtn'] = 0;
    }
    this.services.resource.updateSurveyData(this.sId, data).subscribe((data) => {
      this.services.toastr.success('Survey Setting has been updated succesfully.');
    })
  }

  setExitLink(e: any){
    let data: any = {}
    if(e.target.checked){
      data['showExit'] = 1;
    }else{
      data['showExit'] = 0;
    }
    this.services.resource.updateSurveyData(this.sId, data).subscribe((data) => {
      this.services.toastr.success('Survey Setting has been updated succesfully.');
    })
  }

  setQuesProgDesktop(e: any){
    let data: any = {}
    if(e.target.checked){
      data['showQProgDesktop'] = 1;
    }else{
      data['showQProgDesktop'] = 0;
    }
    this.services.resource.updateSurveyData(this.sId, data).subscribe((data) => {
      this.services.toastr.success('Survey Setting has been updated succesfully.');
    })
  }

  setQuesProgMobTab(e: any){
    let data: any = {}
    if(e.target.checked){
      data['showQProgMobTab'] = 1;
    }else{
      data['showQProgMobTab'] = 0;
    }
    this.services.resource.updateSurveyData(this.sId, data).subscribe((data) => {
      this.services.toastr.success('Survey Setting has been updated succesfully.');
    })
  }

  setProgessBar(e: any){
    let data: any = this.formObj.showProg;
    this.show_progressbr_config = true;
    if(e.target.checked){
      data['hasProg'] = 1;
    }else{
      data['hasProg'] = 0;
      data['position'] = 0;
      data['format']['showPages'] = 0;
    }
    let res: any = {
      showProg: data
    }
    this.services.resource.updateSurveyData(this.sId, res).subscribe((data) => {
      // this.services.toastr.success('Progress Bar is successfully set.', 'Updated Succesfully');
    })
  }

  setProgressBarFormat(type: string){
    let format: any = this.formObj.showProg.format;
    if(this.progBarPages.nativeElement.checked && this.progBarPerc.nativeElement.checked){
      format.showPages = 2;
    }else if(this.progBarPages.nativeElement.checked){
      format.showPages = 0;
    }else if(this.progBarPerc.nativeElement.checked){
      format.showPages = 1;
    }else{
      if(type == 'page'){
        format.showPages = 1;
      }else if(type == 'perc'){
        format.showPages = 0;
      }
    }
  }

  setConfigOfProgressBar(){
    let data: any = this.services.utils.cloneObject(this.formObj.showProg);
    let res: any = {
      showProg: data
    }
    this.services.resource.updateSurveyData(this.sId, res).subscribe((data) => {
      this.services.toastr.success('Survey Setting has been updated succesfully.');
    })
    this.show_progressbr_config = false;
  }

  setPageTitle(e: any){
    let data: any = {}
    if(e.target.checked){
      data['showPTitle'] = 1;
    }else{
      data['showPTitle'] = 0;
    }
    this.services.resource.updateSurveyData(this.sId, data).subscribe((data) => {
      this.services.toastr.success('Survey Setting has been updated succesfully.');
    })
  }

  setPageNumbers(e: any){
    let data: any = {}
    if(e.target.checked){
      data['showPageNumber'] = 1;
    }else{
      data['showPageNumber'] = 0;
    }
    this.services.resource.updateSurveyData(this.sId, data).subscribe((data) => {
      this.services.toastr.success('Survey Setting has been updated succesfully.');
    })
  }

  setQuestionSymbol(e: any){
    let data: any = {}
    if(e.target.checked){
      data['showQuesSymbol'] = 1;
    }else{
      data['showQuesSymbol'] = 0;
    }
    this.services.resource.updateSurveyData(this.sId, data).subscribe((data) => {
      this.services.toastr.success('Survey Setting has been updated succesfully.');
    })
  }

  setQuestionNumbers(e: any){
    let data: any = {}
    if(e.target.checked){
      data['showQuesNum'] = 1;
    }else{
      data['showQuesNum'] = 0;
    }
    this.services.resource.updateSurveyData(this.sId, data).subscribe((data) => {
      this.services.toastr.success('Survey Setting has been updated succesfully.');
    })
  }

  setRequired(e: any){
    let data: any = {}
    if(e.target.checked){
      data['required'] = 1;
    }else{
      data['required'] = 0;
    }
    this.services.resource.updateSurveyData(this.sId, data).subscribe((data) => {
      this.services.toastr.success('Survey Setting has been updated succesfully.');
    })
  }

  setLocationTracking(e: any){
    let data: any = {}
    if(e.target.checked){
      data['trackLocation'] = 1;
    }else{
      data['trackLocation'] = 0;
    }
    this.services.resource.updateSurveyData(this.sId, data).subscribe((data) => {
      this.services.toastr.success('Survey Setting has been updated succesfully.');
    })
  }

  ngOnDestroy(){
    this.subscription.unsubscribe();
  }

}



