import { Component, OnInit } from '@angular/core';
import { SharedServices } from "../../../../../shared/services/services.module";
@Component({
  selector: 'app-reports-page',
  templateUrl: './reports.page.component.html',
  styleUrls: ['./reports.page.component.scss','../survey.advance.component.scss']
})
export class ReportsPageComponent implements OnInit {
  allCriterias: any;
  allQuestions: any = [];
  fontSizes: any = [];
  contentType: any = [
      {
            key : 'e',
            value: 'Embedded data',
      },
      {
            key : 'p',
            value: 'Questions',
      },
      {
            key : 'c',
            value: 'Contact',
      },
  ];
  reportsObject: any = {
      type : "e",
      criId: "",
      eData: "",
      contact: "",
      ques: "",
  }
  reportData : any = {
      pageContent:  "",
      hasPrint: 0,
      piping : {
          hasPiping: 0,
          pipedQuestions:[]
      }
  }
  fontFamList : any;
  quesPiped: string;
  constructor(
     private services: SharedServices,
  ) {}

  ngOnInit() {
      try {
          let survey = this.services.form.formObj;
          this.fontFamList = this.services.constants.fontFamList;
          if(survey.hasOwnProperty('reportPage') && survey.reportPage["pageContent"]) {
              let report = this.services.utils.cloneObject(survey.reportPage);
              this.reportData.pageContent = report.pageContent;
              this.reportData.hasPrint = report.hasPrint;
              this.reportData._id = report._id;
          }
          for(let i = 2; i <= 76; i = i+2) {
              this.fontSizes.push(i+'px');
          }
          this.allCriterias = this.services.form.formObj.getAllCriterias();
          let questions = this.services.form.formObj.getAllQuestions();
          questions.forEach((q) => {
              if(q["fieldType"]["family"] !== 'statement') {
                  if(q["fieldType"]["family"] == 'matrix') {
                      for (let i = 0; i < q.fieldOptions.row.length; i++) {
                          let obj = {};
                          obj["id"] = q._id + '_' + q.fieldOptions.row[i]._id;
                          obj["title"] = this.services.utils.getTextOutOfHtml(q.title) + ' - Row' + (i + 1);
                          this.allQuestions.push(obj);
                      }
                  }  else {
                      let obj = {};
                      obj["id"] = q._id;
                      obj["title"] = this.services.utils.getTextOutOfHtml(q.title);
                      this.allQuestions.push(obj);
                  }

              }
          })
      } catch(err) {
          console.log(err);
      }
  }
  setColor(event) {
     try {
        //  console.log(event);
     } catch(err) {
         console.log(err);
     }
  }
  insertDataIntoReport() {
      try {
        let value = "";
        let tempValue = null;
        let type = this.reportsObject.type;
        if(type == 'e' && this.reportsObject.eData !== '') {
            value = encodeURIComponent(this.reportsObject.eData);
        } else if(type == 'p' && this.reportsObject.ques !== '') {
            value = this.reportsObject.ques;
            if(value.length > 24) {
                tempValue = this.reportsObject.ques.split('_')[0];
            } else {
                tempValue = null;
            }
        } else if(type == 'c' && this.reportsObject.contact !== '') {
            value = this.reportsObject.contact;
        } else {
            this.services.toastr.error('All fields must have a value');
            return;
        }
        let expression = '';
        if(type == 'p') {
            this.reportData.piping.hasPiping = 1;
            if(tempValue) {
                this.reportData.piping.pipedQuestions.push({ isPiped : 1, _id : tempValue });
            } else {
                this.reportData.piping.pipedQuestions.push({ isPiped : 1, _id : value });
            }
        }
        if(this.reportsObject.criId !== '') {
            expression = `$c\{${this.reportsObject.criId}\}\{\{${type}://${value}\}\}`;
        } else {
            expression = `\{\{${type}://${value}\}\}`;
        }
        tinymce.activeEditor.execCommand('mceInsertContent',true,expression);
      } catch(err) {
        console.log(err);
      }
  }
  saveReport() {
      try {
          this.reportData.pageContent = tinymce.activeEditor.getBody().innerHTML;
          if(this.reportData.pageContent.match(/\{\{p:\/\/([a-f0-9]{24}|[a-f0-9]{24}_[a-f0-9]{24})\}\}/g) && this.reportData.pageContent.match(/\{\{p:\/\/([a-f0-9]{24}|[a-f0-9]{24}_[a-f0-9]{24})\}\}/g).length) {
              this.reportData.piping.hasPiping = 1;
          }
          if(this.reportData.hasOwnProperty('_id')) {
              this.services.resource.updateReport(this.reportData._id,this.reportData).subscribe((data)=>{
                  this.reportData._id = data.data._id;
                  this.services.toastr.success('Report has been saved successfully.','Success');
              });
          } else {
              this.services.resource.saveReport(this.services.form.formObj.getId(),this.reportData).subscribe((data)=>{
                  this.reportData._id = data.data._id;
                  this.services.toastr.success('Report has been saved successfully.','Success');
              });
          }
      } catch(err) {
          console.log(err);
      }
  }
  setHasPrint(event) {
      try {
          if(event.target.checked) {
              this.reportData.hasPrint = '1';
          } else {
              this.reportData.hasPrint = '0';
          }
      } catch(err) {
          console.log(err);
      }
  }
}



