import { Injectable } from '@angular/core';
import { SharedServices } from '../../../../shared/services/services.module';

@Injectable()
export class ThemeService {

  constructor(
    private services: SharedServices
  ) { }

  getPageTheme(formObj: any){
    try{
      let pageTheme: any = {};
      let themeData = formObj.theme;
      let pageArray = formObj.getPageFields();
      if(pageArray){
        pageArray.forEach((pageObj) => {
          let pId = pageObj._id;
          pageTheme[pId] = pageObj.getCssObj(themeData);
        })
      }
      return pageTheme;
    }catch (err){
      throw err;
    }
  }

  getQuesTheme(formObj: any){
    try{
      let quesTheme: any = {};
      let themeData = formObj.theme;
      let quesArray = formObj.getAllQuestions();
      if(quesArray){
        quesArray.forEach((quesObj) => {
          let qId = quesObj._id;
          quesTheme[qId] = quesObj.getCssObj(themeData);
        })
      }
      return quesTheme;
    }catch (err){
      throw err;
    }
  }

  getOptionTheme(formObj: any){
    try{
      let optionTheme: any = {};
      let themeData = formObj.theme;
      let quesArray = formObj.getAllQuestions();
      let optTheme = themeData.getOptSettingCss();
      if(quesArray){
        quesArray.forEach((quesObj) => {
          let qId = quesObj._id;
          optionTheme[qId] = this.services.utils.cloneObject(optTheme);
          let pageSetId:string = formObj.getPageByQuestionId(qId)['themeSetId'];
          let color: string = quesObj.getOptionColor(themeData, pageSetId);
          optionTheme[qId]['color'] = color;
        })
      }
      return optionTheme;
    }catch (err){
      throw err;
    }
  }


}



