import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-question-actions-header',
  templateUrl: './question.actions.header.component.html',
  styleUrls: ['./question.actions.header.component.css']
})
export class QuestionActionsHeaderComponent implements OnInit {
  @Input() inpData; //<obj> = {text: '', icon: ''}
  headerData: any

  constructor() { }

  ngOnInit() {
    this.headerData = this.inpData;
  }

}



