import { Component, OnInit, Input, Output, ViewChild, EventEmitter } from '@angular/core';
import { SharedServices } from "../../../../../shared/services/services.module";
import { QuestionTypesService } from "../../../../../shared/questionTemplates/question-types.service";
import { BsModalService } from 'ngx-bootstrap/modal';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { QuestionPipingService } from '../../survey.question.edit/question.piping/question.piping.service';
import { SwapMongoIdAndQuesNum } from '../../../../../shared/pipes/common.pipes';
import { ImageUploadService } from '../../../../../shared/components/image.upload/image.upload.service';

@Component({
  selector: 'app-language-translate',
  templateUrl: './language.translate.component.html',
  styleUrls: ['./language.translate.component.scss','../survey.advance.component.scss']
})
export class LanguageTranslateComponent implements OnInit {
@Input() langInpData;
@Output() addLang;
@Output() onSaveLang: EventEmitter<any> = new EventEmitter<any>();
@ViewChild('addBulkModal') addBulkModal;
@ViewChild('uploadImageModal') uploadImageModal;
@ViewChild('insertPipingModal') insertPipingModal;
@ViewChild('insertImageModal') insertImageModal;
  langData: any = {};
  languageResponse: any;
  langKey: string;
  langDirection: string;
  allQuestionOfSurvey: any;
  allIdsDataMap: any = {};
  quesIdByNumber: any = {};
  allQuestionNumbers: any;
  currentBulkObj: any;
  modalRef: BsModalRef;
  addBulkValueString: string;
  pipingVariable: any;
  erorShowHide: boolean = false;
  currentImageAddButton: any;

  constructor(
      private services: SharedServices,
      private qTypeService: QuestionTypesService,
      private modalService: BsModalService,
      private pipingService: QuestionPipingService,
      private swapPipingService: SwapMongoIdAndQuesNum,
      private imageService: ImageUploadService
  ) { }

  ngOnInit() {
      let surId = this.services.form.formObj.getId();
      this.allQuestionOfSurvey = this.services.form.formObj.getAllQuestions();
      this.allQuestionOfSurvey.forEach( (ques,index) => {
          this.quesIdByNumber[index+1] = ques._id;
      });
      this.allQuestionNumbers = this.services.form.getQuestionCountAndId();
      this.langKey = this.langInpData.langKey;
      this.langDirection = this.langInpData.direction;
      this.services.resource.getSurveyLanguagesData(surId,this.langKey).subscribe(data => {
          this.languageResponse = this.services.utils.cloneObject(data);
          if(this.languageResponse.hasOwnProperty('language') && this.languageResponse.language && this.languageResponse.language.hasOwnProperty('langData') && this.languageResponse.language.langData && this.languageResponse.language.langData.hasOwnProperty(this.langKey)) {
                this.langData = this.services.utils.cloneObject(this.languageResponse.language.langData[this.langKey]);
                this.createLangObject();
                for(let id in this.allQuestionNumbers.data) {
                    if(this.langData.hasOwnProperty(id) && this.langData[id].hasOwnProperty('title')) {
                        this.langData[id].title = this.swapPipingService.transform(this.langData[id].title,true,id);
                    }
                }
          } else {
            this.createLangObject();
          }
      });
  }
  saveLanguage() {
      let langObj = {};
      let retFlag = false;
      langObj[this.langKey] = this.services.utils.cloneObject(this.langData);
      this.allQuestionOfSurvey.forEach( (ques) => {
          if(langObj[this.langKey][ques._id].title) {
              let title = langObj[this.langKey][ques._id].title;
              let originalTitle = this.allIdsDataMap[ques._id].title;
              let matchArray = title.match(/{{p:\/\/Q\d{1,}}}/g);
              let originalMatchArray = originalTitle.match(/{{p:\/\/[a-f0-9]{24}}}/gi) || [];
              title = this.swapPipingService.transform(title,false,ques._id);
              langObj[this.langKey][ques._id].title = title;
              matchArray = langObj[this.langKey][ques._id].title.match(/{{p:\/\/[a-f0-9]{24}}}/gi) || [];
              if(originalMatchArray.length != matchArray.length) {
                  retFlag = true;
              }
          }
      });
      if(retFlag) {
          this.services.toastr.error("Some piping in the questions doesn`t match with the translation");
      }
      this.services.resource.saveLanguageTranslation(this.services.form.formObj.getId(), langObj).subscribe((data) => {
          this.services.toastr.success("Language Translated Successfully");
        //   this.languageResponse = !this.languageResponse;
          this.onSaveLang.emit();
      });
  }
  checkId(id,key) {
    if(this.langData.hasOwnProperty(id)) {
        this.langData[id][key] = "";
    } else {
        this.langData[id] = {};
        this.langData[id][key] = "";
    }
  }
  createLangObject() {
      if(!this.langData.hasOwnProperty(this.services.form.formObj.getId())) {
          this.langData[this.services.form.formObj.getId()] = {
              title: null,
              done: null,
              next: null,
              previous: null
          }
      }
      this.services.form.formObj.getPageFields().forEach((page) => {
          if(!this.langData.hasOwnProperty(page.getId())) {
              this.langData[page.getId()] = {
                  title: null,
                  desc: null
              }
          }
          page.getAllQuestions().forEach((ques) => {
              if(!this.langData.hasOwnProperty(ques.getId())) {
                  this.langData[ques.getId()] = {
                      title: null,
                      reqMsg: null
                  }
              }
              this.allIdsDataMap[ques._id] = {
                  title: ques.title
              }
              ques.getRowArray().forEach((row) => {
                  if(!this.langData.hasOwnProperty(row.getId())) {
                      this.langData[row.getId()] = {
                          text: null,
                      }
                  }
                  this.allIdsDataMap[row._id] = {
                      text: row.text
                  }
              });
              ques.getColArray().forEach((col) => {
                  if(!this.langData.hasOwnProperty(col.getId())) {
                      this.langData[col.getId()] = {
                          text: null,
                      }
                  }
                  this.allIdsDataMap[col._id] = {
                      text: col.text
                  }
                  if(col.items && col.items.length) {
                      col.items.forEach((item) => {
                          if(!this.langData.hasOwnProperty(item._id)) {
                              this.langData[item._id] = {
                                  text: null
                              }
                          }
                          this.allIdsDataMap[item._id] = {
                              text: item.text
                          }
                      });
                  }
              });
          });
      });
  }
  addInBulk() {
      /*
        this.currentBulkObj:
            ques: <object>,
            type: <string>,
            colId: <objectId>
      */
      let elemArr = [];
      let type = this.currentBulkObj.type;
      let ques = this.currentBulkObj.ques;
      let colId = this.currentBulkObj.colId ? this.currentBulkObj.colId : null;
      let insertString = this.addBulkValueString;
      let newArray = [];
      let newMenuVals = [];
      if(type == 'r') {
          elemArr = ques.getRowArray();
      } else if(type == 'c') {
          elemArr = ques.getColArray();
      } else if(type == 'i') {
        elemArr = ques.getColById(colId).items;
      }
      insertString = insertString.replace(/^\s*\n/gm, ""); // Removing only spaces and \n at last of string
      newArray = insertString.split("\n"); // Converting string into array of sepecrated values

      for (let i = 0; i < newArray.length; i++)
        newArray[i] = newArray[i].trim();

      for (let i = 0; i < newArray.length; i++)
        if (newArray[i] != '')
            newMenuVals.push(newArray[i]);
      if(newMenuVals.length > elemArr.length) {
          this.erorShowHide = true;
          return;
      } else {
          this.erorShowHide = false;
          for(let i = 0; i < newMenuVals.length; i++) {
              this.langData[elemArr[i]._id]["text"] = newMenuVals[i];
          }
      }
      this.modalRef.hide();
  }
  openBulkModal(options) {
      this.currentBulkObj = {
          type : options.type,
          ques: options.ques,
          colId: options.colId ? options.colId: null
      }
      let elemArr = [];
      if(options.type == 'r') {
          elemArr = options.ques.getRowArray();
      } else if(options.type == 'c') {
          elemArr = options.ques.getColArray();
      } else if(options.type == 'i') {
          elemArr = options.ques.getColById(options.colId).items;
      }
      let setString = '';
      for(let i = 0; i < elemArr.length; i++) {
          if(this.langData[elemArr[i]._id] && this.langData[elemArr[i]._id]["text"]) {
              setString += this.langData[elemArr[i]._id]["text"] + "\n";
          }
      }
      this.addBulkValueString = setString;
      this.modalRef = this.modalService.show(this.addBulkModal, { class: 'modal-md' });
  }
  hideModal(): void {
    this.modalRef.hide();
  }
  insertPiping(type) {
      this.pipingService.initializePipingObj();
      this.pipingService.pipingJSON.from = type;
      this.modalRef = this.modalService.show(this.insertPipingModal, { class: 'modal-md' });
  }
  onPipingDone() {
      this.modalRef.hide();
  }
  insertImage(id,key) {
      this.currentImageAddButton = {id: id, key: key};
      this.modalRef = this.modalService.show(this.insertImageModal, { class: 'modal-lg' });
  }
  onDoneImageSelect(e: any) {
    if(e){
      this.imageService.initializePipingImgObj(e);
      // this.insertInQuesOrOpt();
      let textToInsert = this.imageService.getPipedImgElem();
      if(this.currentImageAddButton.key == 'title') {
        this.services.utils.insertContentInActiveTinyMce(textToInsert);
      } else {
        if(!this.langData[this.currentImageAddButton.id][this.currentImageAddButton.key]) {
          this.langData[this.currentImageAddButton.id][this.currentImageAddButton.key] = textToInsert;
        } else {
          this.langData[this.currentImageAddButton.id][this.currentImageAddButton.key] += textToInsert;
        }
      }
    }
    this.modalRef.hide();
  }
  checkPipingExistence(text){
      if((/{{(c|e):\/\/.*?}}/g).test(text)) {
          return true;
      } else {
          return false;
      }
  }
  checkQuesTypeForImage(fieldType,type) {
      var allowRowJSON = {
          'text-textBox' : false,
          'matrix-rating': false,
          'multiChoice-dropdown': false,
          'matrix-slider': false,
          'comment': false,
          'file': false,
      }
      var allowColJSON = {
          'matrix-ranking' : false,
          'matrix-rating': false,
          'matrix-slider': false,
      }
      if(type =='row') {
          if(fieldType.family) {
              if(fieldType.subtype && allowRowJSON.hasOwnProperty(fieldType.family + '-' + fieldType.subtype)) {
                  return false;
              } else if(allowRowJSON.hasOwnProperty(fieldType.family)) {
                  return false;
              } else {
                  return true;
              }
          }
      } else if(type == 'col') {
          if(fieldType.subtype && allowRowJSON.hasOwnProperty(fieldType.family + '-' + fieldType.subtype)) {
              return false;
          } else {
              return true;
          }
      }
  }
  setPipingVariable(text) {
      let matchArray = text.match(/{{p:\/\/[a-f0-9]{24}}}/gi);
      let resArray = [];
      if(matchArray)  {
          for(let i = 0; i < matchArray.length; i++) {
              if(resArray.indexOf(matchArray[i]) == -1) {
                  resArray.push(matchArray[i]);
              }
          }
          for(let i = 0; i < resArray.length; i++) {
              resArray[i] = resArray[i].replace('{{p://', '').replace('}}', '');
          }
          this.pipingVariable = resArray;
          return true;
      } else {
          this.pipingVariable = [];
          return false;
      }
  }
  addPipingToTitle(qIdToPipe,qIdPiping,qNumToPipe) {
      this.services.utils.insertContentInActiveTinyMce( '{{p://Q'+ qNumToPipe +'}}' );
  }
  checkBulkAddShowHide(question,type) {
      let family = question.fieldType.family;
      let subtype = question.fieldType.subtype;
      var bulkAddTypeArray = [
              { "family" : "multiChoice","subtype": "radio" , showROw : true, showCol: false },
              { "family" : "multiChoice","subtype": "checkbox" , showROw : true, showCol: false },
              { "family" : "multiChoice","subtype": "dropdown" , showROw : true, showCol: false },
              { "family" : "matrix","subtype": "radio" , showROw : true, showCol: true },
              { "family" : "matrix","subtype": "checkbox" , showROw : true, showCol: true },
              { "family" : "matrix","subtype": "dropdown" , showROw : true, showCol: true },
              { "family" : "matrix","subtype": "rating" , showROw : false, showCol: false },
              { "family" : "matrix","subtype": "graphical" , showROw : true, showCol: false },
              { "family" : "matrix","subtype": "ranking" , showROw : true, showCol: false },
              { "family" : "text", "subtype" : "textBox" , showROw : false, showCol: false},
              { "family" : "text", "subtype" : "multitextbox" , showROw : true, showCol: false },
              { "family" : "text", "subtype" : "textArea", showROw : false, showCol: false },
              { "family" : "date", "subtype" : "dateOnly", showROw : true, showCol: false },
              { "family" : "date", "subtype" : "timeOnly", showROw : true, showCol: false },
              { "family" : "date", "subtype" : "both", showROw : true, showCol: false },
              { "family" : "comment" , "subtype": undefined, showROw : false, showCol: false },
              { "family" : "statement" ,"subtype": undefined, showROw : false, showCol: false },
              { "family" : "file" ,"subtype": undefined, showROw : false, showCol: false },
              { "family" : "contact" ,"subtype": undefined, showROw : true, showCol: false },
            ];
    for (var aIndex = 0; aIndex < bulkAddTypeArray.length; aIndex++) {
        if (bulkAddTypeArray[aIndex].family == family && (subtype ? bulkAddTypeArray[aIndex].subtype == subtype : 1) ) {
            if(type == 'row') {
                if (bulkAddTypeArray[aIndex].showROw) {
                    return true;
                } else {
                    return false;
                }
            } else if(type == 'col') {
                if (bulkAddTypeArray[aIndex].showCol) {
                    return true;
                } else {
                    return false;
                }
            }
            break;
        }
    }
  }
  onEvent(event,id,key) {
        if(event.type == 'keyup'){
            event.options.element = event.options.content;
            this.langData[id][key] = event.options.content;
        }
        else if(event.type == 'btnClick' && event.options.action == 'imgUpload'){
          this.insertImage(id,key);
        }
  }
}



