import { Component, OnInit, OnChanges, OnDestroy ,SimpleChanges, ViewChild, ViewChildren, QueryList, Input, Output, EventEmitter, TemplateRef, ElementRef } from '@angular/core';
import { Router } from '@angular/router';
import { FormArray, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { trigger, transition, style, animate, state } from '@angular/animations';

import { QuestionTypesService } from '../../../../shared/questionTemplates/question-types.service';
import { EditDataService } from '../edit.data.service';
import { EditConstantsService } from '../edit.constants.service';
import { BsModalService } from 'ngx-bootstrap/modal';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { BsDropdownDirective } from 'ngx-bootstrap';
import { SharedServices } from '../../../../shared/services/services.module';
import { CarryForwardService } from '../survey.design/left.panel/carry.forward/carry.forward.service';
import { LogoService } from './logo.service';
import { PageOperationsService } from './page.operations.service';
import { ThemeService } from './theme.service';
import { RandomizationService } from '../survey.randomization/randomization.service';

declare var Page;

@Component({
  selector: 'app-survey-main-list',
  animations: [
    trigger(
      'myAnimation',
      [
        // state('enter', style({
        //   backgroundColor: '#eee',
        //   //   transform: 'scale(1)',
        //   transform: 'translateX(-100%)',
        //   opacity: 0
        // })),
        // state('leave', style({
        //   backgroundColor: '#cfd8dc',
        //   //   transform: 'scale(1.1)',
        //   transform: 'translateX(0)',
        //   'opacity': 1
        // })),
        // transition('leave => enter', animate('2000ms ease-in')),
        // transition('active => inactive', animate('2000ms ease-out'))
        transition(
          ':enter', [
            style({ opacity: 0 }),
            animate('500ms', style({ 'opacity': 1 }))
          ]
        ),
        transition(
          ':leave', [
            style({ 'opacity': 1 }),
            // animate('500ms', style({ transform: 'translateX(-100%)', 'opacity': 0 }))
            animate('500ms', style({ 'opacity': 0 }))
          ]
        )
      ]
    )
  ],
  templateUrl: './survey.main-list.component.html',
  styleUrls: ['../survey.edit.component.scss', './survey.main-list.component.scss'],
  providers: [CarryForwardService]
})
export class SurveyMainListComponent implements OnInit, OnChanges {
  @Input() onSuccess: any;
  @Output() onChange: EventEmitter<any> = new EventEmitter<any>();
  carryJSON: any;
  sId: string;
  carryMatrixJSON: any;
  formObj: any;
  surveyDesignData: any;
  allQuestionsInFormGroup: any = {};
  allQuestionsOfSurvey: any = {};
  allPagesAndQues: any = {};
  questionCountAndId: any = {};
  questionTypesJSON: any;
  addQuestionRelatedData: any;
  logoDetails: any;
  changesToChildComp: any = {};
  delNotification: any = {};
  showDelNotification: boolean = false;
  titlesOfDelQues: any = {};
  themeData: any;
  themeDetails: any = {
    pageTheme: '',
    quesTheme: '',
    optionTheme: ''
  };
  reBuildProgBar:boolean = true;

  @ViewChild('surveyLogo') surveyLogo;
  @ViewChild('carryForwardModal') carryForwardModal;

  modalRef: BsModalRef;

  openModal(template: TemplateRef<any>, modalClass?: string) {
    this.modalRef = this.modalService.show(template, { class: modalClass || 'modal-md' });
  }

  hideModal(): void {
    this.modalRef.hide();
  }

  constructor(
    private router: Router,
    private editDataService: EditDataService,
    private editConstants: EditConstantsService,
    private questionTypesService: QuestionTypesService,
    private modalService: BsModalService,
    private services: SharedServices,
    private elementRef: ElementRef,
    private carryService: CarryForwardService,
    private logoService:LogoService,
    private pageOpService: PageOperationsService,
    private themeService:ThemeService,
    private randServices: RandomizationService
  ) { }

  ngOnInit() {
    this.carryJSON = this.carryService.getCarryJSON();
    this.carryMatrixJSON = this.carryService.getCarryMatrixJSON();
    this.carryMatrixJSON = this.carryMatrixJSON.concat([
        {
            val: 12, text: 'Selected Rows', scale: 0
        },
        {
            val: 13, text: 'Unselected Rows', scale: 0
        },
        {
            val: 14, text: 'Selected Columns', scale: 0
        }
    ]);
    this.sId = this.editDataService.sId;
    let ques = this.services.form.formObj.getAllQuestions();
    if(this.editDataService.getTipsEnable() && !(ques && ques.length)) {
      this.editDataService.setTipsEnable(false);
      this.router.navigate(['/survey', 'design', this.sId, 'tips']);
      return;
    } else {
      this.editDataService.setTipsEnable(true);
    }
    this.surveyDesignData = this.editDataService.surveyDesignData;
    this.formObj = this.services.form.formObj;
    this.allQuestionsInFormGroup = this.services.form.allQuestionsInFormGroup;
    this.allQuestionsOfSurvey = this.services.form.allQuestionsOfSurvey;
    this.addQuestionRelatedData = this.editDataService.addQuestionRelatedData;
    this.questionTypesJSON = this.questionTypesService.questionTypesJSON;
    this.allPagesAndQues = this.services.form.getAllPagesAndQuestions();
    this.questionCountAndId = this.services.form.getQuestionCountAndId();
    this.logoDetails = this.logoService.logoDetails;
    this.logoService.setLogoOnLoad(this.sId, this.formObj);
    this.themeData = this.formObj.theme;
    this.themeDetails.pageTheme = this.themeService.getPageTheme(this.formObj);
    this.themeDetails.quesTheme = this.themeService.getQuesTheme(this.formObj);
    this.themeDetails.optionTheme = this.themeService.getOptionTheme(this.formObj);
  }

  ngOnChanges(changes: SimpleChanges){
    let act = changes.onSuccess.currentValue.action;
    if(act == 'page-delete' || act == 'copy-question' || act == 'restore-del-ques'){
      setTimeout(() => {
        this.reBuildData();
      },1);
    }else if(act == 'logo-replace'){
      this.replaceLogo();
    }else if(act == 'logo-remove'){
      this.removeLogo();
    }
  }

  onEvent(event) {
    if (event.type == 'keyup') {
      this.onEditorKeyup(event.options);
    }
  }

  onEditorKeyup(options: any) {
    options.element = options.content;
  }

  logicChange(data: any) {
    this.editDataService.getSurveyData();
  }

  makeAllPagesAndQuesObj() {
    this.services.form.setAllPagesAndQuestions();
  }

  getQuesType(family: string, subtype: string) {
    return this.questionTypesService.getQuestionType(family, subtype);
  }

  addNewQuestion(pageId: string, pageIndex: number, quesRefId: string, quesRefIndex: number) {
    this.surveyDesignData.presentAction = 'add-question';
    this.surveyDesignData.quesNav = 'new';
    this.surveyDesignData.displaySidebar = false;
    this.addQuestionRelatedData.surId = this.sId;
    this.addQuestionRelatedData.pageId = pageId;
    this.addQuestionRelatedData.pageIndex = pageIndex;
    this.addQuestionRelatedData.addQuesRefId = quesRefId;
    this.addQuestionRelatedData.addQuesRefIndex = quesRefIndex;
  }

  reBuildData(){
    this.services.form.setAllPagesAndQuestions();
    this.reflectChangesToQuesTemps();
    this.themeDetails.pageTheme = this.themeService.getPageTheme(this.formObj);
    this.themeDetails.quesTheme = this.themeService.getQuesTheme(this.formObj);
    this.reBuildProgressBar();
  }

  reflectChangesToQuesTemps(){
    this.changesToChildComp = {
      from: 'main-list'
    }
  }

  addPageBreak(pageId: string, relatQuesId: string, page_idx: number) {
    this.services.resource.addPageBreak(pageId, this.sId, relatQuesId).subscribe((data) => {
      this.formObj.pageFields.splice(page_idx, 1, new Page(data.pageOne), new Page(data.pageTwo));
      this.reBuildData();
    });
  }

  reBuildProgressBar(){
    this.reBuildProgBar = false;
    setTimeout(() => {
      this.reBuildProgBar = true;
    }, 1);
  }

   editIntroPage(){
    this.router.navigate(['/survey', 'design', this.editDataService.sId, 'intro']);
  }

  editSurveyTitle(){
    let data: any = {
      action: 'survey-page-title-desc',
      titleDescInpData: {
        id: this.sId,
        field: 'survey',
        subField: 'title'
      }
    }
    this.onChange.emit(data);
  }

  editPageTitleDesc(pId: string, pIdx: number, subField: string){
    let data: any = {
      action: 'survey-page-title-desc',
      titleDescInpData: {
        id: pId,
        idx: pIdx,
        field: 'page',
        subField: subField
      }
    }
    this.onChange.emit(data);
  }

  editSurveyLogo(){
    let data: any = {
      action: 'survey-logo',
      logoInpData: {
        sId: this.sId
      }
    }
    this.onChange.emit(data);
  }

  logoUploadChange(){
    if(this.surveyLogo.nativeElement.files.length > 0){
      this.logoService.logoUploadChange(this.sId, this.surveyLogo.nativeElement.files[0],  this.formObj);
    }
  }

  removeLogo(){
    this.surveyLogo.nativeElement.value = '';
  }

  replaceLogo(){
    this.surveyLogo.nativeElement.click();
  }

  deletePage(pageId: string, page_idx: number) {
    let deleteOrder: number = 0;
    let res: any = {
      tittleArr: [],
      piping: {
        pipedQuestions: [],
        piperQuestions: []
      }
    }
    if (this.formObj.pageFields[page_idx].formFields.length == 0) {
      if(this.formObj.getPageFields().length == 1){
        this.services.toastr.error('Atleast one page is required.', 'Cannot be Deleted.');
      }else{
        this.services.resource.deletePage(pageId, this.sId, deleteOrder, res).subscribe((data) => {
          this.formObj.pageFields.splice(page_idx, 1);
          this.reBuildData();
          this.services.toastr.success('Page has been deleted successfully.', 'Deleted');
        });
      }
    } else {
      let data: any = {
        action: 'page-delete',
        delPageInpData: {
          pageId: pageId,
          pageIdx: page_idx,
          surId: this.sId
        }
      }
      this.onChange.emit(data);
    }
  }

  opencopyOrMoveQuestionDropDown(fromPId: string, quesId: string, quesIdx: number) {
    let data: any = {
      action: 'copy-question',
      copyInputData: {
        fromPId: fromPId,
        quesId: quesId,
        quesIdx: quesIdx
      }
    }
    this.onChange.emit(data);
  }

  delQuestion(pId: string, qId: string, pIdx: number, qIdx: number) {
    let delQuesObj:any = {
        piping: {
            piperQuestions: [],
            pipedQuestions: []
        }
    };

    let piped = this.services.form.getPipingObject();
    let isPiped = this.services.form.getIsPipingObject();

    if(isPiped[qId]){
      isPiped[qId].forEach((quesId) => {
        let piperObj: any = {};
        piperObj['id'] = quesId;
        let newTitle = this.allQuestionsOfSurvey[quesId].title;
        let strToBeReplaced = '{{p://' + qId + '}}';
        newTitle = newTitle.replace(new RegExp(strToBeReplaced, 'g'), '');
        piperObj['title'] = newTitle;  //title to be set at the backend for the questions contained this question as pipe
        if (piped[quesId].length == 1) {
            piperObj['hasPiping'] = 0;
        } else {
            piperObj['hasPiping'] = 1;
        }
        delQuesObj.piping.piperQuestions.push(piperObj);
      })
    }

    if(piped[qId]){
      piped[qId].forEach((quesId) => {
        let pipedObj: any = {};
        pipedObj['id'] = quesId;
        if (isPiped[quesId].length == 1) {
            pipedObj['isPiped'] = 0;
        } else {
            pipedObj['isPiped'] = 1;
        }
        delQuesObj.piping.pipedQuestions.push(pipedObj);
      })
    }

    if (!isPiped[qId] && !piped[qId]) {
        delQuesObj = {};
    }

    this.services.alert({
      title: '<p class="sweetalert_title">Are you sure you want to delete this question?</p>',
      text: "",
      type: 'warning',
      showCancelButton: true,
      buttonsStyling: false,
      confirmButtonClass: 'sweetalert_apply_btn',
      cancelButtonClass: 'sweetalert_cancel_btn',
      confirmButtonText: 'Yes, delete it!'
    }).then((result) => {
      if (result.value) {
        this.titlesOfDelQues = {};
        this.delNotification[qId] = {}
        this.titlesOfDelQues[qId] = {}
        this.titlesOfDelQues[qId] = this.allQuestionsOfSurvey[qId].title
        this.services.resource.deleteQuestion(pId, qId, delQuesObj).subscribe((data) => {
          this.formObj.pageFields[pIdx].formFields.splice(qIdx, 1);
          this.formObj.criteria = data.updatedCriteria;

          if(delQuesObj.piping && delQuesObj.piping.piperQuestions.length > 0){
            if(!this.delNotification[qId]['piping']){
              this.delNotification[qId]['piping'] = []
            }
            delQuesObj.piping.piperQuestions.forEach((ques) => {
              this.allQuestionsOfSurvey[ques.id].title = ques.title;
              this.allQuestionsInFormGroup[ques.id].data.data.get('title').patchValue(ques.title);
              this.delNotification[qId]['piping'].push(ques.id);
            })
          }

          if(data.isQuotaChanged == 1){
            this.delNotification[qId]['quota'] = {
              name: this.formObj.quota.name
            }
            if (data.quota.quota.hasOwnProperty('cQuota'))
              this.formObj.quota.cQuota = data.quota.quota.cQuota;
            if (data.quota.quota.hasOwnProperty('sQuota'))
              this.formObj.quota.sQuota = data.quota.quota.sQuota;
            this.formObj.quota.name = data.quota.quota.name;
            this.formObj.quota.type = data.quota.quota.type;
            this.formObj.quota.respType = data.quota.quota.respType;
            this.formObj.quota.isQuota = data.quota.quota.isQuota;
          }

          let toCarryData = [];
          if (this.allQuestionsOfSurvey[qId].toCarryForward) {
            toCarryData = this.allQuestionsOfSurvey[qId].toCarryForward;
            if(toCarryData.length){
              this.delNotification[qId]['carryfrwd'] = [];
              toCarryData.forEach((cQuesId) => {
                let quesToUpdate = this.allQuestionsOfSurvey[cQuesId];
                if(quesToUpdate.frmCarryForward){
                  quesToUpdate.frmCarryForward = quesToUpdate.frmCarryForward.filter((item) =>
                    (item.quesId != qId)
                  )
                  this.delNotification[qId]['carryfrwd'].push(cQuesId);
                }
              })
            }
          }
          let frmCarryData = [];
          if (this.allQuestionsOfSurvey[qId].frmCarryForward) {
            frmCarryData = this.allQuestionsOfSurvey[qId].frmCarryForward;
            frmCarryData.forEach((frmCarryItem) => {
              let quesToUpdate = this.allQuestionsOfSurvey[frmCarryItem.quesId];
              if(quesToUpdate.toCarryForward){
                quesToUpdate.toCarryForward = quesToUpdate.toCarryForward.filter((cQId) =>
                  (cQId != qId)
                )
              }
            })
          }

          let criteriaFlag: boolean = false;
          let corruptedCriteria: Array<string> = [];
          this.formObj.criteria.forEach((criteria) => {
            if (criteria.isCorrupt == 1) {
              criteria.quesProp.forEach((ques) => {
                if(ques.quesId == qId){
                  corruptedCriteria.push(criteria.name);
                  criteriaFlag = true;
                }
              })
            }
          })
          if (criteriaFlag) {
            this.delNotification[qId]['criteria'] = corruptedCriteria;
          }

          let logicAffected = this.pageOpService.isQuestionInvolvedInLogic(qId);
          if(Object.keys(logicAffected).length != 0){
            for(let qid in logicAffected){
              logicAffected[qid].forEach((logicName,logicIdx) => {
                // this.pageOpService.logicLeftOnAffQues(qid, logicName); //to remove logics
                logicAffected[qid][logicIdx] = this.editDataService.getLogicType(logicName);
              })
            }
            this.delNotification[qId]['logics'] = logicAffected;
          }
          if(Object.keys(this.delNotification[qId]).length){
            this.showDelNotification = true;
          }
          this.reBuildData();
          delete this.allQuestionsOfSurvey[qId];
          delete this.allQuestionsInFormGroup[qId];
          this.services.toastr.success('Question has been deleted successfully.', 'Deleted');
          this.services.form.setPipingObject();
        })
      }
    })
  }

  closeDelNotification(){
    this.showDelNotification = false;
    this.delNotification = {};
  }

  hideQuestion(qId: string, hidden: number, pId: number, pIdx: number, qIdx: number) {
    let quesData: any = {
      isHidden: (hidden ? 0 : 1),
      _id: qId
    }
    this.services.resource.hideQuestion(qId, quesData).subscribe((data) => {
      this.formObj.pageFields[pIdx].formFields[qIdx].isHidden = quesData.isHidden;
      this.allPagesAndQues.data[pId].ques[qIdx].isHidden = quesData.isHidden;
      this.services.toastr.success('Question has been updated successfully.', 'Updated');
    })
  }

  editQuestion(pageId: string, pageIndex: number, quesId: string, quesIndex: number) {
    this.addQuestionRelatedData.pageId = pageId;
    this.addQuestionRelatedData.pageIndex = pageIndex;
    this.addQuestionRelatedData.updateQuesIndex = quesIndex;
    this.router.navigate(['/survey', 'design', this.editDataService.sId, 'question', quesId]);
  }

  editLogic(pId: string, qId: string, qType: string, logicType: string){
    this.setLogicObj(pId, qId, 'logic', qType, logicType);
  }

  setLogicObj(pId: string, qId: string, type: string, qType: string, subType: string){
    let data: any = {
      action: type
    };
    data["pId"] = pId;
    data["qId"] = qId;
    data["qType"] = qType;
    data["editLogicType"] = subType;
    this.onChange.emit(data);
  }

  actionFunction(qId: string, type: string, pageId?: string, subType?: string) {
    let data: any = {
      action: type
    };
    switch (type) {
      // case 'logic':
      //   data["qId"] = qId;
      //   data["qType"] = pageId;
      //   data["editLogicType"] = subType;
      //   this.onChange.emit(data);
      //   break;
      case 'recode':
        data["qId"] = qId;
        this.onChange.emit(data);
        break;
      case 'question-randomize':
        let dataRanQ: any = {
         action: 'randomization',
         ranInpData: {'type':'Question', 'pid': pageId}
        };
        this.onChange.emit(dataRanQ);
        break;
      case 'option-randomize':
        let dataRanO: any = {
         action: 'randomization',
         ranInpData: {'type':'Options', 'qid': qId}
        };
        this.onChange.emit(dataRanO);
        break;
    }
  }

  removeCarryForward(qId) {
      this.services.alert({
        title: '<p class="sweetalert_title">Are you sure you want to delete?</p>',
        text: "",
        type: 'warning',
        showCancelButton: true,
        buttonsStyling: false,
        confirmButtonClass: 'sweetalert_apply_btn',
        cancelButtonClass: 'sweetalert_cancel_btn',
        confirmButtonText: 'Yes, delete it!'
      }).then((result) => {
        if (result.value) {
            let carryForward = this.services.utils.cloneObject(this.allQuestionsOfSurvey[qId].frmCarryForward);
            this.services.resource.updateQuestionData(qId, { frmCarryForward: [] }).subscribe((data) => {
              this.services.form.formObj.getQuestionById(qId).setFrmCarryForward(data.resBody.frmCarryForward);
              /*  Removing Present Question ID from toCarryForward of carried QUestions */
              for(let i = 0; i < carryForward.length; i++) {
                  let cQId = carryForward[i].quesId;
                  if(this.allQuestionsOfSurvey[cQId].toCarryForward && this.allQuestionsOfSurvey[cQId].toCarryForward.length) {
                      let idx = this.allQuestionsOfSurvey[cQId].toCarryForward.indexOf(qId);
                      this.allQuestionsOfSurvey[cQId].toCarryForward.splice(idx,1)
                  }
              }
              /*  Removing Present Question ID from toCarryForward of carried QUestions */
              this.services.toastr.success('Question has been updated successfully.', 'Updated');
            });
        }
    });
 }

  trackByFn(index, item) {
    return index
  }
}



