import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { SharedServices } from '../../../../shared/services/services.module';
import { EditDataService } from '../edit.data.service';

declare var Question;

@Component({
  selector: 'app-survey-copy-question',
  templateUrl: './survey.copy.question.component.html',
  styleUrls: ['./survey.copy.question.component.css', '../survey.edit.component.scss']
})
export class SurveyCopyQuestionComponent implements OnInit {
  @Input() copyInputData;
    // <copyInputData> = {
    //   fromPId: '',
    //   quesId: '',
    //   quesIdx: ''
    // }
  @Output() onCancel = new EventEmitter<any>();
  @Output() onSuccess = new EventEmitter<any>();
  formObj: any;

  allPagesAndQues: any;
  copyMoveQuesObj: any = {
    action: 'copy',
    fromPageId: '',
    toPageId: '',
    toRefQuesId: '', //ques sel in dropdown while input of data
    pos: '' //<'before','after'>
  }

  constructor(
    private editDataService: EditDataService,
    public services: SharedServices
  ) { }

  ngOnInit() {
    this.formObj = this.services.form.formObj;
    this.allPagesAndQues = this.services.form.getAllPagesAndQuestions();
    this.copyMoveQuesObj.action = 'copy';
    this.copyMoveQuesObj.fromPageId = this.copyInputData.fromPId;
    this.copyMoveQuesObj.toPageId = Object.keys(this.allPagesAndQues.data)[0];
    this.copyMoveQuesObj.pos = "before";
    this.setPageIdInCopyOrMoveQues();
  }

  setPageIdInCopyOrMoveQues() {
    this.copyMoveQuesObj.toRefQuesId = (this.allPagesAndQues.data[this.copyMoveQuesObj.toPageId].ques[0] ? this.allPagesAndQues.data[this.copyMoveQuesObj.toPageId].ques[0]._id : null);
  }

  cancelCopyOrMoveQuestion(){
    this.onCancel.emit();
  }

  copyOrMoveQuestion(){
    let refQuesId: string = ''; // this is ref ques id to be sent at backend.
    let toPIdx: number = this.allPagesAndQues.data[this.copyMoveQuesObj.toPageId].count - 1;
    let fromPIdx: number = this.allPagesAndQues.data[this.copyMoveQuesObj.fromPageId].count - 1;
    let quesArr: any = this.allPagesAndQues.data[this.copyMoveQuesObj.toPageId].ques;
    let toQIdx: number = -1; //toQIdx = -1 for pages which have no questions

    if (this.copyMoveQuesObj.toRefQuesId != null) {
      toQIdx = this.services.utils.findIndex(quesArr, '_id', this.copyMoveQuesObj.toRefQuesId);
    }

    if (this.copyMoveQuesObj.pos == "after") {
      refQuesId = ((this.copyMoveQuesObj.toRefQuesId != null) ? this.copyMoveQuesObj.toRefQuesId : null);
    } else if (this.copyMoveQuesObj.pos == "before") {
      if (toQIdx == 0 || toQIdx == -1) {
        refQuesId = null;
      } else {
        refQuesId = quesArr[toQIdx - 1]._id;
      }
    }


    if (this.copyMoveQuesObj.action == 'copy') {
      this.copyQuestion(fromPIdx, toPIdx, refQuesId, toQIdx);

    } else if(this.copyMoveQuesObj.action == 'move') {
      this.moveQuestion(fromPIdx, toPIdx, refQuesId, toQIdx);
    }
  }

  copyQuestion(fromPIdx, toPIdx, refQuesId, toQIdx){
    let pagesArr = this.services.form.getAllPageIdsArr();
    let question = this.services.form.allQuestionsOfSurvey[this.copyInputData.quesId];

    let carryFrwdFlag: boolean = this.checkForCarryForward(question, pagesArr, 'copied');
    if(!carryFrwdFlag){
      return;
    }

    let pipingFlag: boolean = this.checkForPiping(pagesArr);
    if(!pipingFlag){
      return;
    }

    this.services.resource.copyQuestion(this.copyInputData.quesId, this.copyMoveQuesObj.toPageId, refQuesId).subscribe((data) => {
      if (toQIdx == -1) {
        this.formObj.pageFields[toPIdx].formFields.splice(0, 0, new Question(data));
      } else {
        if (this.copyMoveQuesObj.pos == "after") {
          this.formObj.pageFields[toPIdx].formFields.splice(toQIdx + 1, 0, new Question(data));
        } else {
          this.formObj.pageFields[toPIdx].formFields.splice(toQIdx, 0, new Question(data));
        }
      }
      this.services.form.addNewQuesInAllQuesObj(new Question(data));
      this.onSuccess.emit('copy-question');
      setTimeout(() => {
        this.services.utils.scrollPage(data._id, 150);
        this.cancelCopyOrMoveQuestion();
        this.services.toastr.success('Question has been copied successfully.','Success');
      },1000);
    })
  }

  moveQuestion(fromPIdx, toPIdx, refQuesId, toQIdx){
    let pagesArr = this.services.form.getAllPageIdsArr();
    let question = this.services.form.allQuestionsOfSurvey[this.copyInputData.quesId];

    let carryFrwdFlag: boolean = this.checkForCarryForward(question, pagesArr, 'moved');
    if(!carryFrwdFlag){
      return;
    }

    let pipingFlag: boolean = this.checkForPiping(pagesArr);
    if(!pipingFlag){
      return;
    }

    let arrayOfSrcQuesId: Array<string> = [];
    for(let logic of question.destLogicArr){
      arrayOfSrcQuesId.push(logic.srcQId);
    }

    let allquesArr = this.services.form.getAllQuesIdArr();
    let toCheckForCircularLogic: boolean = false;
    for(let qId of allquesArr){
      if(qId == this.copyMoveQuesObj.toRefQuesId && qId != this.copyInputData.quesId){
        toCheckForCircularLogic = true;
      }
      if(toCheckForCircularLogic){
        if(arrayOfSrcQuesId.indexOf(qId) != -1){
          this.services.toastr.warning('Logic behaviour might be circular now.', 'Circular Logic');
        }
      }
    }

    this.services.resource.moveQuestion(this.copyInputData.quesId, this.copyMoveQuesObj.fromPageId, this.copyMoveQuesObj.toPageId, refQuesId).subscribe((data) => {
      this.formObj.pageFields[fromPIdx].formFields.splice(this.copyInputData.quesIdx, 1);
      let idx: number = 0;
      for(let qId of data.moveDone.formFields){
        if(qId == question._id){
          this.formObj.pageFields[toPIdx].formFields.splice(idx, 0, new Question(question));
          break;
        }
        idx++;
      }
      this.onSuccess.emit('copy-question');

      setTimeout(() => {
        this.services.utils.scrollPage(question._id, 150);
        this.cancelCopyOrMoveQuestion();
        this.services.toastr.success('Question has been moved successfully.','Success');
      },1000);
    })
  }

  checkForCarryForward(question: any, pagesArr: any, str: string): boolean{
    for(let carryItem of question.frmCarryForward){
      let pIdOfCarryQues = this.services.form.getPageId(carryItem.quesId);
      if(pagesArr.indexOf(this.copyMoveQuesObj.toPageId) <= pagesArr.indexOf(pIdOfCarryQues)){
        this.services.alert({
            title: "Alert",
            text: "Carry forwarded Question can not be " + str + " on or before the page from which it is carrying the carry forwarded responses.",
            showConfirmButton: true
        });
        return false;
      }
    }

    for(let qId of question.toCarryForward){
      let pIdOfCarryQues = this.services.form.getPageId(qId);
      if(pagesArr.indexOf(this.copyMoveQuesObj.toPageId) >= pagesArr.indexOf(pIdOfCarryQues)){
        this.services.alert({
            title: "Alert",
            text: "Carry forwarded Question can not be " + str + " on or after the page to which it is carry forwarded.",
            showConfirmButton: true
        });
        return false;
      }
    }
    return true;
  }

  checkForPiping(pagesArr: any): boolean{
    let piped = this.services.form.getPipingObject();
    let isPiped = this.services.form.getIsPipingObject();

    if(piped[this.copyInputData.quesId]){
      for(let qId of piped[this.copyInputData.quesId]){
        let pIdOfPipedQues = this.services.form.getPageId(qId);
        if(pagesArr.indexOf(this.copyMoveQuesObj.toPageId) <= pagesArr.indexOf(pIdOfPipedQues)){
          this.services.alert({
              title: "Alert",
              text: "Piped question can not be on the same or later page",
              showConfirmButton: true
          });
          return false;
        }
      }
    }

    if(isPiped[this.copyInputData.quesId]){
      for(let qId of isPiped[this.copyInputData.quesId]){
        let pIdOfIsPipedQues = this.services.form.getPageId(qId);
        if(pagesArr.indexOf(this.copyMoveQuesObj.toPageId) >= pagesArr.indexOf(pIdOfIsPipedQues)){
          this.services.alert({
              title: "Alert",
              text: "Piped question can not be on the same or later page",
              showConfirmButton: true
          });
          return false;
        }
      }
    }
    return true;
  }

}



