import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { SharedServices } from '../../../../../../shared/services/services.module';

@Component({
  selector: 'app-survey-page-title-settings',
  templateUrl: './survey-page-title-settings.component.html',
  styleUrls: ['./survey-page-title-settings.component.css']
})
export class SurveyPageTitleSettingsComponent implements OnInit {
  @Input() settingsInpData: any;
  @Output() onCancel = new EventEmitter<any>();

  settings = {
    model: ''
  }

  formObj: any;
  allPagesAndQues: any;
  constructor(
    private services: SharedServices
  ) { }

  ngOnInit() {
    this.formObj = this.services.form.formObj;
    this.allPagesAndQues = this.services.form.getAllPagesAndQuestions();
    this.setAction();
  }

  setAction(){
    let inpData = this.settingsInpData;
    if(inpData.field == 'survey'){
      this.settings.model = this.formObj.title;
    }else if(inpData.field == 'page'){
      this.settings.model = this.allPagesAndQues['data'][inpData.id][inpData.subField];
    }
  }

  onEvent(event: any){
    if (event.type == 'keyup') {
      event.options.element = event.options.content;
      this.settings.model = event.options.content;
    }
  }

  saveSettings(){
    let str: string = this.settings.model;
    if(this.settingsInpData.field == 'survey'){
      this.saveSurveyTitle(str);
    }else if(this.settingsInpData.field == 'page'){
      this.savePageDetails(str);
    }
  }

  saveSurveyTitle(str: string){
    if (this.services.utils.getTextOutOfHtml(str).trim() == '') {
      this.services.toastr.error('Survey Title can not be Empty');
      return false;
    }
    let surveyData: any = {
      title: str
    }
    this.services.resource.setSurveyTtile(this.settingsInpData.id, surveyData).subscribe((data) => {
      this.formObj.title = str;
      this.services.toastr.success('Survey Title has been updated successfully', 'Updated');
      this.cancelSettings();
    })
  }

  savePageDetails(str: string){
    let inpData = this.settingsInpData;
    this.allPagesAndQues.data[inpData.id][inpData.subField] = str;
    let pageData: any = {
      title: this.allPagesAndQues.data[inpData.id].title,
      desc: this.allPagesAndQues.data[inpData.id].desc
    }
    this.services.resource.setPageTitleAndDesc(inpData.id, pageData).subscribe((data) => {
      if (inpData.subField == 'title') {
        this.formObj.pageFields[inpData.idx].title = pageData.title;
        this.allPagesAndQues.data[inpData.id].titleText = this.services.utils.getTextOutOfHtml(pageData.title);
        this.services.toastr.success('Page Title has been updated successfully', 'Updated');
      } else if (inpData.subField == 'desc') {
        this.formObj.pageFields[inpData.idx].desc = pageData.desc;
        this.services.toastr.success('Page Description has been updated successfully', 'Updated');
      }
      this.cancelSettings();
    });
  }

  cancelSettings(){
    this.onCancel.emit();
  }

}



