import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { SharedServices } from '../../../../shared/services/services.module';
import { PageOperationsService } from '../survey.main-list/page.operations.service';
import { EditDataService } from '../edit.data.service';

declare var Page;

@Component({
  selector: 'app-survey-page-delete',
  templateUrl: './survey.page.delete.component.html',
  styleUrls: ['./survey.page.delete.component.css', '../survey.design/left.panel/logics/logics.component.scss']
})
export class SurveyPageDeleteComponent implements OnInit {
  @Input() delPageInpData;
  @Output() onCancel = new EventEmitter<any>();
  @Output() onSuccess = new EventEmitter<any>();
  delPage: any = {
    delOrder: 0,
    pageId: '',
    pageIdx: '',
    data: {}
  }

  allQuestionsOfSurvey: any;
  formObj: any;
  questionCountAndId: any;
  thisPageQuesIdArr: any;
  titlesOfthisPageQues: any = {};

  showDelNotification: boolean = false;
  delNotification: any = {}

  constructor(
    private services: SharedServices,
    private pageOpService: PageOperationsService,
    private editService: EditDataService
  ) { }

  ngOnInit() {
    this.delPage.pageIdx = this.delPageInpData.pageIdx;
    this.delPage.pageId = this.delPageInpData.pageId;
    this.formObj = this.services.form.formObj;
    this.allQuestionsOfSurvey = this.services.form.allQuestionsOfSurvey;
    this.questionCountAndId = this.services.form.getQuestionCountAndId();
    this.thisPageQuesIdArr = this.formObj.getAllQuesIdByPageId(this.delPage.pageId);
  }

  confirmDelPage() {
    let pageData = this.delPage;
    let allPageIdArr = this.services.form.getAllPageIdsArr();

    if(pageData.delOrder == 1){
      let prevPageId = allPageIdArr[this.delPage.pageIdx - 1]
      let prevPageQuesIdArray = this.formObj.getAllQuesIdByPageId(prevPageId);
      this.thisPageQuesIdArr.forEach((qId) => {
        let frmCarryOfEach = this.allQuestionsOfSurvey[qId].frmCarryForward;
        frmCarryOfEach.forEach((fQItem) => {
          if(prevPageQuesIdArray.indexOf(fQItem.quesId) >= 0){
            this.services.alert({
              title: "Alert",
              text: "Questions cannot be moved up as there are some questions which contains carry forwarded choices",
              showConfirmButton: true
            });
            return;
          }
        })
      })
    }else if(pageData.delOrder == 2){
      let nextPageId = allPageIdArr[this.delPage.pageIdx + 1]
      let nextPageQuesIdArray = this.formObj.getAllQuesIdByPageId(nextPageId);
      this.thisPageQuesIdArr.forEach((qId) => {
        let toCarryOfEach = this.allQuestionsOfSurvey[qId].toCarryForward;
        toCarryOfEach.forEach((tQId) => {
          if(nextPageQuesIdArray.indexOf(tQId) >= 0){
            this.services.alert({
              title: "Alert",
              text: "Questions cannot be moved down as there are some questions whose options are being carry forwarded",
              showConfirmButton: true
            });
            return;
          }
        })
      })
    }
    this.deleteSelectedPage(allPageIdArr);
  }

  deleteSelectedPage(allPageIdArr: any){
    let piped = this.services.form.getPipingObject();
    let isPiped = this.services.form.getIsPipingObject();
    if(this.delPage.delOrder == 1){
      this.thisPageQuesIdArr.forEach((quesId) => {
        let numOfPagesBeforeNewCurrentPos = (this.delPage.pageIdx - 1); //num of pages before prevpage
        if(piped[quesId]){
          piped[quesId].forEach((pipeQId) => {
            let pageOfPipeQues = this.formObj.getPageByQuestionId(pipeQId);
            let numberOfPagesBeforePipeQId = allPageIdArr.indexOf(pageOfPipeQues._id);
            if(numOfPagesBeforeNewCurrentPos <= numberOfPagesBeforePipeQId){
              this.services.alert({
                title: "Alert",
                text: "Piped question can not be on the same or later page",
                showConfirmButton: true
              })
            }
          })
        }
      })
    }else if(this.delPage.delOrder == 2){
      this.thisPageQuesIdArr.forEach((quesId) => {
        let numOfPagesBeforeNewCurrentPos = (this.delPage.pageIdx + 1);  //num of pages before next page
        if(isPiped[quesId]){
          isPiped[quesId].forEach((isPipeQId) => {
            let pageOfIsPipeQues = this.formObj.getPageByQuestionId(isPipeQId);
            let numberOfPagesBeforeIsPipeQId = allPageIdArr.indexOf(pageOfIsPipeQues._id);
            if(numOfPagesBeforeNewCurrentPos >= numberOfPagesBeforeIsPipeQId){
              this.services.alert({
                title: "Alert",
                text: "Piped question can not be on the same or later page",
                showConfirmButton: true
              })
            }
          })
        }
      })
    }else if(this.delPage.delOrder == 0){
      let deletePageAjax = {
          titleArr: [],
          piping: {
              piperQuestions: [],
              pipedQuestions: []
          }
      }
      this.thisPageQuesIdArr.forEach((quesIdOnCurrPage) => {
        if(isPiped[quesIdOnCurrPage]){
          if(!this.delNotification[quesIdOnCurrPage]){
            this.delNotification[quesIdOnCurrPage] = {
              'piping': []
            }
          }else{
            this.delNotification[quesIdOnCurrPage]['piping'] = [];
          }
          isPiped[quesIdOnCurrPage].forEach((isPipeQId) => {
            let titleObj:any = {}
            let strToBeReplaced = '{{p://' + quesIdOnCurrPage + '}}';
            titleObj['id'] = isPipeQId;
            titleObj['title'] = this.allQuestionsOfSurvey[isPipeQId].title.replace(new RegExp(strToBeReplaced, 'g'), '');
            deletePageAjax.titleArr.push(titleObj);
            this.delNotification[quesIdOnCurrPage]['piping'].push(isPipeQId);
          })
        }
      })

      this.thisPageQuesIdArr.forEach((quesIdOnCurrPage) => {
        if(piped[quesIdOnCurrPage]){
          piped[quesIdOnCurrPage].forEach((pipeQId) => {
            let validPipeElseWhere = 0;
            isPiped[pipeQId].forEach((quesId) => {
              let pageIdOfPipeQues = this.formObj.getPageByQuestionId(quesId)._id;
              if(pageIdOfPipeQues != this.delPage.pageId){
                validPipeElseWhere = 1;
              }
            })
            if(validPipeElseWhere == 0){
              let ifPresentIdx = this.services.utils.findIndex(deletePageAjax.piping.pipedQuestions, 'id', pipeQId);
              if(ifPresentIdx == -1){
                let pipedObj: any = {}
                pipedObj['id'] = pipeQId;
                pipedObj['isPiped'] = 0;
                deletePageAjax.piping.pipedQuestions.push(pipedObj);
              }
            }
          })
        }
      })
      this.delPage.data = deletePageAjax;
    }
    this.makeCallTodeletePage();
  }

  makeCallTodeletePage(){
    let pageData = this.delPage;
    if (pageData.delOrder == 0) {
      if(this.formObj.getPageFields().length == 1){
        this.services.toastr.warning('There must be atleast one page.', 'Cannot be Deleted.');
        this.cancelDelPage();
        this.onSuccess.emit('page-delete');
      }else{
        this.services.resource.deletePage(pageData.pageId, this.delPageInpData.surId, pageData.delOrder, pageData.data).subscribe((data) => {
          this.getTitlesOfthisPageQues();
          this.questionAffectedByDeletingPage();
          this.carryForwardAffectedQues();
          this.checkForQuota();
          this.checkForCorruptedCriteria();
          this.formObj.pageFields.splice(pageData.pageIdx, 1);
          if(Object.keys(this.delNotification).length){
            this.showDelNotification = true;
          }else{
            this.cancelDelPage();
          }
          this.services.toastr.success('Page has been deleted successfully', 'Success');
          this.onSuccess.emit('page-delete');
        });
      }
    } else {
      this.services.resource.deletePage(pageData.pageId, this.delPageInpData.surId, pageData.delOrder, {}).subscribe((data) => {
        if (pageData.delOrder == 1) {
          this.formObj.pageFields.splice(pageData.pageIdx - 1, 2, new Page(data.page));
        } else if (pageData.delOrder == 2) {
          this.formObj.pageFields.splice(pageData.pageIdx, 2, new Page(data.page));
        }
        this.services.toastr.success('Page has been deleted successfully', 'Success');
        this.onSuccess.emit('page-delete');
        this.cancelDelPage();
      });
    }
  }

  getTitlesOfthisPageQues(){
    this.thisPageQuesIdArr.forEach((qId) => {
      this.titlesOfthisPageQues[qId] = this.allQuestionsOfSurvey[qId].getTitle();
    })
  }

  questionAffectedByDeletingPage(){
    let logicAffected;
    this.thisPageQuesIdArr.forEach((quesIdOnCurrPage) => {
      let quesTitle: string = this.services.utils.getTextOutOfHtml(this.allQuestionsOfSurvey[quesIdOnCurrPage].title);
      logicAffected = this.pageOpService.isQuestionInvolvedInLogic(quesIdOnCurrPage);
      if(Object.keys(logicAffected).length != 0){
        if(!this.delNotification[quesIdOnCurrPage]){
          this.delNotification[quesIdOnCurrPage] = {
            'logics': []
          }
        }else{
          this.delNotification[quesIdOnCurrPage]['logics'] = [];
        }
        for(let qid in logicAffected){
          logicAffected[qid].forEach((logicName,logicIdx) => {
            // this.pageOpService.logicLeftOnAffQues(qid, logicName); //to remove logic
            logicAffected[qid][logicIdx] = this.editService.getLogicType(logicName);
            this.delNotification[quesIdOnCurrPage]['logics'] = logicAffected;
          })
        }
      }
    })
  }

  carryForwardAffectedQues(){
    this.thisPageQuesIdArr.forEach((quesIdOnCurrPage) => {
      let toCarryData = [];
      if (this.allQuestionsOfSurvey[quesIdOnCurrPage].toCarryForward && this.allQuestionsOfSurvey[quesIdOnCurrPage].toCarryForward.length) {
        if(!this.delNotification[quesIdOnCurrPage]){
          this.delNotification[quesIdOnCurrPage] = {
            'carryfrwd' : []
          }
        }else{
          this.delNotification[quesIdOnCurrPage]['carryfrwd'] = [];
        }
        toCarryData = this.allQuestionsOfSurvey[quesIdOnCurrPage].toCarryForward;
        if(toCarryData.length){
          toCarryData.forEach((cQuesId) => {
            let quesToUpdate = this.allQuestionsOfSurvey[cQuesId];
            if(quesToUpdate.frmCarryForward){
              quesToUpdate.frmCarryForward = quesToUpdate.frmCarryForward.filter((item) =>
                (item.quesId != quesIdOnCurrPage)
              )
              this.delNotification[quesIdOnCurrPage]['carryfrwd'].push(cQuesId);
            }
          })
        }
      }
      let frmCarryData = [];
      if (this.allQuestionsOfSurvey[quesIdOnCurrPage].frmCarryForward) {
        frmCarryData = this.allQuestionsOfSurvey[quesIdOnCurrPage].frmCarryForward;
        frmCarryData.forEach((frmCarryItem) => {
          let quesToUpdate = this.allQuestionsOfSurvey[frmCarryItem.quesId];
          if(quesToUpdate.toCarryForward){
            quesToUpdate.toCarryForward = quesToUpdate.toCarryForward.filter((cQId) =>
              (cQId != quesIdOnCurrPage)
            )
          }
        })
      }
    })
  }

  checkForQuota(){
    if(this.formObj.quota && this.formObj.quota.sQuota){
      if(this.thisPageQuesIdArr.indexOf(this.formObj.quota.sQuota.quesId) > -1){
        if(!this.delNotification[this.formObj.quota.sQuota.quesId]){
          this.delNotification[this.formObj.quota.sQuota.quesId] = {}
        }
        this.delNotification[this.formObj.quota.sQuota.quesId]['quota'] = {
          'name': this.formObj.quota.name
        }
      }
    }
    if(this.formObj.quota && this.formObj.quota.cQuota && this.formObj.quota.cQuota.quesInQuota){
      this.thisPageQuesIdArr.forEach((qId) => {
        if(this.services.utils.findIndex(this.formObj.quota.cQuota.quesInQuota, 'quesId', qId) > -1){
          if(!this.delNotification[qId]){
            this.delNotification[qId] = {}
          }
          this.delNotification[qId]['quota'] = {
            'name': this.formObj.quota.name
          }
        }
      })
    }
  }

  checkForCorruptedCriteria(){
    this.thisPageQuesIdArr.forEach((qId) => {
      this.formObj.criteria.forEach((criteria, cIdx) => {
        let idx = this.services.utils.findIndex(criteria.quesProp, 'quesId', qId);
        if(idx > -1){ //if ques present in criteria
          !this.delNotification[qId] && (this.delNotification[qId] = {});
          !this.delNotification[qId]['criteria'] && (this.delNotification[qId]['criteria'] = []);
          this.delNotification[qId]['criteria'].push(criteria.name);
          this.formObj.criteria[cIdx]['isCorrupt'] = 1;
        }
      })
    })
  }

  cancelDelPage() {
    this.delPage.delOrder = 0;
    this.delPage.pageId = '';
    this.delPage.pageIdx = '';
    this.onCancel.emit();
  }

  onCloseDelNot(){
    this.showDelNotification = false;
    this.cancelDelPage();
  }

}



