import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { SharedServices } from '../../../shared/services/services.module';
import { EditDataService } from './edit.data.service';

@Component({
    selector: 'survey-edit',
    templateUrl: './survey.edit.component.html',
    styleUrls: ['./survey.edit.component.scss']
})
export class SurveyEdit implements OnInit {
  surveyDesignData: any;

  constructor(
    private route: ActivatedRoute,
    private editDataService: EditDataService,
    private services: SharedServices
  ) { }

  ngOnInit() {
    this.editDataService.sId = this.route.params["value"].sid;
    this.surveyDesignData = this.editDataService.surveyDesignData;
  }

}



