import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subscription } from 'rxjs/Subscription';
import { SharedServices } from '../../../../shared/services/services.module';
import { EditDataService } from '../edit.data.service';

@Component({
  selector: 'app-survey-advance',
  templateUrl: './survey.advance.component.html',
  styleUrls: ['../survey.edit.component.scss','./survey.advance.component.scss']
})
export class SurveyAdvanceComponent implements OnInit, OnDestroy {
  advanceSelected: string;   //<'quota','tic-rule','language-translate'>
  addOtherLanguagesButton: boolean = false;
  langData: any;
  subscription: Subscription;
  
  constructor(
    private services: SharedServices,
    private editService: EditDataService
  ) { }

  ngOnInit() {
    this.subscription = this.editService.getSurveyData().subscribe(data => {
      if(!data) return;
      this.advanceSelected = 'quota';
    });
  }

  onEditLanguage(e: any){
      this.langData = e;
      this.advanceSelected = 'language-translate';
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

}



