import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';
import { EditDataService } from '../edit.data.service';

@Component({
  selector: 'app-survey-add-question',
  templateUrl: './survey.add.question.component.html',
  styleUrls: ['../survey.edit.component.scss']
})
export class SurveyAddQuestionComponent implements OnInit {
  @Output() onRestoreQues: EventEmitter<any> = new EventEmitter<any>();

  selectedQuesType: any;
  surveyDesignData: any;

  constructor(
    private router: Router,
    private editDataService : EditDataService,
  ) { }

  ngOnInit() {
    this.selectedQuesType = this.editDataService.selectedQuesType;
    this.surveyDesignData = this.editDataService.surveyDesignData;
  }

  setQuestionType(type: string){
    this.selectedQuesType.type = type;
    this.router.navigate(['/survey', 'design', this.editDataService.addQuestionRelatedData.surId, 'question', 'qid']);
  }

  onRestoreQuestion(e: any){
    this.onRestoreQues.emit(e);
  }

}



