import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { EditDataService} from '../../edit.data.service';
import { SharedServices } from '../../../../../shared/services/services.module';
import { QuestionTypesService } from '../../../../../shared/questionTemplates/question-types.service';

declare var Question;

@Component({
  selector: 'app-survey-deleted-question',
  templateUrl: './survey.deleted-question.component.html',
  styleUrls: ['./survey.deleted-question.component.scss']
})
export class SurveyDeletedQuestionComponent implements OnInit {

  constructor(
    private services: SharedServices,
    private editDataServices: EditDataService,
    private qTypesServ: QuestionTypesService,
  ) { }

  @Output() onRestore: EventEmitter<any> = new EventEmitter<any>();

  formObj:any;
  deletedQuestionList:any = [];
  pageId:String;
  resData:any;

  ngOnInit() {
    this.formObj = this.services.form.formObj;
    console.log("survey",this.formObj)
    this.resData = this.editDataServices.addQuestionRelatedData;
    this.services.resource.getDeletedQuestionList(this.formObj._id).subscribe((dat) => {
      console.log(dat);
      this.deletedQuestionList = dat;
      for(let i=0; i<this.deletedQuestionList.length; i++){
        this.deletedQuestionList[i].d = this.getDateDiffFromCurrent(new Date(this.deletedQuestionList[i].modified));
      }
    });

    console.log(this.deletedQuestionList)
  }

  getIcon(fieldType){
    return this.qTypesServ.questionTypesJSON[this.qTypesServ.getQuestionType(fieldType.family,fieldType.subtype).type].icon;
  }

  restoreQuestion(dqID){
    let dq = this.deletedQuestionList[dqID];
    delete this.deletedQuestionList[dqID];

    let toPageId = this.resData.pageId;
    let refQuesId = this.resData.addQuesRefId;

    this.services.resource.restoreDeletedQuestionList(dq._id,toPageId,refQuesId).subscribe((dat) => {
      this.formObj.pageFields[this.resData.pageIndex].formFields.splice(this.resData.addQuesRefIndex, 0, new Question(dat));
      this.services.form.addNewQuesInAllQuesObj(new Question(dat));
      this.onRestore.emit('restore-del-ques');
      console.log(dat);
    });

  }

  getDateDiffFromCurrent(date_old:Date){
    let n:Date = new Date();                                                         //now
    let d = Math.abs(n.getTime() - date_old.getTime()) / 1000;                           // delta
    let r = {};                                                                // result
    let s = {                                                                  // structure
        year: 31536000,
        month: 2592000,
        week: 604800,
        day: 86400,
        hour: 3600,
        minute: 60,
        second: 1
    };

    Object.keys(s).forEach(function(key){
        r[key] = Math.floor(d / s[key]);
        d -= r[key] * s[key];
    });

    let ret = {"type":"","diff":0};
    for(let i=0; i<Object.keys(s).length; i++){
      if(r[Object.keys(s)[i]] != 0){
        if(r[Object.keys(s)[i]] == 1){
            ret = {"type":Object.keys(s)[i],"diff":r[Object.keys(s)[i]]};
        }else{
            ret = {"type":Object.keys(s)[i]+"s","diff":r[Object.keys(s)[i]]};
        }
        break;
      }
    }

    return ret;
  }

}



