import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ConstantsService } from '../../shared/services/constants.service';
import { EditDataService } from './edit/edit.data.service';

@Component({
    selector: 'survey',
    templateUrl: './survey.component.html',
    styleUrls: ['./survey.component.scss']
})
export class Survey implements OnInit {
  isCollapsed: boolean = true;
  surveyData: any;
  apiUrl: string;
  survey: any;

  constructor(
    private editDataServ: EditDataService,
    private router: Router,
    private consServ : ConstantsService
  ) { }

  ngOnInit() {
    this.apiUrl = this.consServ.apiUrl;
    this.survey = this.editDataServ;
  }

  toggleCollapse(){
    this.isCollapsed = !this.isCollapsed;
  }

  changeRouting(){
    this.isCollapsed = true;
  }

  openNewWindowForPreview(){
    window.open(window.location.protocol+"//"+window.location.host+'/survey/preview/'+this.editDataServ.sId, 'targetWindow', 'toolbar=no, location=no, status=no, menubar=no, scrollbars=yes, resizable=yes');
  }
}



