import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormArray, FormControl, FormGroup, FormBuilder } from '@angular/forms';

import { SharedServices } from '../../../../../../../shared/services/services.module';
import { LogicService } from '../logic.service';

@Component({
  selector: 'app-edata-logic',
  templateUrl: './edata.logic.component.html',
  styleUrls: [
    '../logics.component.scss',
      './edata.logic.component.scss'
  ]
})
export class EdataLogicComponent implements OnInit {
  @Input() qId: any;

  @Output() logicChanged: EventEmitter<any> = new EventEmitter<any>();
  @Output() canceled: EventEmitter<any> = new EventEmitter<any>();

  question: any;
  criteriaArr: any[];
  logicFormGroup: FormGroup;
  edataList: any;
  appliedLogicsLength: number;
  logicName: string = this.logicService.allLogicName.embed.name;
  additionalKeysInLogic: any = {
    eDataArr: [
      {field: "", value: ""}
    ]
  };
  requiredOptions: any;
  eDataTypeList: Array<any> = [];
  defaultEdataOption: string;;

  constructor(
  	private logicService: LogicService,
    private services: SharedServices
 	) {}

  ngOnInit() {
    this.question = this.services.form.getFormData().getQuestionById(this.qId);
    this.criteriaArr = this.services.form.getFormData().getAllCriterias();
    this.requiredOptions = {
      typeOfEmbeddedData: this.services.constants.typeOfEmbeddedData,
      contactFields: this.services.constants.contactFields
    }
    this.defaultEdataOption = this.requiredOptions.typeOfEmbeddedData[0].value;
    this.services.resource.getEdataList().subscribe(data=>this.edataList=data);
  	this.setAppliedLogic();
  }

  setAppliedLogic() {
    let question = this.services.utils.cloneObject(this.question);
  	question.logicArr = question.logicArr.filter((logic) => {return logic.logic == this.logicName;});
  	if(!question.logicArr.length) {
  		question.logicArr.push(this.getLogicData());
      this.eDataTypeList = [[this.defaultEdataOption]];
  	} else {
      question.logicArr.forEach(logic => {
        let typeArr = [];
        logic.eDataArr.forEach(item => {
          let eData = this.services.eData.getDataFromString(item.value);
          typeArr.push(eData.type);
          item.value = eData.value;
        });
        this.eDataTypeList.push(typeArr);
      });
    }
    this.buildCriteriaForm(question);
    this.appliedLogicsLength = this.logicFormGroup.get('logicArr')["controls"].length;
  }

  buildCriteriaForm(question: any) {
    this.logicFormGroup = this.logicService.buildCriteriaForm(question, this.additionalKeysInLogic);
  }

  addNewCondition() {
    let logicData = this.getLogicData();
    this.logicService.setDataForAdditionalParam(logicData, this.additionalKeysInLogic, this.additionalKeysInLogic);
    this.logicFormGroup.get('logicArr')["push"](this.logicService.getFormGroup(logicData));
    this.eDataTypeList.push([[this.defaultEdataOption]]);
    this.appliedLogicsLength = this.logicFormGroup.get('logicArr')["controls"].length;
  }

  addNewEdataCondition(logic: any, lIndex: number, eIndex: number) {
    let logicData = this.getLogicData();
    logic.get('eDataArr')["insert"](eIndex + 1, this.logicService.getFormGroup(this.additionalKeysInLogic.eDataArr[0]));
    this.eDataTypeList[lIndex].splice(eIndex + 1, 0, [[this.defaultEdataOption]]);// push([[this.defaultEdataOption]]);
  }

  saveLogic() {
    var logicArr = this.services.utils.cloneObject(this.logicFormGroup.value.logicArr);
    logicArr.forEach((logic, lIndex) => {
      logic.eDataArr.forEach((item, eIndex) => {
        item.value = this.services.eData.getEdataString(this.eDataTypeList[lIndex][eIndex], item.value);
      });
    });

    let logicFormData = this.logicService.getLogicFormData(this.question, logicArr, this.logicName);

    this.logicService.saveLogicData(logicFormData, this.question._id).subscribe((data) => {
      this.logicChanged.emit({logicName: this.logicName, res: data});
    });
  }

  updateLogic(logic: any, index: number) {

  }

  deleteLogic(index: number) {
  	if(this.appliedLogicsLength > 1) {
	  	this.logicFormGroup.get('logicArr')["removeAt"](index);
      this.eDataTypeList.splice(index, 1);
      this.appliedLogicsLength = this.logicFormGroup.get('logicArr')["controls"].length;
	  }
  }

  deleteEdataCondition(logic: any, lIndex: number, eIndex: number) {
    if(logic.get('eDataArr').controls.length > 1) {
      logic.get('eDataArr')["removeAt"](eIndex);
      this.eDataTypeList[lIndex].splice(eIndex, 1);
    }
  }

  getLogicData() {
    let logicObject = this.services.utils.cloneObject(this.logicService.logicData);
    if(this.additionalKeysInLogic) {
      for(let key in this.additionalKeysInLogic) {
        logicObject[key] = this.additionalKeysInLogic[key];
      }
    }
    logicObject["lType"] = "normal";
    logicObject["logic"] = this.logicName;
    return logicObject;
  }

  removeAllLogic() {
    this.logicService.removeAllLogicByName(this.question, this.logicName).subscribe((data) => {
      this.logicChanged.emit({logicName: this.logicName, res: data});
      this.services.toastr.success('Logic has been successfully removed.');
    });
  }

  showCriteriaForm(event) {
    (event.target.value == '0') && this.logicChanged.emit({view: 'Criteria'})
  }

  cancel() {
    this.canceled.emit();
  }

}



