import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { Subscription } from 'rxjs/Subscription';
import { SharedServices } from '../../../../shared/services/services.module';
import { QuestionTypesService } from '../../../../shared/questionTemplates/question-types.service';
import { EditDataService } from '../edit.data.service';

@Component({
  selector: 'app-survey-flow',
  templateUrl: './survey.flow.component.html',
  styleUrls: ['../survey.edit.component.scss', './survey.flow.component.scss']
})
export class SurveyFlowComponent implements OnInit {
  @Output() onRandomization: EventEmitter<any> = new EventEmitter<any>();
  formObj: any;
  allPagesAndQues: any;
  allQuestionsOfSurvey: any;
  quesJSON: any;
  collapseAllQues: boolean = true;

  constructor(
    private service: SharedServices,
    private quesTypesService: QuestionTypesService,
    private editService: EditDataService
  ) { }

  ngOnInit() {
    this.quesJSON = this.quesTypesService.questionTypesJSON;
    this.formObj = this.service.form.getFormData();
    this.allPagesAndQues = this.service.form.getAllPagesAndQuestions();
    this.allQuestionsOfSurvey = this.service.form.allQuestionsOfSurvey;
  }

  collapseAllQuestions(){
    this.collapseAllQues = !this.collapseAllQues;
    for(let page in this.allPagesAndQues.data){
      this.allPagesAndQues.data[page].collapseQues = this.collapseAllQues;
    }
  }

  showPageRandomization(){
    let data: any = {
      action: 'randomization',
      ranInpData: {'type':'Page'}
    };
    this.onRandomization.emit(data);
  }

  goToQuestion(event: any, id: string, type?: string): void {
    event.stopPropagation();
    let scrollOffset: number = (type == 'q') ? 150: 110;
    this.service.utils.scrollPage(id, scrollOffset);
  }

}



