import { Injectable } from '@angular/core';
import {FormArray, FormControl, FormGroup, FormBuilder} from '@angular/forms';

import { SharedServices } from '../../../../../../shared/services/services.module';

@Injectable()
export class LogicService {
  logicNameArr: Array<any> = [
    "rLogic",
    "embed",
    "protractor",
    "promoter",
    "display",
    "compound",
    "skip"
  ];
  allLogicName: any = {};
  logicFormGroup: FormGroup;
  logicData: any = {
    "destPId":"",
    "destQId":"",
    "lType":"",
    "ansId":"",
    "metUnmet":1,
    "logic":"",
    "text": ""
  };
  pageFields: any; //value set from criteria-and-logics component
  pageId: string; //value set from criteria-and-logics component

  constructor(
    private _fb: FormBuilder,
    private service: SharedServices
  ) {
    this.logicNameArr.forEach((logicName) => {
      this.allLogicName[logicName] = {
        name: logicName
      }
    });
  }

  setPageFields(pageFields: any) {
    this.pageFields = this.service.utils.cloneObject(pageFields);

  }

  validateFbAppId(appId: string){
    return this.service.resource.validateApi(appId);
  }

  setPageId(id: string) {
    this.pageId = id;
  }

  buildCriteriaForm(question: any, addtionalParam?: any){
    let logicData = this.service.utils.cloneObject(this.logicData);
    addtionalParam = this.service.utils.cloneObject((addtionalParam || {}));

    let logicFB: FormGroup = this._fb.group({
      logicArr: this.getFormArray()
    });

    question["logicArr"].forEach((logic) => {
      this.setDataForAdditionalParam(logicData, addtionalParam, logic);
      (<FormArray>logicFB.get('logicArr')).push(this.getFormGroup(logicData));
    });
    logicFB.patchValue(question);
    return logicFB;
  }

  getFormGroup(data: any) {
    return this._fb.group(data);
  }

  getFormArray() {
    return this._fb.array([]);
  }

  setDataForAdditionalParam(data: Object, addtionalParam: Object, item: Object) {
    for(let key in addtionalParam) {
      if (Array.isArray(addtionalParam[key]) && (addtionalParam[key][0] != undefined)) {
        data[key] = this.getFormArray();
        item && item[key] && Array.isArray(item[key]) &&  item[key].forEach(keyData => {
          data[key].push(this.getFormGroup(addtionalParam[key][0]));
        });
      } else {
        data[key] = addtionalParam[key];
      }
    }
  }

  getAdditionalOptionsForQuestionList() {
  	return [{
  			value: "disqua",
  			label: "Disqualification Page"
  		}, {
  			value: "end",
  			label: "End Page"
  		}
  	]
  }

  getLogicFormData(question: any, logicArr: Array<any>, logicName: string) {
    let logicFormData = {};
    let questionLogicArr: Array<any> = question.logicArr.filter((logic) => { return logic.logic != logicName; });

    logicArr.forEach((logic, index) => {
      !logic.ansId && (logic.ansId = null);
      !logic.destPId && (logic.destPId = null);
      !logic.destQId && (logic.destQId = null);
      if(!this.checkForAdditionalOptions(logic.lType)) {
        logic.destQId = null;
        logic.destPId = null;
      }
      delete logic.text;
      questionLogicArr.push(logic);
    });

    let finalLogicArr: Array<any> = []
    this.logicNameArr.forEach((lName) => {
      finalLogicArr.push(questionLogicArr.filter((logic) => logic.logic == lName));
    });
    finalLogicArr = Array.prototype.concat.apply([], finalLogicArr);

    logicFormData["logicArr"] = finalLogicArr;
    return logicFormData;
  }

  saveLogicData(logicFormData: any, qId: string) {
    logicFormData['pageId'] = this.service.form.formObj.getPageByQuestionId(qId).getId();
    logicFormData['surId'] = this.service.form.formObj._id;
    return this.service.resource.saveLogicData(logicFormData, qId);
  }

  checkForAdditionalOptions(value: any) {
    return this.getAdditionalOptionsForQuestionList().map((item) => item.value).indexOf(value) == -1;
  }

  removeAllLogicByName(question: any, logicName: string) {
    let LogicArr: Array<any> = question.logicArr.filter((logic) => { return logic.logic != logicName; });
    let data = {'logicArr': LogicArr};
    return this.saveLogicData(data, question._id);
  }

  getCarryOption(qId: string): any[] {
    let result: any = {
      topArray: [],
      bottomArray: []
    };
    let carryOptions = this.service.form.formObj.getCarryOptionArrays(qId);
    carryOptions.topArray.forEach(qData => {
      qData.options.forEach(opt => {
        opt.isCarry = true;
        result.topArray.push(opt);
      });
    });
    carryOptions.bottomArray.forEach(qData => {
      qData.options.forEach(opt => {
        opt.isCarry = true;
        result.bottomArray.push(opt);
      });
    });
    return result;
  }

  getLogicOnOption(logicArr: any[], option: any, scope: any) {
    let result = null;
    try {
      let logic = logicArr.find((logic) => logic.ansId == option._id);
      if(logic) {
        logic['text'] = option['text'];
      } else {
        logic = scope.getLogicData.call(scope, option);
      }
      result = logic;
    } catch (ex) {console.error(ex)}
    return result;
  }

}



