import { Component, OnInit, Input, Output, EventEmitter, ElementRef, ViewChild, TemplateRef, HostListener } from '@angular/core';
import { FormArray } from '@angular/forms';
import { QuestionTypesService } from '../../../../../shared/questionTemplates/question-types.service';
import { SharedServices } from '../../../../../shared/services/services.module';
import { BsModalService } from 'ngx-bootstrap/modal';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';


@Component({
  selector: 'app-row-col-options',
  templateUrl: './row.col.options.component.html',
  styleUrls: [
    './row.col.options.component.scss',
    '../../survey.edit.component.scss',
    '../survey.question.edit.component.scss']
})
export class RowColOptionsComponent implements OnInit {
  @Input() moreOptsInp; //<idx, quesType, rowOrCol, ans, compQues>
  @Output() onSelect: EventEmitter<any> = new EventEmitter<any>();

  @ViewChild('addDropDownItemsModal') addDropDownItemsModal;
  @ViewChild('validationModal') validationModal;

  @HostListener('document:click', ['$event'])
  onClick(e: any){
    if(!this.elemRef.nativeElement.contains(e.target)){
      this.onSelect.emit('done');
    }
  }

  ifDisplay: any = {
    hideColumns: false,
    addtnText: false,
    addtnTextChoiceRows: false,
    dropdownchoices: false,
    imagePiping: false,
    applyValidtn: false
  }
  dropDownItems = {
    value: ''
  }
  validationObject: any = {
    index: '',
    rowOrCol: ''
  };

  modalRef: BsModalRef;

  openModal(template: TemplateRef<any>, size: string) {
    this.modalRef = this.modalService.show(template, { class: size });
  }

  hideModal(): void {
    this.modalRef.hide();
  }

  constructor(
      private modalService: BsModalService,
      private services: SharedServices,
      private quesTypesService: QuestionTypesService,
      private elemRef: ElementRef
    ) {
  }

  ngOnInit() {
    let quesType = this.moreOptsInp.quesType;
    this.ifDisplay.hideColumns = (quesType == 'graph-rating' && this.moreOptsInp.rowOrCol == 'col');
    this.ifDisplay.addtnText = (quesType == 'dropdown' || quesType == 'single-text' || quesType == 'comment' || quesType == 'contacts');
    this.ifDisplay.addtnTextChoiceRows = (quesType == 'matrix-rating' || quesType == 'matrix-dropdown' || quesType == 'matrix-text'  && this.moreOptsInp.rowOrCol == 'row'|| quesType == 'graph-rating' && this.moreOptsInp.rowOrCol == 'row');
    this.ifDisplay.dropdownchoices = (quesType == 'matrix-dropdown' && this.moreOptsInp.rowOrCol == 'col');
    this.ifDisplay.imagePiping = !(quesType == 'dropdown' || quesType == 'single-text' || quesType == 'comment');
    this.ifDisplay.applyValidtn = (quesType == 'single-text' || quesType == 'mul-text' || quesType == 'matrix-text' || quesType == 'comment' || quesType == 'contacts');
  }

  onOptHide(){
    this.moreOptsInp.ans.get('isHidden').patchValue(!this.moreOptsInp.ans.get('isHidden').value);
    this.optSelected();
  }

  applyAddtnText(e: any){
    let opt = this.moreOptsInp.ans;
    if(e.target.checked){
      opt.patchValue({'hasText':1});
      opt._value.hasText = 1;
    }else{
      opt.patchValue({'hasText':0});
      opt.patchValue({'isComment':0});
    }
  }

  applyTextOrComment(e: any, textOrComment: string){
    let opt = this.moreOptsInp.ans;
    if(textOrComment == 'hasText'){
      opt.patchValue({'hasText':(e.target.checked ? 1 : 0)});
    }else if(textOrComment == 'isComment'){
      opt.patchValue({'isComment':(e.target.checked ? 1 : 0)});
    }

    if(opt.get('hasText').value && opt.get('isComment').value){ //if both hasText and isComment are true
      if(textOrComment == 'hasText'){
        opt.patchValue({'isComment':0});
      }else if(textOrComment == 'isComment'){
        opt.patchValue({'hasText':0});
      }
    }else if(!opt.get('hasText').value && !opt.get('isComment').value){ //if both hasText and isComment are false
      if(textOrComment == 'hasText'){
        opt.patchValue({'isComment':1});
      }else if(textOrComment == 'isComment'){
        opt.patchValue({'hasText':1});
      }
    }

    if(opt.get('isComment').value && this.ifDisplay.addtnTextChoiceRows){  //to apply isComment either on rows or on columns
      if(this.moreOptsInp.rowOrCol == 'row'){
        ((<FormArray>this.moreOptsInp.compQues.data.get('fieldOptions.col')).controls).forEach(obj => {
          obj.patchValue({'isComment':0});
        })
      }else{
        ((<FormArray>this.moreOptsInp.compQues.data.get('fieldOptions.row')).controls).forEach(obj => {
          obj.patchValue({'isComment':0});
        })
      }
    }
  }

  addDropDownMenu(){
    let opt = this.moreOptsInp.ans;
    let itemsArr: string[] = [];
    if((<FormArray>opt.get('items')).length){
      ((<FormArray>opt.get('items')).controls).forEach(item => {
        itemsArr.push(item.get('text').value);
      })
      this.dropDownItems.value = itemsArr.join('\n');
    }
    console.log(this.dropDownItems);
    this.openModal(this.addDropDownItemsModal, 'modal-md');
  }

  addDropDownItems(){
    let opt = this.moreOptsInp.ans;
    while((<FormArray>opt.get('items')).length){
      (<FormArray>opt.get('items')).removeAt((<FormArray>opt.get('items')).length - 1)
    }
    let options: any;
    options = this.dropDownItems.value.replace(/^\s*\n/gm, "");
    if(options == ''){
      this.services.alert({
        title: '<p class="sweetalert_title">Please fill atleast one choice</p>'
      });
      return false;
    }
    options = options.split("\n");
    let obj: any;
    options.forEach(val => {
      val = val.trim();
      if(val != ''){
        obj = this.quesTypesService.itemGroup(val);
        (<FormArray>opt.get('items')).push(obj);
      }
    })
    console.log(opt);
    this.closeDropDownItemsModal();
  }

  closeDropDownItemsModal(){
    this.hideModal();
    this.optSelected();
    this.dropDownItems.value = '';
  }

  openValidationModal(rowOrCol: String,idx: number){
    this.validationObject.index = this.moreOptsInp.idx;
    this.validationObject.rowOrCol = this.moreOptsInp.rowOrCol;
    this.openModal(this.validationModal, 'modal-md');
  }

  onValdnDone(){
    this.hideModal();
    this.optSelected();
  }

  addPiping(){
    this.onSelect.emit('piping');
  }

  addImage(){
    this.onSelect.emit('imgUpload');
  }

  optSelected(){
    this.onSelect.emit('done');
  }

}



