import { Injectable } from '@angular/core';
import {FormArray, FormControl, FormGroup, FormBuilder} from '@angular/forms';

@Injectable()
export class CriteriaService {
  criteriaJSON: any = {
    name: '',
    quesProp: [
      {
        quesId: '0',
        select: 1,
        typeOf: 'normal',
        count: '0',
        countOp: 'eq',
        operator: '1',
        opArr: [],
        rowID: [],
        eData: [
          {
            field: '{{e://}}',
            operator: 'eq',
            value: ''
          }
        ]
      }
    ],
    isCorrupt: ''
  }

 constructor(private _fb: FormBuilder) {

 }

 buildCriteriaForm(criteriaJSON: any){
   let criteria: FormGroup = this._fb.group({
     name: '',
     quesProp: this._fb.array([
     ]),
     isCorrupt: '',
     _id: ''
   });

   criteriaJSON.quesProp.forEach((question,question_idx) => {
     (<FormArray>criteria.get('quesProp')).push(this.addQues());
     question.eData.forEach((data,data_idx) => {
       (<FormArray>(<FormArray>criteria.get('quesProp')).at(question_idx).get('eData')).push(this.addEData());
     })
   })

   criteria.patchValue(criteriaJSON);

   return criteria;
 }

 addQues(){
   return this._fb.group({
     opArr: '',
     rowId: '',
     eData: this._fb.array([
     ]),
     quesId: '0',
     select: '1',
     typeOf: 'normal',
     count: '0',
     countOp: 'eq',
     operator: '1',
     _id: ''
   });
 }

 addEData(){
   return this._fb.group({
     field: '{{e://}}',
     operator: 'eq',
     value: '',
     _id: ''
   })
 }

}



