import { Injectable } from '@angular/core';
import { FormArray, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { S2cFormBuilderService } from '../../../../../shared/services/s2c.form.builder.service';

@Injectable()
export class TicketRuleService {
  ticketRuleJSON: object = {
    _id: '',
    name: '',
    folderId: '',
    owner: {
      typeOf: '',
      id: ''
    },
    grpId: '',
    assignee: '',
    watcherUsers: [],
    watcherGroups: [],
    priority: '',
    linkedSurvey: {
      feedback: {
        surId: '',
        method: '',
        _id: ''
      },
      satisfy: {
        surId: '',
        method: '',
        _id: ''
      }
    },
    tags: [],
    ticketData: {},
    defText: '',
  }

  triggerSurveyMethods: Array<any> = [
    {
      text: "Manual",
      value: 2
    }
  ]

  constructor(
    private _fb: FormBuilder,
    private s2cFB: S2cFormBuilderService
  ) { }

  buildTicketRuleFormGroup(ticketRuleJSON: any){
    console.log(ticketRuleJSON);
    let ticketRuleForm: FormGroup = this._fb.group({
      _id: '',
      name: '',
      folderId: '',
      owner: this._fb.group({
        typeOf: '',
        id: ''
      }),
      grpId: '',
      assignee: '',
      watcherUsers: this._fb.array([]),
      watcherGroups: this._fb.array([]),
      priority: '',
      linkedSurvey: this._fb.group({
        feedback: this._fb.group({
          surId: '',
          method: '',
          _id: ''
        }),
        satisfy: this._fb.group({
          surId: '',
          method: '',
          _id: ''
        }),
      }),
      tags: this._fb.array([]),
      ticketData: this._fb.group({}),
      defText: '',
    })

    let idx: number = 0;
    while(idx < ticketRuleJSON.watcherGroups.length){
      (<FormArray>ticketRuleForm.get('watcherGroups')).push(new FormControl(''));
      idx++;
    }
    idx = 0;
    while(idx < ticketRuleJSON.watcherUsers.length){
      (<FormArray>ticketRuleForm.get('watcherUsers')).push(new FormControl(''));
      idx++;
    }

    //tags and ticketData is not formed in formGroup.

    ticketRuleForm.patchValue(ticketRuleJSON);
    console.log(ticketRuleForm);
    return ticketRuleForm;
  }
}



