import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { Survey } from './survey.component';
import { SurveyEdit } from './edit/survey.edit.component';
import { SurveyQuestionEditComponent } from './edit/survey.question.edit/survey.question.edit.component';
import { SurveyDesignComponent} from './edit/survey.design/survey.design.component';
import { SurveyAdvanceComponent } from './edit/survey.advance/survey.advance.component';
import { SurveySettingsComponent } from './edit/survey.settings/survey.settings.component';

import { SurveyAnalyseComponent } from './analyse/survey.analyse.component';
import { SurveyCollectComponent } from './collect/survey.collect.component';
import { SurveyPreviewComponent } from './preview/survey.preview.component';
import { SurveySummaryComponent } from './summary/survey.summary.component';
import { SurveyListComponent } from './list/survey.list.component';
import { SurveyIntroComponent } from './edit/survey.intro/survey.intro.component';

const surveyRoutes: Routes = [
    { path: '',
      component: Survey,
      children: [
        { path: '', component: SurveyListComponent },
        { path: 'design/:sid',
          component: SurveyEdit,
          children: [
            { path: 'questions', component: SurveyDesignComponent },
            { path: 'tips', loadChildren: './edit/survey.tips/survey.tips.module#TipsModule' },
            { path: 'question/:qid', component: SurveyQuestionEditComponent },
            { path: 'intro', component: SurveyIntroComponent},
            { path: 'advance', component: SurveyAdvanceComponent },
            { path: 'themes', loadChildren: './edit/survey.themes/survey.themes.module#ThemesModule' },
            { path: 'settings', component: SurveySettingsComponent },
            { path: '**', redirectTo: 'questions' }
          ]
        },
        { path: 'analyse',
          component: SurveyAnalyseComponent
        },
        { path: 'collect',
          component: SurveyCollectComponent
        },
        { path: 'summary',
          component: SurveySummaryComponent
        },
      ]
    }
];

@NgModule({
  imports: [
    RouterModule.forChild(surveyRoutes)
  ],
  exports: [
    RouterModule
  ]
})
export class SurveyRoutesModule { }



