import { Pipe, PipeTransform } from '@angular/core';
import { UtilsService } from '../../../shared/services/utils.service';

@Pipe({
  name: 'embedDataType'
})
export class EmbedDataTypePipe implements PipeTransform {
  transform(value: string, args?: any): string {
    return value.charAt(2);
  }
}

@Pipe({
  name: 'embedDataField'
})
export class EmbedDataFieldPipe implements PipeTransform {
  transform(value: string, args?: any): string {
    return value.substr(6, (value.length-8));
  }
}

@Pipe({
  name: 'showSkipLogic'
})
export class ShowSkipLogic implements PipeTransform {
  constructor(private utils: UtilsService){}
  transform(value: string, ques?: any): string {
    let idx = this.utils.findIndex(ques.fieldOptions.row, '_id', value);
    if(idx != -1){
      return ques.fieldOptions.row[idx].text;
    }else{
      idx = this.utils.findIndex(ques.fieldOptions.col, '_id', value);
      if(idx != -1){
        return ques.fieldOptions.col[idx].text;
      }else{
        return null;
      }
    }
  }
}

@Pipe({
  name: 'findCriteriaName'
})
export class FindCriteriaName implements PipeTransform {
  constructor(private utils: UtilsService){}
  transform(value: string, criteriaArr?: any): string {
    let idx = this.utils.findIndex(criteriaArr, '_id', value);
    if(idx != -1){
      return criteriaArr[idx].name;
    }else{
      return null;
    }
  }
}

@Pipe({
  name: 'findOptionsInDisplayLogic'
})
export class FindOptionsInDisplayLogic implements PipeTransform {
  constructor(private utils: UtilsService){}
  transform(value: any, ques?: any): string {
    let idx;
    let res: Array<string> = [];
    value.forEach((item) => {
      idx = this.utils.findIndex(ques.fieldOptions.row, '_id', item);
      if(idx != -1){
        res.push(ques.fieldOptions.row[idx].text);
      }
    })
    return res.join(', ');
  }
}

export const EditPipe = [EmbedDataFieldPipe, EmbedDataTypePipe, ShowSkipLogic, FindCriteriaName, FindOptionsInDisplayLogic];



