import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { SharedServices } from '../../../../../../shared/services/services.module';
import { LogoService } from '../../../survey.main-list/logo.service';

@Component({
  selector: 'app-logo-settings',
  templateUrl: './logo-settings.component.html',
  styleUrls: ['./logo-settings.component.scss']
})
export class LogoSettingsComponent implements OnInit {
  @Input() logoInpData: any;
  @Output() onCancel = new EventEmitter<any>();
  @Output() onSuccess = new EventEmitter<any>();
  formObj: any;

  logoSettings: any;
  copyLogoData: any;
  logoDetails: any;

  constructor(
    private logoService: LogoService,
    private services: SharedServices
  ) { }

  ngOnInit() {
    this.formObj = this.services.form.formObj;
    this.logoSettings = this.formObj.logo;
    this.copyLogoData = this.services.utils.cloneObject(this.logoSettings);
    this.logoDetails = this.logoService.logoDetails;
  }

  editLogo(){
    this.onSuccess.emit('logo-replace');
    this.doneSettings();
  }

  removeLogo(){
    this.logoService.removeLogo(this.logoInpData.sId, this.formObj);
    this.onSuccess.emit('logo-remove');
    this.doneSettings();
  }

  saveSize(){
    let data: any = this.logoSettings;
    let res: any = {
      logo: data
    }
    this.services.resource.addLogo(this.logoInpData.sId, res).subscribe((data) => {
      this.logoService.setLogoOnLoad(this.logoInpData.sId, this.formObj);
    });
  }

  save(){
    let data: any = this.logoSettings;
    let res: any = {
      logo: data
    }
    this.services.resource.addLogo(this.logoInpData.sId, res).subscribe((data) => {
      this.logoService.setLogoOnLoad(this.logoInpData.sId, this.formObj);
      this.services.toastr.success('Logo settings has been updated successfully.', 'Success');
      this.doneSettings();
    });
  }

  cancel(){
    if(this.formObj.logo.size != this.copyLogoData.size){ //if previous size is not same as new size
      let res: any = {
        logo: this.copyLogoData
      }
      this.services.resource.addLogo(this.logoInpData.sId, res).subscribe((data) => {
        this.logoService.setLogoOnLoad(this.logoInpData.sId, this.formObj);
      });
    }
    this.formObj.logo = this.services.utils.cloneObject(this.copyLogoData);
    this.doneSettings();
  }

  doneSettings(){
    this.onCancel.emit();
  }

}



