import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { SharedServices } from '../../../shared/services/services.module';

@Component({
  selector: 'app-survey.preview',
  templateUrl: './survey.preview.component.html',
  styleUrls: ['./survey.preview.component.css']
})
export class SurveyPreviewComponent implements OnInit {
  sid: string= "";


  constructor(
    private route: ActivatedRoute,
    private services: SharedServices
  ) { }

  ngOnInit() {
    this.sid = this.route.params["value"].sid;
  }

}



