import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { SharedServices } from "../../../../../shared/services/services.module";

@Component({
  selector: 'app-language-list',
  templateUrl: './language.list.component.html',
  styleUrls: ['./language.list.component.scss', '../../survey.edit.component.scss']
})
export class LanguageListComponent implements OnInit {
  @Output() onEditLang: EventEmitter<any> = new EventEmitter<any>();
  @Output() onBackLang: EventEmitter<any> = new EventEmitter<any>();;
  langScriptsData: any;
  addLanguageList: boolean = false;
  languagesData: any;
  alreadyAddedLanguages : any = {};
  currentlySeletedLangs: any = [];
  multiLingualCheck: boolean = true;
  constructor(private services: SharedServices) { }

  ngOnInit() {
      let surId = this.services.form.formObj.getId();
      this.services.resource.getSurveyLanguages(surId).subscribe(data => {
          this.langScriptsData = this.services.utils.cloneObject(data);
      });
  }
  addNewLanguages() {
      this.addLanguageList = true;
      let surId = this.services.form.formObj.getId();
      this.services.resource.getLanguageScripts(surId).subscribe(data => {
          this.languagesData = this.services.utils.cloneObject(data);
          this.languagesData.surveyLang.forEach((l) => {
             this.alreadyAddedLanguages[l.langKey] = l;
             this.currentlySeletedLangs.push(l);
          });
        //   console.log("\n\n\nabc\n",this.alreadyAddedLanguages);
        //   console.log("\n\n\nabc\n",this.languagesData);
      });
  }
  saveNewLanguages() {
      let surId = this.services.form.formObj.getId();
      if(!this.currentlySeletedLangs || !this.currentlySeletedLangs.length) {
          this.services.toastr.error("Please select atleast one language");
          return;
      }
      this.services.resource.addLanguageToSurvey(surId, this.currentlySeletedLangs).subscribe((data) => {
          this.addLanguageList = false;
          this.langScriptsData.languages = this.currentlySeletedLangs;
        //   console.log('\n\n\nlang\n\n\n',this.langScriptsData);
      });
  }
  cancelAddingLanguage() {
      this.addLanguageList = false;
  }
  addLanguageToSelected(lang,event) {
      let langObj = {
          langKey: lang.langKey,
          direction: lang.direction,
          name: lang.name
      }
      if(!this.currentlySeletedLangs)
        this.currentlySeletedLangs = [];
      if (event.target.checked) {
        let langFlag = false;
        for(let i = 0; i < this.currentlySeletedLangs.length; i++) {
            if(this.currentlySeletedLangs[i].langKey == langObj.langKey) {
                langFlag = true;
            }
        }
        if(!langFlag) {
          this.currentlySeletedLangs.push(langObj);
        }
      } else {
          for(let i = 0; i < this.currentlySeletedLangs.length; i++) {
              if(this.currentlySeletedLangs[i].langKey == langObj.langKey) {
                  this.currentlySeletedLangs.splice(i,1);
                  break;
              }
          }
      }
  }
  deleteLanguage(lang) {
     let surId = this.services.form.formObj.getId();
     this.services.resource.deleteLanguageFromSurvey(surId,lang.langKey).subscribe((data) => {
         for(let i = 0; i < this.langScriptsData.languages.length; i++) {
             if(this.langScriptsData.languages[i].langKey == lang.langKey) {
                  // console.log(this.langScriptsData.languages[i],"testing");
                 this.langScriptsData.languages.splice(i,1);
                 delete this.alreadyAddedLanguages[lang.langKey];
                 break;
             }
         }
        //  console.log('\n\n\nlang\n\n\n',this.langScriptsData);
     });
  }
  editLanguage(lang) {
    //   console.log("list",lang);
      this.onEditLang.emit(lang);
  }
  backToAdvance() {
      this.onBackLang.emit();
  }
}



