import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormArray, FormControl, FormGroup, FormBuilder } from '@angular/forms';

import { SharedServices } from '../../../../../../../shared/services/services.module';
import { LogicService } from '../logic.service';

@Component({
  selector: 'app-delayed-branching',
  templateUrl: './delayed.branching.component.html',
  styleUrls: ['../logics.component.scss', './delayed.branching.component.scss'] //'../../../../criteria.and.logics/criteria.and.logics.component.css'
})
export class DelayedBranchingComponent implements OnInit {
  @Input() qId: string;

  @Output() logicChanged: EventEmitter<any> = new EventEmitter<any>();
  @Output() canceled: EventEmitter<any> = new EventEmitter<any>();

  question: any;
  criteriaArr: any[];
  logicFormGroup: FormGroup;
  questionList: any;
  additionalOptions: Array<any>;
  appliedLogicsLength: number;
  logicName: string = this.logicService.allLogicName.compound.name;

  constructor(
  	private logicService: LogicService,
  	private services: SharedServices
 	) {}

  ngOnInit() {
    this.question = this.services.form.getFormData().getQuestionById(this.qId);
    this.criteriaArr = this.services.form.getFormData().getAllCriterias();
  	this.questionList = this.services.form.getQuestionsAfter(this.question._id)
  	this.additionalOptions = this.logicService.getAdditionalOptionsForQuestionList();
  	this.setAppliedLogic();
  }

  setAppliedLogic() {
    let question = this.services.utils.cloneObject(this.question);
  	question.logicArr = question.logicArr.filter((logic) => {return logic.logic == this.logicName;});
  	if(question.logicArr.length < 2) {
      for(let i = 0; i < 2; i++) {
        question.logicArr.push(question.logicArr[i] || this.getLogicData());
      }
  	}
    question.logicArr.forEach((logic) => {
      if(!this.checkForAdditionalOptions(logic.lType)) {
        logic.destQId = logic.lType;
      }
    });
    this.logicFormGroup = this.logicService.buildCriteriaForm(question);
    this.appliedLogicsLength = this.logicFormGroup.get('logicArr')["controls"].length;
  }

  addNewCondition() {
  	this.logicFormGroup.get('logicArr')["insert"](this.appliedLogicsLength - 1, this.logicService.getFormGroup(this.getLogicData()));
    this.appliedLogicsLength = this.logicFormGroup.get('logicArr')["controls"].length;
  }

  saveLogic() {
    var logicArr = this.logicFormGroup.value.logicArr;
    logicArr.forEach((logic, index) => {
      if(index == logicArr.length - 1) {
        logic.metUnmet = 0;
        logic.ansId = logicArr[0].ansId;
      }
    });

    let logicFormData = this.logicService.getLogicFormData(this.question, logicArr, this.logicName);

    this.logicService.saveLogicData(logicFormData, this.question._id).subscribe((data) => {
      if(!this.services.form.formObj.isLastQuestionOnPage(this.question._id)) {
        let page = this.services.form.formObj.getPageByQuestionId(this.question._id);
        this.services.resource.addPageBreak(page._id, this.services.form.formObj._id, this.question._id).subscribe((data1) => {
          this.logicChanged.emit({logicName: this.logicName, res: data});
          this.services.form.fetchSurveyDataFromServer(this.services.form.formObj._id).subscribe(data2 => {

          });
        });
      } else {
        this.logicChanged.emit({logicName: this.logicName, res: data});
      }
    });
  }

  updateLogic(logic: any, index: number) {
    if(this.checkForAdditionalOptions(logic.get('destQId').value)) { //if question selected
      logic.get('lType').setValue("normal");
      logic.get('destPId').setValue(this.services.form.getPageId(logic.get('destQId').value));
    } else {
      logic.get('lType').setValue(logic.get('destQId').value);
    }
  }

  deleteLogic(index: number) {
  	if(this.appliedLogicsLength > 2) {
	  	this.logicFormGroup.get('logicArr')["removeAt"](index);
      this.appliedLogicsLength = this.logicFormGroup.get('logicArr')["controls"].length;
	  }
  }

  getLogicData() {
    let logicObject = this.services.utils.cloneObject(this.logicService.logicData);
    logicObject["logic"] = this.logicName;
    return logicObject;
  }

  checkForAdditionalOptions(value: any) {
    return this.additionalOptions.map((item) => item.value).indexOf(value) == -1;
  }

  removeAllLogic() {
    this.logicService.removeAllLogicByName(this.question, this.logicName).subscribe((data) => {
      this.logicChanged.emit({logicName: this.logicName, res: data});
      this.services.toastr.success('Logic has been successfully removed.');
    });
  }

  showCriteriaForm(event) {
    (event.target.value == '0') && this.logicChanged.emit({view: 'Criteria'})
  }

  cancel() {
    this.canceled.emit();
  }

}



