import { Component, OnInit, OnDestroy } from '@angular/core';
import { Request, Headers, RequestOptions, Http } from '@angular/http';
import { AuthHttp } from 'angular2-jwt';
import { EditDataService } from '../edit.data.service';
import { Subscription } from 'rxjs/Subscription';
import { Router } from '@angular/router';
import { ConstantsService } from '../../../../shared/services/constants.service'
import { SharedServices } from '../../../../shared/services/services.module';

@Component({
  selector: 'app-survey-intro',
  templateUrl: './survey.intro.component.html',
  styleUrls: ['../survey.edit.component.scss', './survey.intro.component.scss','../survey.question.edit/survey.question.edit.component.scss']
})
export class SurveyIntroComponent implements OnInit {
  onSuccessAction: any = {}
  isDataReady: boolean = false;
  surveyDesignData: any;
  actionObject: any;
  subscription: Subscription;
  title: string ="Welcome! Lets get  started";
  des: string ="The activity shouldnt take longer than 10-15 minutes to complete";
  buttonText: string = "Start";
  buttonTextColor: string = "#ffffff";
  buttonBGColor: string = "#000000";
  backgroundColor: string = "#ffffff";
  imageSrc: string = "";
  imageBGSrc: string = "";
  loaded: boolean= false;
  margins: any = {
    title_m_l : 0,
    title_m_r : 0,
    title_m_t : 0,
    title_m_b : 0,
    des_m_l : 0,
    des_m_r : 0,
    des_m_t : 0,
    des_m_b : 0,
    btn_m_l : 0,
    btn_m_r : 0,
    btn_m_t : 0,
    btn_m_b : 0,
    img_m_l : 0,
    img_m_r : 0,
    img_m_t : 0,
    img_m_b : 0,
  };
  marginTitle: any = {
    'margin-left': '0px',
    'margin-right': '0px',
    'margin-top': '0px',
    'margin-bottom': '0px'
  };
  marginDes: any = {
    'margin-left': '0px',
    'margin-right': '0px',
    'margin-top': '0px',
    'margin-bottom': '0px'
  };
  marginBtn: any = {
    'margin-left': '0px',
    'margin-right': '0px',
    'margin-top': '0px',
    'margin-bottom': '0px'
  };
  marginImg: any = {
    'margin-left': '0px',
    'margin-right': '0px',
    'padding-top': '0px',
    'margin-bottom': '0px'
  };
  borderBtn: any = {
    color: '#ffffff',
    width: 0,
    type: 'solid'
  };
  alignBtn: string = 'center';
  alignImg: string = 'center';
  titleP: any = {
    piping: 'e',
    contactPipe: '',
    eDataPipe: ''
  };
  desP: any = {
    piping: 'e',
    contactPipe: '',
    eDataPipe: ''
  };
  showStart: boolean = false;
  pointeevts: string = 'none';
  opacity: number = 0.3;

  constructor(
    private router: Router,
    private service: SharedServices,
    private http: AuthHttp,
    private constants: ConstantsService,
    private _http: Http,
    private editDataService: EditDataService
  ) { }

  ngOnInit() {
    setTimeout(() => {
      this.editDataService.surveyDesignData.displaySidebar = false;
    },0);
    this.surveyDesignData = this.editDataService.surveyDesignData;
    console.log("editDataService",this.editDataService)
    this.editDataService.getSurveyData().subscribe(data => {
      if(!data) return;
      this.loaded = true;
      console.log("this.editDataService",data);
      (data.startPage.title)? this.title = data.startPage.title : '';
      (data.startPage.des)? this.des = data.startPage.des: '';
      (data.startPage.buttonText)? this.buttonText = data.startPage.buttonText: '';
      (data.startPage.buttonTextColor)? this.buttonTextColor = data.startPage.buttonTextColor : '';
      (data.startPage.buttonBGColor)?this.buttonBGColor = data.startPage.buttonBGColor:'';
      (data.startPage.backgroundColor)? this.backgroundColor = data.startPage.backgroundColor:'';
      (data.startPage.imageSrc)?this.imageSrc = data.startPage.imageSrc: '';
      (data.startPage.imageBGSrc)?this.imageBGSrc = data.startPage.imageBGSrc: '';
      (data.startPage.marginTitle)?this.marginTitle = data.startPage.marginTitle: '';
      (data.startPage.marginDes)?this.marginDes = data.startPage.marginDes: '';
      (data.startPage.marginBtn)?this.marginBtn = data.startPage.marginBtn: '';
      (data.startPage.marginImg)?this.marginImg = data.startPage.marginImg: '';
      (data.startPage.borderBtn)?this.borderBtn = data.startPage.borderBtn: '';
      (data.startPage.alignBtn)?this.alignBtn = data.startPage.alignBtn: '';
      (data.startPage.alignImg)?this.alignImg = data.startPage.alignImg: '';
      (data.startPage.showStart)?this.showStart = data.startPage.showStart: '';

      if(this.showStart){
        this.pointeevts = "auto";
        this.opacity= 1;
      }else{
        this.pointeevts = "none";
        this.opacity= 0.3;
      }

      this.margins.title_m_l = this.marginTitle['margin-left'].substring(0,this.marginTitle['margin-left'].length-2);
      this.margins.title_m_r = this.marginTitle['margin-right'].substring(0,this.marginTitle['margin-right'].length-2);
      this.margins.title_m_t = this.marginTitle['margin-top'].substring(0,this.marginTitle['margin-top'].length-2);
      this.margins.title_m_b = this.marginTitle['margin-bottom'].substring(0,this.marginTitle['margin-bottom'].length-2)
      this.margins.des_m_l = this.marginDes['margin-left'].substring(0,this.marginDes['margin-left'].length-2);
      this.margins.des_m_r = this.marginDes['margin-right'].substring(0,this.marginDes['margin-right'].length-2);
      this.margins.des_m_t = this.marginDes['margin-top'].substring(0,this.marginDes['margin-top'].length-2),
      this.margins.des_m_b = this.marginDes['margin-bottom'].substring(0,this.marginDes['margin-bottom'].length-2),
      this.margins.btn_m_l = this.marginBtn['margin-left'].substring(0,this.marginBtn['margin-left'].length-2);
      this.margins.btn_m_r = this.marginBtn['margin-right'].substring(0,this.marginBtn['margin-right'].length-2);
      this.margins.btn_m_t = this.marginBtn['margin-top'].substring(0,this.marginBtn['margin-top'].length-2);
      this.margins.btn_m_b = this.marginBtn['margin-bottom'].substring(0,this.marginBtn['margin-bottom'].length-2);
      this.margins.img_m_l = this.marginImg['margin-left'].substring(0,this.marginImg['margin-left'].length-2);
      this.margins.img_m_r = this.marginImg['margin-right'].substring(0,this.marginImg['margin-right'].length-2);
      this.margins.img_m_t = this.marginImg['padding-top'].substring(0,this.marginImg['padding-top'].length-2);
      this.margins.img_m_b = this.marginImg['margin-bottom'].substring(0,this.marginImg['margin-bottom'].length-2);

    });
  }

  onSuccessToActions(act){
    this.onSuccessAction = {
      action: act
    }
  }

  updateContact(type, where){
    let expression = '';
    if(type == 'c' && where == "des" && this.desP.contactPipe)
      expression = `\{\{${type}://${this.desP.contactPipe}\}\}`;
    else if(type == 'e' && where == "des" && this.desP.eDataPipe)
      expression = `\{\{${type}://${this.desP.eDataPipe}\}\}`;
    else if(type == 'c' && where == "title" && this.titleP.contactPipe)
        expression = `\{\{${type}://${this.titleP.contactPipe}\}\}`;
    else if(type == 'e' && where == "title" && this.titleP.eDataPipe)
      expression = `\{\{${type}://${this.titleP.eDataPipe}\}\}`;
    expression && tinymce.activeEditor.execCommand('mceInsertContent',true,expression);
  }

  setMargin(type, pos, event){
    this.marginTitle['margin-left'] = this.margins.title_m_l+'px';
    this.marginTitle['margin-right'] = this.margins.title_m_r+'px';
    this.marginTitle['margin-top'] = this.margins.title_m_t+'px';
    this.marginTitle['margin-bottom'] = this.margins.title_m_b+'px';

    this.marginDes['margin-left'] = this.margins.des_m_l+'px';
    this.marginDes['margin-right'] = this.margins.des_m_r+'px';
    this.marginDes['margin-top'] = this.margins.des_m_t+'px';
    this.marginDes['margin-bottom'] = this.margins.des_m_b+'px';

    this.marginBtn['margin-left'] = this.margins.btn_m_l+'px';
    this.marginBtn['margin-right'] = this.margins.btn_m_r+'px';
    this.marginBtn['margin-top'] = this.margins.btn_m_t+'px';
    this.marginBtn['margin-bottom'] = this.margins.btn_m_b+'px';

    this.marginImg['margin-left'] = this.margins.img_m_l+'px';
    this.marginImg['margin-right'] = this.margins.img_m_r+'px';
    this.marginImg['padding-top'] = this.margins.img_m_t+'px';
    this.marginImg['margin-bottom'] = this.margins.img_m_b+'px';
  }

  removeImg(){
    this.imageSrc = '';
  }

  removeBG(){
    this.imageBGSrc = '';
  }

  saveImage(event){
    let that = this;
    let uploadId = "";
    let fileList: FileList = event.target.files;
    let file: File = fileList[0];
    if (file) {
      let that = this;
      let reader: FileReader = new FileReader();
      reader.onload = function (e) {
        that.imageSrc = e.target['result'];
      };
      reader.readAsDataURL(file);
    } else {
      that.imageSrc = '';
    }
    let fileData = {id: uploadId, file: file}
    console.log('fileData',fileData)
    // file && (this.ansData = [{imgSrc: file.name, uploadId: uploadId}]);
  }

  saveBGImage(event){
    let that = this;
    let uploadId = "";
    let fileList: FileList = event.target.files;
    let file: File = fileList[0];
    if (file) {
      let that = this;
      let reader: FileReader = new FileReader();
      reader.onload = function (e) {
        that.imageBGSrc = e.target['result'];
      };
      reader.readAsDataURL(file);
    } else {
      that.imageBGSrc = '';
    }
    let fileData = {id: uploadId, file: file}
    console.log('fileData',fileData)
    // file && (this.ansData = [{bgImgSrc: file.name, uploadId: uploadId}]);
  }

  cancel(){
    // this.ngOnDestroy()
    this.router.navigate(['/survey', 'design', this.editDataService.sId]);
  }

  offOnIntro(event){
    this.showStart = !this.showStart;
    if(this.showStart){
      this.pointeevts = 'auto';
      this.opacity = 1;
    }else{
      this.pointeevts = 'none';
      this.opacity = 0.3;
    }
    // console.log("eve",event);
    // console.log("this.showStart",this.showStart);
    this.save();
  }

  save(){
    let data = {
      title: this.title,
      des: this.des,
      buttonText: this.buttonText,
      buttonTextColor: this.buttonTextColor,
      buttonBGColor: this.buttonBGColor,
      backgroundColor: this.backgroundColor,
      imageSrc: this.imageSrc,
      imageBGSrc:this.imageBGSrc,
      showStart: this.showStart,
      marginTitle : this.marginTitle,
      marginDes : this.marginDes,
      marginBtn : this.marginBtn,
      marginImg : this.marginImg,
      borderBtn : this.borderBtn,
      alignBtn : this.alignBtn,
      alignImg : this.alignImg
    }

    let sp = {startPage : data};

    this.service.resource.updateSurveyData(this.editDataService.sId, sp).subscribe((dat) => {
        console.log("dat",dat)
    });

    this.service.toastr.success('Intro Page has been saved!', 'Saved Successfully');
  }

  onTitleEvent(event){
    // console.log("event",event)
    this.title=event.options.content;
  }

  onDesEvent(event){
    // console.log("event",event)
    this.des=event.options.content;
  }

  ngOnDestroy() {
    if(this.subscription)
      this.subscription.unsubscribe();
  }


}



