import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

import { SharedServices } from '../../../../../../shared/services/services.module';
import { QuestionTypesService } from '../../../../../../shared/questionTemplates/question-types.service';

@Component({
  selector: 'app-recode-value',
  templateUrl: './recode.value.component.html',
  styleUrls: ['../../../survey.edit.component.scss', './recode.value.component.css']
})
export class RecodeValueComponent implements OnInit {
  @Input() qId: string;
  @Output() onCancel: EventEmitter<any> = new EventEmitter<any>();

  questionCountList: any;
  options: any[] = [];
  weights: string;
  recodeValues: any = {};

  constructor(
    public services: SharedServices,
    private qTypesServ: QuestionTypesService
  ) { }

  ngOnInit() {
    let question = this.services.form.formObj.getQuestionById(this.qId);
    this.weights = question.recodeLocked ? "1" : "";
    let carryOptions = this.services.form.formObj.getCarryOptionArrays(this.qId);
    carryOptions.topArray.forEach(opt => {
      opt.isCarry = true;
      this.options.push(opt);
    });
    if(question.getQuestionFamily() === 'matrix') {
      this.options.push({qId: this.qId, options: question.getColArray(), carrObj: {}});
    } else {
      this.options.push({qId: this.qId, options: question.getRowArray(), carrObj: {}});
    }
    carryOptions.bottomArray.forEach(opt => {
      opt.isCarry = true;
      this.options.push(opt);
    });
    this.options.forEach(item => {
      item.options.forEach(opt => {
        opt.text = this.services.utils.getTextOutOfHtml(opt.text);
        if(question.recodeLocked) {
          this.recodeValues[opt._id] = item.isCarry ? (!item.carrObj.wtOrInd ? (item.carrObj.weightDoc ? item.carrObj.weightDoc[opt._id] : '') : opt.weight) : opt.weight;
        }
      });
    });
    this.questionCountList = this.services.form.getQuestionCountAndId()['data'];    
    let fieldType = question.getFieldType();
    //if(fieldType.family == 'matrix') {}
  }

  changeWeights(event) {
    this.weights = event.target.checked ? "1" : "";
    if(event.target.checked) {
      let count = 1;
      this.options.forEach(item => {
        item.options.forEach(opt => {
          opt.text = this.services.utils.getTextOutOfHtml(opt.text);
          this.recodeValues[opt._id] = item.carrObj.wtOrInd ? (opt.weight || '0') : count
          !item.carrObj.wtOrInd && count++;
        });
      });
    } else {
      this.recodeValues = {};
    }
  }

  save() {
    let data = {
      recodeLocked: !this.weights ? 0 : 1,
      _id: this.qId
    };
    let question = this.services.form.formObj.getQuestionById(this.qId);
    let optData = []
    let frmCarryForward = [];
    if(data.recodeLocked) {
      this.options.forEach(item => {
        item.options.forEach(opt => {
          if(!item.isCarry) {
            opt.weight = this.recodeValues[opt._id] || "";
            optData.push(opt);
          } else if (item.isCarry && item.carrObj.wtOrInd == 0) {
            !item.carrObj.weightDoc && (item.carrObj.weightDoc = {});
            item.carrObj.weightDoc[opt._id] = this.recodeValues[opt._id] || "";
          }
        });
        if (item.isCarry) {
          frmCarryForward.push(item.carrObj);
        }
      });
      data['fieldOptions'] = question.getFieldOptions();
      if(question.getQuestionFamily() === 'matrix') {
        data['fieldOptions']['col'] = optData;
        question.setColArray(optData);
      } else {
        data['fieldOptions']['row'] = optData;
        question.setRowArray(optData);
      }
      data['frmCarryForward'] = frmCarryForward;
    }
    this.services.resource.updateQuestionData(this.qId, data).subscribe(res => {
      question.setRecodeLocked(data.recodeLocked);
      this.cancel();
      this.services.toastr.success('Recode value updated', 'Question Updated');
    });
  }

  cancel() {
    this.onCancel.emit();
  }

}



