import { Injectable } from '@angular/core';
import { FormArray, FormControl, FormGroup, FormBuilder } from '@angular/forms';

@Injectable()
export class QuotaService {
  quotaJSON: object = {
    name: '',
    isQuota: 0,
    respType: 0,
    type: 1,
    sQuota: {
      quesId: '-1',
      optArr: []
    },
    cQuota: {
      value: '',
      quesInQuota: [{
        quesId: '-1',
        select: 1,
        optArr: [],
        ope: 1
      }]
    }
  }

  constructor(private _fb: FormBuilder) { }

  buildQuotaFormGroup(quotaJSON: any){
    let quotaForm: FormGroup = this._fb.group({
      name: '',
      isQuota: '',
      respType: '',
      type: '',
      sQuota: this._fb.group({
        quesId: '',
        optArr: this._fb.array([
        ]),
      }),
      cQuota: this._fb.group({
        value: '',
        quesInQuota: this._fb.array([
        ])
      })
    })
    quotaJSON.cQuota.quesInQuota.forEach((ques) => {
      (<FormArray>quotaForm.get('cQuota.quesInQuota')).push(this.addQuesInCombtnQuota());
    })
    quotaForm.patchValue(quotaJSON);
    return quotaForm;
  }

  addItemInOptArrInSimpleQuota(id?: string, limit?: number, sel?: number){
    return this._fb.group({
      ansId: (id ? id : ''),
      select: (sel ? sel : 1),
      value: (limit ? limit : '')
    })
  }

  addQuesInCombtnQuota(){
    return this._fb.group({
      quesId: -1,
      select: 1,
      optArr: '',
      ope: 1
    })
  }

}



